<template>
	<div class="preview-main">
		<div class="preview-music" @click="operaMusic" :class="{'pause':!isPlayMusic}" v-show='isShowMusic' v-if='!!music'>
			<iframe style="display: none;" id='ifr' src="./static/music/white.mp3" width="0" height="0"></iframe>
			<audio :src="!!music?$api+music.url:''" id="preview-audio" loop="loop"></audio>
			<img src="../assets/images/music_pause.png" v-if="!isPlayMusic" />
			<img src="../assets/images/music_play.png" v-else="isPlayMusic" />
		</div>
		<div class="swiper-container">
			<div class="swiper-wrapper">
				<div class="swiper-slide" v-for="(item,index) in newEleData">
					<ul class="main-wraper" style="z-index: 11;height: 100%;">
						<div class="main-back" style="height: 100%;" :style="{
									opacity:item.style.opacity,
									backgroundColor:item.style.backgroundColor,
									backgroundImage:'url('+(item.style.backgroundImageUrl?($api+item.style.backgroundImageUrl):'')+')',
								}"></div>
						<template v-for="(items,indexs) in item.children">
							<template-list @showFeedback='showFeedback' @submitForm='submitForm' :items='items' :index='index' :pageActiveIndex='pageActiveIndex'></template-list>
							<div class="edit-video-type2" v-if='items.type == "video" && items.data.type == "2" && items.data.isShowVideo'>
								<i class="el-icon-close close" @click='closeVideo(items)'></i>
								<iframe :src="items.data.src" width="100%"></iframe>
							</div>
						</template>
						<!--   -->
						<template v-if='feedback.index.indexOf(index)!=-1'>
							<div class="feedback">
								<i class="el-icon-circle-close feedback-close" @click="closeShowFeedback(index)"></i>
								<img :src="feedback.url[index]" class="feedback-img"/>'
							</div>
						</template>
					</ul>
				</div>

			</div>
			<div class="swiper-pagination"></div>
		</div>
		<!--  -->
		<template v-if='pageActiveIndex<newEleData.length-1'>
			<i class="fa fa-chevron-up pre1 pre" v-if='direction=="vertical"' aria-hidden="true"></i>
			<i class="fa fa-chevron-up pre2 pre" v-if='direction=="horizontal"' aria-hidden="true"></i>
		</template>
		<!--  -->
		<template>
			<div class="page-tip">{{pageActiveIndex+1}}/{{newEleData.length}}</div>
		</template>
		<div class="tip animated fadeIn" v-if='isShowTip'>
			<div class="tip-main">
				<h3></h3>
				<p v-if='isShowTipSuccess'></p>
				<p v-else></p>
				<div @click="isShowTip = false">&ensp;</div>
			</div>
		</div>
	</div>
</template>
<script>
	import Swiper from 'swiper';
	import ElementList from '@/components/ElementList'
	import TemplateList from '@/components/TemplateList'
	export default {
		props: ['effect', 'direction', 'arrEleData', 'music', 'isAutoPlay', 'autoplay'],
		components: {
			ElementList,
			TemplateList
		},
		data() {
			return {
				Vue: this,			
				feedback:{
					url:{},
					index: []
				},
				
				isShowPreviewSlip: false,
				setAnimTimeOut: '',
				isShowVideo: false,
				isPlayMusic: false,
				previewForm: {
					titleLimit: 24,
					describeLimit: 30,
				},
				initialSlide: 0,
				mySwiper: '',
				isSubmitForm: false,
				isShowTip: false, //
				isShowTipSuccess: false, //
				slideArr: [],
				pageActiveIndex: 0, //
				newEleData: [],
				isShowMusic: true, //
				isChangeMusic: false, //
			}
		},
		mounted() {
			var Vue = this;
			this.newEleData = JSON.parse(JSON.stringify(this.arrEleData))
			this.createSwiper();
			this.$nextTick(()=>{
				var ifr = document.getElementById('ifr')
				if(ifr){
					ifr.onload = function() {
						Vue.operaMusic()
					}
				}
			})
		},
		created() {
			var Vue = this;
			this.$store.state.Form = []
		},
		methods: {
			closeShowFeedback(index){
				for (var i=this.feedback.index.length-1;i>=0;i--) {
					if(this.feedback.index[i] == index){
						this.feedback.index.splice(i,1);
						break;
					}
				}
				this.feedback.url[index] = undefined;
			},
			//
			showFeedback(val,index){
				this.feedback.url[index] = val;
				this.feedback.index.push(index);
			},
			//Video
			closeVideo(items) {
				this.isShowMusic = true;
				items.data.isShowVideo = false;
				var audio = document.getElementById('preview-audio')
				if(this.isPlayMusic && this.isChangeMusic) {
					this.isPlayMusic = false
					audio.pause()
					$('.preview-music').addClass('pause')
				} else if(!this.isPlayMusic && this.isChangeMusic) {
					this.isPlayMusic = true
					audio.play()
					$('.preview-music').removeClass('pause')
				}
			},
			//Swiper
			createSwiper() {
				this.pageActiveIndex = 0
				var Vue = this
				if(this.mySwiper) {
					this.mySwiper.destroy(false);
					if(this.effect == 'slide') {
						document.getElementsByClassName('swiper-wrapper')[0].style.transform = 'translate3d(0px, 0px, 0px)'
					}
				}
				this.mySwiper = new Swiper('.swiper-container', {
					//
					longSwipesRatio: 0.2,
					initialSlide: 0,
					observer: true,
					direction: this.direction,
					effect: this.effect,
					loop: false,
					//					loop: (Vue.$store.state.Swiper.loop.toString())=="false"?false:true,
					paginationType: 'progress',
					pagination: '.swiper-pagination',
					prevButton: '.preview-page-up',
					nextButton: '.preview-page-down',
					autoplay: (Vue.isAutoPlay.toString()) == "false" ? false : Vue.autoplay * 1000,
					autoplayDisableOnInteraction: false,
					onTransitionStart: function(swiper) {
						Vue.slideArr = $('.swiper-slide')
						if((Vue.$store.state.Swiper.loop.toString()) == "false") {
							Vue.animShow(swiper.realIndex)
							Vue.pageActiveIndex = swiper.realIndex
						} else {

							Vue.slideArr = $('.swiper-slide')
//							Vue.animCopyShow(pageActiveIndex)
						}
					},
					onInit: function(swiper) {
						//Swiper				
						Vue.slideArr = $('.swiper-slide')
						Vue.initialSlide = swiper.realIndex
						if((Vue.$store.state.Swiper.loop.toString()) == "false") {
							Vue.animShow(swiper.realIndex)
						}
						//						else {
						//							console.log(pageActiveIndex)
						//							Vue.animCopyShow(pageActiveIndex)	
						//						}														
					},

				});
			},
			//form
			submitForm() {
				var Vue = this;
				if(this.isSubmitForm) {
					this.isShowTipSuccess = false
					this.isShowTip = true
					return false;
				}
				this.$store.state.Form = [];
				this.newEleData.forEach(function(val, index) {
					if(val.children) {
						for(var i = 0; i < val.children.length; i++) {
							if(val.children[i].type == "checkbox" || val.children[i].type == "select" || val.children[i].type == "radiobox" || val.children[i].type == "input" || val.children[i].type == "message") {
								if(val.children[i].type == "checkbox") {
									Vue.$store.state.Form.push({
										id: val.children[i].id,
										title: val.children[i].EditStyleData.form.title,
										data: val.children[i].EditStyleData.form.store.length == 0 ? null : val.children[i].EditStyleData.form.store
									})
								} else {
									Vue.$store.state.Form.push({
										id: val.children[i].id,
										title: val.children[i].EditStyleData.form.title,
										data: val.children[i].EditStyleData.form.store
									})
								}
							}
						}
					}
				})
				this.axiosSubmitForm()
			},
			//
			axiosSubmitForm() {
				var Vue = this
				var edit_ID = this.$route.params.id;
				this.http.axiosSubmitForm({
					id: edit_ID,
					form: Vue.$store.state.Form,
					username: this.$store.state.user.username
				}).then((response) => {
					if(response.data.status == 1) {
						Vue.isSubmitForm = true
						Vue.isShowTipSuccess = true
						Vue.isShowTip = true
					}
				})
			},

			//
			operaMusic() {
				var audio = document.getElementById('preview-audio')
				if(this.isPlayMusic) {
					this.isPlayMusic = false
					audio.pause()
					$('.preview-music').addClass('pause')
				} else {
					this.isPlayMusic = true
					audio.play()
					$('.preview-music').removeClass('pause')
				}
			},
			//
			animShow(i) {
				var Vue = this
				if(this.newEleData[i].children) {
					this.newEleData[i].children.forEach(function(val, index) {
						var count = 0,
							time = 0,
							animShowTimeOut;
						setAnim()

						function setAnim() {
							animShowTimeOut = setTimeout(function() {
								if(count >= val.animData.length) {
									clearInterval(animShowTimeOut)
									return false
								}
								val.animPower.className = ''
								val.animPower.style.animationDuration = ''
								val.animPower.style.animationDelay = ''
								val.animPower.style.animationIterationCount = ''
								var className = val.animData[count].className,
									animationDuration = parseFloat(val.animData[count].style.animationDuration),
									animationDelay = parseFloat(val.animData[count].style.animationDelay),
									animationIterationCount = parseInt(val.animData[count].style.animationIterationCount),
									isInfinite = val.animData[count].style.isInfinite
								val.animPower.className = className
								val.animPower.style.animationDuration = animationDuration + 's'
								val.animPower.style.animationDelay = animationDelay + 's'
								if(typeof(isInfinite) == 'string') {
									isInfinite = eval(isInfinite.toLowerCase())
								}
								if(isInfinite) {
									val.animPower.style.animationIterationCount = "infinite"
									time = (animationDuration + animationDelay) * 100000
								} else {
									val.animPower.style.animationIterationCount = animationIterationCount
									time = (animationDuration + animationDelay) * animationIterationCount
									count++
									setAnim()
								}
	

							}, time * 1000)
						}
					})
				}
			},
			//slide
			animCopyShow(i) {
				var eq = i;
				if(eq == 0) {
					eq = this.newEleData.length - 1
				} else if(eq > this.newEleData.length) {
					eq = 0
				} else {
					eq = i - 1
				}
				var Vue = this;
				$(this.slideArr[i]).find('.element-box').each(function(index, val) {
					var count = 0,
						time = 0,
						animShowTimeOut,
						animData = Vue.newEleData[eq].children[index].animData
					setAnim()

					function setAnim() {
						animShowTimeOut = setTimeout(function() {
							if(count >= animData.length) {
								clearInterval(animShowTimeOut)
								return false
							}
							$(val).attr('class', 'element-box animated')

							setTimeout(function() {
								var className = animData[count].className,
									animationDuration = parseInt(animData[count].style.animationDuration),
									animationDelay = parseInt(animData[count].style.animationDelay),
									animationIterationCount = parseInt(animData[count].style.animationIterationCount),
									isInfinite = animData[count].style.isInfinite
								$(val).addClass(className)
									.css({
										'animation-duration': animationDuration + 's',
										'animation-delay': animationDelay + 's',
										'animation-iterationCount': animationIterationCount,

									})
								if(typeof(isInfinite) == 'string') {
									isInfinite = eval(isInfinite.toLowerCase())
								}
								if(isInfinite) {
									$(val).css('animation-iterationCount', 'infinite')
									time = (animationDuration + animationDelay) * 100000
								} else {

									time = (animationDuration + animationDelay) * animationIterationCount
									count++
									setAnim()
								}
							}, 50)

						}, time * 1000)
					}
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>

<style src='@/assets/style/element.scss' lang="scss"></style>


