import QrcodeVue from 'qrcode.vue';
export default {
	props: ['id', 'type', 'data', 'animData', 'animPower', 'BoxStyleData', 'svg', 'imgSrc'],
	components: {
	    QrcodeVue
	},
	data() {
		return {
			size: parseFloat(this.BoxStyleData.width),
			textValue: '',
			textStyle: {},
			checkbox: [],
			radiobox: [],
			selectbox: [],
			eleBoxStyle: {
				top: '',
				left: '',
				width: '',
				height: '',
				zIndex: '',
				transform: '',
			},
			elementForm: {},
			//
			eleAnimDate: {},
			angle: 0,
			selectValue: "",
			isClearFocusPart: false,
			isShowAngle:false,
		
		}
	},
	mounted() {		
		var _this = this
		this.$nextTick(function() {		
			_this.initEditBoxStyle()			
			_this.animShow()
		})
	},
	computed: {
		comBoxStyleData:function(){
			return this.BoxStyleData
		},
		comTextStyle: function() {		
			var _this = this;
			this.$store.state.ElementData.forEach(function(val, index) {
				if(!val.children) {
					return false
				}
				for(var i = 0; i < val.children.length; i++) {
					if(val.children[i].id == _this.id) {
						if(!val.children[i].EditStyleData) {
							return false
						}
						if(val.children[i].EditStyleData.style !== undefined && val.children[i].EditStyleData.style) {
							_this.textStyle = val.children[i].EditStyleData.style
							_this.textValue = val.children[i].data ? val.children[i].data : val.children[i].text
							_this.elementForm = val.children[i].EditStyleData.form
						}
					}
				}
			})
			this.size = parseFloat(_this.comBoxStyleData.width)-parseFloat(_this.textStyle.borderWidth)*2
		},
		comAnimData() {
			var Vue = this;
			this.$store.state.ElementData.forEach(function(val, index) {
				if(!val.children) {
					return false
				}
				for(var i = 0; i < val.children.length; i++) {
					if(val.children[i].id == _this.id) {
						Vue.eleAnimDate = val.children[i].EditStyleData.animData
					}
				}
			})
			
			return Vue.eleAnimDate
		}
	},
	watch: {
		//BoxStyleData
		comBoxStyleData:{
			handler(val, oldVal) {
				this.size = parseFloat(val.width)-parseFloat(this.textStyle.borderWidth)*2
			},
			deep: true
		},
		textValue(val, old) {
			var _this = this;
			this.$store.state.ElementData.forEach(function(val, index) {
				if(!val.children) {
					return false
				}
				for(var i = 0; i < val.children.length; i++) {
					if(val.children[i].id == _this.id) {
						val.children[i].data = _this.textValue
					}
				}
			})
		}
	},
	beforeDestroy() {
		document.removeEventListener('keydown', this.keydownMove, false)
	},
	methods: {
		//
		partFocus(e){		
			if(this.isClearFocusPart && !e.ctrlKey){	
				this.focusBox()
			}
			this.isClearFocusPart = false;
		},
		//
		changeVideo() {
			this.$emit('changeVideo', this.data, this.id)
		},
		//
		editText(event) {
			var _this = event.currentTarget
			$(_this).children('.edit-text').removeClass('pointer-none').focus()
			_this.addEventListener('mousedown', this.stopPropagation, false);
		},

		blurText(event) {
			var _this = event.currentTarget;
			$(_this).addClass('pointer-none')
			_this.parentNode.removeEventListener('mousedown', this.stopPropagation, false);
		},
		stopPropagation(e) {
			e.stopPropagation();
		},
		//
		animShow() {
			this.animPowerClear()
			var Vue = this
			var count = 0,
				time = 0,
				animShowTimeOut;
			clearInterval(this.setAnimTimeOut)
			this.setAnimTimeOut = setTimeout(function() {
				setAnim()
			}, 50)
			function setAnim() {
				animShowTimeOut = setTimeout(function() {
					if(count >= Vue.animData.length) {
						clearInterval(animShowTimeOut)
						return false
					}
					var className = Vue.animData[count].className,
						animationDuration = parseFloat(Vue.animData[count].style.animationDuration),
						animationDelay = parseFloat(Vue.animData[count].style.animationDelay),
						animationIterationCount = parseFloat(Vue.animData[count].style.animationIterationCount),
						isInfinite = Vue.animData[count].style.isInfinite
					Vue.animPower.className = className
					Vue.animPower.style.animationDuration = animationDuration + 's'
					Vue.animPower.style.animationDelay = animationDelay + 's'
					Vue.animPower.style.animationIterationCount = animationIterationCount

					if(isInfinite) {
						time = (animationDuration + animationDelay) * 1
					} else {
						time = (animationDuration + animationDelay) * animationIterationCount
					}
					count++
					setAnim()

				}, time * 1000)
			}
		},
		//
		animPowerClear() {
			this.animPower.className = ''
			this.animPower.style.animationDuration = ''
			this.animPower.style.animationDelay = ''
			this.animPower.style.animationIterationCount = ''
		},
		//
		operateBox(e) {
			e.stopPropagation()
			this.focusBox(e)
			this.$emit('showOpetare', e)
		},
		//
		focusArrBox(){
			this.$store.state.focusPart.push(this.id);
			if(this.$store.state.focusPart.length>1){
				this.$store.state.editNavNum=4
			}else {
				this.$store.state.editNavNum=0
			}
		},
		//Box
		focusBox() {
			this.$store.state.focusPart = [];
			this.$store.state.focusPart.push(this.id);
			if(this.$store.state.editNavNum != 0 && this.$store.state.editNavNum != 3) {
				this.$store.state.editNavNum = 0
			}
		},
		//BOX
		partDown(e) {
			if(this.$store.state.focusPart.indexOf(this.id) != -1) {
				var Vue = this;
				var startClientX = e.clientX;
				var startClientY = e.clientY;
				var address = []; //
				var measure = []; //
				var main = document.getElementById('main');
				var ruler = document.getElementsByClassName('main-parent-line')[0];
				var rulerX = ruler.getElementsByClassName('line-x');
				var rulerY = ruler.getElementsByClassName('line-y');
				var mainW = main.clientWidth;
				var mainH = main.clientHeight;
				var size = 3;
				ruler.style.display = 'block'
				this.isClearFocusPart = true;
				var moveNum = 0;//
				Vue.$store.state.ElementData.forEach(function(val, i) {
					if(val.id == Vue.$store.state.focusPage) {
						for(var t = 0; t < val.children.length; t++) {
							if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1) {
								var moveEle = document.querySelector('li[data-id='+val.children[t].id+']')								
								var moveEleRealW = moveEle.getBoundingClientRect().width;
								var moveEleRealH = moveEle.getBoundingClientRect().height;
								address.push([parseFloat(val.children[t].BoxStyleData.left), parseFloat(val.children[t].BoxStyleData.top)])
								measure.push([moveEleRealW,moveEleRealH])
							}
						}
						return
					}
				})
				$(document).on('mousemove', function(e) {
					moveNum ++
					let clientX = e.clientX;
					let clientY = e.clientY;
					let diffLeft = (clientX - startClientX)/Vue.$store.state.scaleRatio*100;
					let diffTop = (clientY - startClientY)/Vue.$store.state.scaleRatio*100;
					let arrPosition = [];
					var num = 0;
					if(moveNum >=2){
						Vue.isClearFocusPart = false;
					}					
					
					Vue.$store.state.ElementData.forEach(function(val, i) {
						if(val.id == Vue.$store.state.focusPage) {
							for(var t = 0; t < val.children.length; t++) {
								if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1) {
									let moveEle = $('li[data-id='+val.children[t].id+']')
									let moveEleRealL = moveEle.position().left;
									let moveEleRealT = moveEle.position().top;
									let spaceX = 40*Vue.$store.state.scaleRatio/100;
									let spaceY = 40.5*Vue.$store.state.scaleRatio/100;
									val.children[t].BoxStyleData.left = address[num][0] + diffLeft + 'px'
									val.children[t].BoxStyleData.top = address[num][1] + diffTop + 'px'

									for(var j = 0; j < rulerX.length; j++) {
										if((moveEleRealT - spaceY * j <= 2 && moveEleRealT - spaceY * j >= -2) ||
											((moveEleRealT + measure[num][1] / 2) - spaceY * j <= 2 && (moveEleRealT + measure[num][1] / 2) - spaceY * j >= -2) ||
											((moveEleRealT + measure[num][1]) - spaceY * j <= 2 && (moveEleRealT + measure[num][1]) - spaceY * j >= -2)
										) {
											rulerX[j].style.display = 'block'
										} else {
											rulerX[j].style.display = 'none'
										}				
									}
									for(var j = 0; j < rulerY.length; j++) {
										if(moveEleRealL - spaceX * j <= 2 && moveEleRealL - spaceX * j >= -2 ||
											((moveEleRealL + measure[num][0] / 2) - spaceX * j <= 2 && (moveEleRealL + measure[num][0] / 2) - spaceX * j >= -2) ||
											((moveEleRealL + measure[num][0]) - spaceX * j <= 2 && (moveEleRealL + measure[num][0]) - spaceX * j >= -2)
										) {
											rulerY[j].style.display = 'block'
										} else {
											rulerY[j].style.display = 'none'
										}
									}
									num++;
								}
							}
							return
						}
					})
				})
			} else {
				this.focusBox(e)
				this.partDown(e)
			}		
		},

		//BOX
		changeBox(e) {
			var circleELE = $(e.currentTarget)
			var dataDir = circleELE.data('dir') //
			var parentLi = circleELE.parents('.item-comp')[0] //LI
			var parentLiHeight = circleELE.parents('.item-comp').height()
			var parentLiWidth = circleELE.parents('.item-comp').width()
			var arrSize = {};
			var Vue = this;
			Vue.$store.state.ElementData.forEach(function(val, i) {
				if(val.id == Vue.$store.state.focusPage) {
					for(var t = 0; t < val.children.length; t++) {
						if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1) {
							arrSize[t] = {
								w:parseFloat(val.children[t].BoxStyleData.width),
								h:parseFloat(val.children[t].BoxStyleData.height),
								r:parseFloat(val.children[t].BoxStyleData.width)/parseFloat(val.children[t].BoxStyleData.height)
							}
						}
					}
				}
			})
			var parentLiTop = parentLi.offsetTop //LI TOP
			var parentLiLeft = parentLi.offsetLeft
			var mouseStartTop = e.screenY //
			var mouseStartLeft = e.screenX //
			var ratioWH = parentLiWidth / parentLiHeight
			var Vue = this
			e.stopPropagation()
			switch(dataDir) {
				case 'n': //	
				case 's':					
					$(document).on('mousemove', function(e) {
						if(dataDir =='n'){
							var diffWidth = (e.screenX - mouseStartLeft) * Math.sin(Math.PI * Vue.angle / 180).toFixed(3)
							var diffHeight = (mouseStartTop - e.screenY) * Math.cos(Math.PI * Vue.angle / 180).toFixed(3)
						}else {
							var diffWidth = (mouseStartLeft - e.screenX) * Math.sin(Math.PI * Vue.angle / 180).toFixed(3)
							var diffHeight =(e.screenY - mouseStartTop ) * Math.cos(Math.PI * Vue.angle / 180).toFixed(3)
						}					
						
						if((diffHeight + diffWidth + parentLiHeight) <= 0) {
							return false
						}
						var moveSize = [];
						Vue.$store.state.ElementData.forEach(function(val, i) {
							if(val.id == Vue.$store.state.focusPage) {
								for(var t = 0; t < val.children.length; t++) {
									if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1) {
										moveSize[t] = {
											w:parseFloat(val.children[t].BoxStyleData.width),
											h:parseFloat(val.children[t].BoxStyleData.height)
										}
										val.children[t].BoxStyleData.height = diffHeight + diffWidth + arrSize[t].h + 'px';
										val.children[t].BoxStyleData.top = parseFloat(val.children[t].BoxStyleData.top) - (parseFloat(val.children[t].BoxStyleData.height) - moveSize[t].h) / 2 + "px";									
									}
								}
								return
							}
						})
					})
					break;
				case 'w': //	
				case 'e': //
					$(document).on('mousemove', function(e) {
						if(dataDir =='w'){
							var diffWidth = (mouseStartLeft - e.screenX) * Math.cos(Math.PI * Vue.angle / 180).toFixed(3)
							var diffHeight = (e.screenY - mouseStartTop) * Math.sin(Math.PI * Vue.angle / 180).toFixed(3)
						}else {
							var diffHeight = (mouseStartTop -e.screenY ) * Math.sin(Math.PI * Vue.angle / 180).toFixed(3)
							var diffWidth = ( e.screenX -mouseStartLeft) * Math.cos(Math.PI * Vue.angle / 180).toFixed(3)
						}
						if((parentLiWidth + diffWidth - diffHeight) <= 0) {
							return false
						}						
						var moveSize = [];		
						Vue.$store.state.ElementData.forEach(function(val, i) {
							if(val.id == Vue.$store.state.focusPage) {
								for(var t = 0; t < val.children.length; t++) {
									if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1) {
										moveSize[t] = {
											w:parseFloat(val.children[t].BoxStyleData.width),
											h:parseFloat(val.children[t].BoxStyleData.height)
										}
										val.children[t].BoxStyleData.width =  diffWidth -diffHeight  + arrSize[t].w + 'px';					
										val.children[t].BoxStyleData.left = parseFloat(val.children[t].BoxStyleData.left) - (parseFloat(val.children[t].BoxStyleData.width) - moveSize[t].w) / 2 + "px";
										
									}
								}
								return
							}
						})
					})
					break;
				case 'ne':
				case 'se':
				case 'nw':
				case 'sw':
					$(document).on('mousemove', function(e) {
						var diffWidth = (mouseStartLeft - e.screenX) * Math.cos(Math.PI * Vue.angle / 180).toFixed(3) + (-e.screenY + mouseStartTop) * Math.sin(Math.PI * Vue.angle / 180).toFixed(3)
						var diffHeight = diffWidth / ratioWH
						if(dataDir == 'se' || dataDir=='ne'){
							if((parentLiWidth - diffWidth) <= 0 || (parentLiHeight - diffHeight) <= 0) {
								return false
							}
						}else {
							if((parentLiWidth + diffWidth) <= 0 || (parentLiHeight + diffHeight) <= 0) {
								return false
							}
						}												
						var moveSize = [];		
						Vue.$store.state.ElementData.forEach(function(val, i) {
							if(val.id == Vue.$store.state.focusPage) {
								for(var t = 0; t < val.children.length; t++) {
									if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1) {
										moveSize[t] = {
											w:parseFloat(val.children[t].BoxStyleData.width),
											h:parseFloat(val.children[t].BoxStyleData.height),							
										}
										diffHeight = diffWidth/arrSize[t].r

										if(dataDir == 'se' || dataDir=='ne'){
											val.children[t].BoxStyleData.width =  arrSize[t].w  - diffWidth + 'px';	
											val.children[t].BoxStyleData.height =  arrSize[t].h - diffHeight +  'px';

										}else {
											val.children[t].BoxStyleData.width =  arrSize[t].w  + diffWidth + 'px';	
											val.children[t].BoxStyleData.height =  arrSize[t].h + diffHeight +  'px';
											
										}	
										val.children[t].BoxStyleData.left = parseFloat(val.children[t].BoxStyleData.left) - (parseFloat(val.children[t].BoxStyleData.width) - moveSize[t].w) / 2 + "px";															
											val.children[t].BoxStyleData.top = parseFloat(val.children[t].BoxStyleData.top) - (parseFloat(val.children[t].BoxStyleData.height) - moveSize[t].h) / 2 + "px";
									}
								}
								return
							}
						})
						
					})
					break;
			}

		},
		//
		cacheData() {		
			var Vue = this;
			var PageData = '';
			var isHasPage = false;
			this.$store.state.ElementData.forEach(function(val, i) {
				if(val.id == Vue.$store.state.focusPage) {
					PageData = JSON.parse(JSON.stringify(val))
				}
			})
			this.$store.state.cacheElementData.forEach(function(val, i) {
				if(val.id == Vue.$store.state.focusPage) {
					//
					if(JSON.stringify(val.data[val.data.length - 1]) == JSON.stringify(PageData)) {} else {
						//
						val.data = val.data.slice(0, val.index + 1)
						val.data.push(JSON.parse(JSON.stringify(PageData)))
						val.index = val.data.length - 1
					}
					isHasPage = true;
				}
			})
			if(!isHasPage) {
				this.$store.state.cacheElementData.push({
					id: Vue.$store.state.focusPage,
					index: 0,
					data: [JSON.parse(JSON.stringify(PageData))]
				})
			}

		},
		getStyle(obj, attr) {
			if(obj.currentStyle) {
				return obj.currentStyle[attr];
			} else if(getComputedStyle) {
				var styleVal = getComputedStyle(obj, null)[attr] ?
					getComputedStyle(obj, null)[attr] :
					getComputedStyle(obj, null).getPropertyValue(attr);
				return styleVal;
			}
		},
		//BOX
		changeBoxCursor(e) {
			var Vue = this;
			var circleELE = $(e.currentTarget)
			var dataDir = circleELE.data('dir') //
			switch(dataDir) {
				case 'n': //	
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'n-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'e-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'n-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'e-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'n-resize')
					}
					break;
				case 's': //			
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'n-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'e-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'n-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'e-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'n-resize')
					}
					break;
				case 'w': //	
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'e-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'n-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'e-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'n-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'e-resize')
					}
					break;
				case 'e': //			
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'e-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'n-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'e-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'n-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'e-resize')
					}
					break;
				case 'ne':
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'ne-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'se-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'ne-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'se-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'ne-resize')
					}
					break;
				case 'se':
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'se-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'ne-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'se-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'ne-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'se-resize')
					}
					break;
				case 'nw':
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'se-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'ne-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'se-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'ne-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'se-resize')
					}
					break;
				case 'sw':
					if(0 <= Vue.angle && Vue.angle <= 45) {
						circleELE.css('cursor', 'ne-resize')
					} else if(45 < Vue.angle && Vue.angle < 135) {
						circleELE.css('cursor', 'se-resize')
					} else if(135 <= Vue.angle && Vue.angle < 225) {
						circleELE.css('cursor', 'ne-resize')
					} else if(225 <= Vue.angle && Vue.angle < 315) {
						circleELE.css('cursor', 'se-resize')
					} else if(315 <= Vue.angle && Vue.angle <= 360) {
						circleELE.css('cursor', 'ne-resize')
					}
					break;
			}
		},
		
		//
		rotateBox(e) {
			var Vue = this
			var ele = $(e.currentTarget)
			var sx = e.clientX
			var sy = e.clientY

			var main = document.getElementById('main')
			var eleBox = ele.parents('.item-comp')
			var mainL = main.getBoundingClientRect().left + parseFloat(this.getStyle(main, 'borderLeftWidth'))* Vue.$store.state.scaleRatio / 100
			var mainT = main.getBoundingClientRect().top + parseFloat(this.getStyle(main, 'borderTopWidth'))* Vue.$store.state.scaleRatio / 100
			var cx = parseFloat(this.getStyle(eleBox[0], 'left'))* Vue.$store.state.scaleRatio / 100 + mainL + eleBox.outerWidth() / 2* Vue.$store.state.scaleRatio / 100
			var cy = parseFloat(this.getStyle(eleBox[0], 'top'))* Vue.$store.state.scaleRatio / 100 + mainT + eleBox.outerHeight() / 2* Vue.$store.state.scaleRatio / 100
			var oldAngle = parseFloat((Vue.BoxStyleData.transform.split('(')[1]));
			var arrFocusAngle = [];
			this.isShowAngle = true;
			var rotateMove = 0;
			Vue.$store.state.ElementData.forEach(function(val, i) {
				if(val.id == Vue.$store.state.focusPage) {
					for(var t = 0; t < val.children.length; t++) {
						if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1 && val.children[t].id != Vue.id) {
							var focusPartAngle = parseFloat((val.children[t].BoxStyleData.transform.split('(')[1]));
							arrFocusAngle.push(focusPartAngle)
						}
					}
					return
				}
			})
			e.stopPropagation()
			$(document).on('mousemove', function(e) {
				rotateMove ++ 
				if(rotateMove>=2){
					Vue.isClearFocusPart = false;
				}
				var mx = e.clientX
				var my = e.clientY
				var x = Math.abs(mx - cx)
				var y = Math.abs(my - cy)
				var z = Math.sqrt(Math.pow(x, 2) + Math.pow(y, 2))
				var sin = x / z
				var radina = Math.asin(sin);
				var num = 0;
				Vue.angle = Math.floor(180 / (Math.PI / radina)); //		        
				if(mx > cx && my > cy) { //
					Vue.angle = 180 - Vue.angle;
				}
				if(mx == cx && my > cy) { //y
					Vue.angle = 180;
				}

				if(mx > cx && my == cy) { //x
					Vue.angle = 90;
				}

				if(mx < cx && my > cy) { //
					Vue.angle = 180 + Vue.angle;
				}

				if(mx < cx && my == cy) { //x
					Vue.angle = 270;
				}
				if(mx < cx && my < cy) { //
					Vue.angle = 360 - Vue.angle;
				}				
				Vue.$store.state.ElementData.forEach(function(val, i) {
					if(val.id == Vue.$store.state.focusPage) {
						for(var t = 0; t < val.children.length; t++) {
							if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1 && val.children[t].id != Vue.id) {								
								var newangle = Vue.angle-oldAngle+ arrFocusAngle[num]
	
								if(newangle<0){
									newangle = newangle + 360
								}
								if(newangle>360){
									newangle = newangle - 360
								}
								val.children[t].BoxStyleData.transform = 'rotateZ(' + newangle + 'deg)';
								num ++
							}
						}
						return
					}
				})
				Vue.BoxStyleData.transform = 'rotateZ(' + Vue.angle + 'deg)';
			})

		},
		//
		initEditBoxStyle() {
			var Vue = this
			this.$store.state.ElementData.forEach(function(val, index) {
				if(!val.children) {
					return false
				}
				for(var i = 0; i < val.children.length; i++) {
					if(val.children[i].id == Vue.id) {
						if(!val.children[i].BoxStyleData.zIndex) {
							Vue.BoxStyleData = JSON.parse(JSON.stringify(Vue.$store.state.startEditBoxStyle))
							//							val.children[i].BoxStyleData = Vue.BoxStyleData
						} else {
							//							Vue.eleBoxStyle = val.children[i].BoxStyleData
							Vue.angle = Number(Vue.getNum(val.children[i].BoxStyleData.transform))
						}
					}
				}
			})
		},
		getNum(text) {
			return text.replace(/[^0-9]/ig, "");
		},
		openLoading() {
			this.loading = this.$loading({
				lock: true,
				text: 'Loading',
				spinner: 'el-icon-loading',
				background: 'rgba(0, 0, 0, 0.7)'
			});
		},
		closeLoading() {
			this.loading.close();
		},
	}
}


