<template>
	<div class="statistics-wraper">
		<div class="statistics-main">
			<el-container>
				<el-header height='auto'>
					<div class="top">
						<div class="cover-img">
							<img :src="cover_img==''?'./static/images/cover.png':$api+cover_img" />
						</div>
						<div class="info">
							<h3 class="title">{{title}}</h3>
							<p class="time">{{create_time}}</p>
							<p class="describe">{{describe}}</p>
						</div>
					</div>
				</el-header>
				<el-container>
					<el-aside width="140px">
						<div class="main-aside">
							<div class="item" :class="{'active': type == 'form'}" @click = "type = 'form'"></div>
							<div class="item" :class="{'active': type == 'link'}" @click = "type = 'link'"></div>
						</div>
					</el-aside>
					<el-main>
						<div class="main-table" v-show='type == "form"'>
							<el-table ref="form.arrData" border :data="form.arrData" tooltip-effect="dark" style="width: 100%;color: #777;">
								<el-table-column :label="item.title" v-for='(item,index) in form.arrLastData' :key='index'>
									<template slot-scope="scope">{{setTableData(JSON.parse(scope.row.form),item.id)}}</template>
								</el-table-column>
								<el-table-column label="" prop='create_time' v-if='form.total>0' fixed width='140px'>
									<template slot-scope="scope">{{utill.timestampToTime(scope.row.create_time) }}</template>
								</el-table-column>
							</el-table>
							<el-pagination v-if='form.total>0' background layout="prev, pager, next,total,jumper" @current-change='changeFormPage' :page-size='form.pageSize' :total="form.total"></el-pagination>
						</div>
						<div class="main-table" v-show='type == "link"'>
							<el-table ref="link.arrData" border :data="link.arrData" tooltip-effect="dark" style="width: 100%;color: #777;">
								<el-table-column prop="link" label=""></el-table-column>
								<el-table-column prop="ip" label="IP"></el-table-column>
								<el-table-column prop="visit_time" label="">
									<template slot-scope="scope">{{utill.timestampToTime(scope.row.visit_time)}}</template>	
								</el-table-column>
							</el-table>
							<el-pagination v-if='link.total>0' background layout="prev, pager, next,total,jumper" @current-change='changeLinkPage' :page-size='link.pageSize' :total="link.total"></el-pagination>
						</div>
					</el-main>
				</el-container>
			</el-container>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: this.$route.params.id,
				type:"form",
				form: {
					arrData: [],
					arrFormData: [],
					arrLastData: [],
					pageSize: 12, //
					total: 0, //
					page: 1, //
				},
				link: {
					pageSize: 12, //
					total: 0, //
					page: 1, //
					arrData: []
				},
				title: '',
				cover_img: '',
				describe: '',
				create_time: '',
			}
		},
		created() {
			if(this.utill.getCookie('username')) {
				this.$store.state.user.username = this.utill.getCookie('username')
				this.axiosGetSceneData();
				this.axiosGetLinkInfo();
			} else {
				this.$router.push({
					name: 'home'
				});
			}

		},
		methods: {
			axiosGetLinkInfo() {
				var Vue = this
				this.http.axiosGetLinkInfo({
					id: Vue.id,
					page: Vue.link.page,
					pageSize: Vue.link.pageSize,
				}).then((response) => {
					Vue.closeLoading()
					Vue.link.arrData = response.data.data;
					Vue.link.total = parseInt(response.data.count);

				})
			},
			axiosGetSceneForm() {
				var Vue = this
				this.http.axiosGetSceneForm({
					id: Vue.id,
					page: Vue.form.page,
					pageSize: Vue.form.pageSize,
				}).then((response) => {
					Vue.closeLoading()
					Vue.form.total = parseInt(response.data.count)
					Vue.form.arrData = response.data.data
					for(var i = 0; i < Vue.form.arrData.length; i++) {
						Vue.form.arrFormData.push(JSON.parse(Vue.form.arrData[i].form))
					}
					if(Vue.form.arrLastData.length <= 0) {
						Vue.form.arrLastData = Vue.form.arrFormData[0]
					}
				})
			},
			axiosGetSceneData() {
				this.openLoading()
				var Vue = this
				this.http.axiosGetSceneData({
					id: Vue.id,
					username: this.$store.state.user.username
				}).then((res) => {
					Vue.describe = res.data.info.describe
					Vue.cover_img = res.data.info.cover_img
					Vue.title = res.data.info.title
					Vue.create_time = Vue.utill.timestampToTime(Number(res.data.info.create_time))
					Vue.axiosGetSceneForm()
				})
			},
			changeLinkPage(val) {
				this.link.page = val
				this.axiosGetSceneForm()
			},
			changeFormPage(val) {
				this.form.page = val
				this.axiosGetSceneForm()
			},
			//
			setTableData(data, id) {
				for(var i = 0; i < data.length; i++) {
					if(data[i].id == id) {
						return data[i].data
					}
				}
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>


