<template>
	<div class="HomePage">
		<div class="home-swiper">
			<div class="swiper-container">
				<div class="swiper-wrapper">
					<div class="swiper-slide" v-for="slide in swiperSlides" :key="slide.id" :style="'background-image:url('+$api+slide.picurl+')'">
					</div>
				</div>
				<div class="swiper-pagination" slot="pagination"></div>
			</div>
		</div>
		<div class="section">
			<div class="introduct" id="arrow">
				<h2></h2>
				<span> </span>
				<p>H5</p>
				<p>   </p>
				<div v-if='!$store.state.user.username' class="reg" @click='$store.state.isShowRegister = true'></div>
			</div>
		</div>
	</div>
</template>
<script>
	import Swiper from 'swiper';	
	export default {
		name: 'Home',
		data() {
			return {
				swiperSlides: [],
			}
		},
		created() {
			var Vue = this;
			this.openLoading()
			this.http.axiosSelectAllCarousel()
				.then(function(response) {
					Vue.swiperSlides = response.data.data
					Vue.$nextTick(() => {
						Vue.closeLoading()
						Vue.createSwiper()
					})
				})
				.catch(function(error) {
					console.log(error);
			});			
		},
		mounted() {

		},
		methods: {
			createSwiper() {
				var Vue = this;
				var mySwiper = new Swiper('.swiper-container', {
					autoplay: 3000,
					loop: true,
					pagination: '.swiper-pagination',
					paginationType: 'bullets',
					autoplayDisableOnInteraction: false,
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>


