<template>
	<div class="login-box">
		<div class="account-login" v-show='type==1'>
			<el-form :model="LoginForm" status-icon :rules="LoginRule" ref="LoginForm" class="demo-ruleForm">
				<el-form-item label="" prop="user">
					<el-input type="text"  v-model.trim="LoginForm.user" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="pass">
					<el-input type="password" v-model.trim="LoginForm.pass" autocomplete="off"></el-input>
				</el-form-item>
				<el-form-item>
					<el-row :gutter="30">
					  	<el-col :span="24">
					  		<el-button type="primary" @click="submitForm('LoginForm')" style='width: 100%;margin-top: 10px;'></el-button>
					  	</el-col>			  	
					</el-row>
				</el-form-item>
			</el-form>
			<!--<div class="other-login">
				<p class="tip"><span></span></p>
				<img class="wx-icon" style="width: 48px;" @click='changeLoginType(2)' src="../assets/images/wx_icon.png"/>	
			</div>-->
		</div>
		<!--<div class="wx-login" v-show='type==2'>
			<qrcode-vue :value='WX.code'  level="H" size='200' ></qrcode-vue>
			<div class="other-login">
				<p class="tip"><span></span></p>
				<i class="account-icon fa fa-user-circle" @click='changeLoginType(1)' aria-hidden="true"></i>
			</div>
		</div>-->
	</div>
</template>

<style scoped lang="scss">
	.login-box {padding: 0 60px}
	.el-form-item{		
		margin-bottom: 12px;
	}
	.wx-login {
		text-align: center;
		.account-icon {
			cursor: pointer;
			margin-top: 20px;
			color: #1C9DFA;
			font-size: 48px;
		}
	}
	.other-login {
		margin-top: 20px;
		text-align: center;
		.wx-icon {
			cursor: pointer;
			margin-top: 20px;
		}
		.tip {
			text-align: center;
			height: 30px;
			line-height: 30px;
			position: relative;			
			span {
				font-size: 12px;
				color: #76838f;
				position: relative;
				background-color: #fff;
				z-index: 2;
				padding: 0 25px;
			}
			&:after {
				content: '';
				position: absolute;
				width: 100%;
				height: 1px;
				top: 15px;
				left: 0;
				background-color: #ccd5db;
				z-index: 1;
			}
		}
	}
</style>
<script>
	import QrcodeVue from 'qrcode.vue';
	export default {
		data() {
			var checkUser = (rule, value, callback) => {
				if(!value) {
					return callback(new Error(''));
				}else if(value.length<2 || value.length>12){
					return callback(new Error(''));
				}else {
					callback();
				}				
			};
			var validatePass = (rule, value, callback) => {
				if(value === '') {
					callback(new Error(''));
				} else {
					callback();
				}
			};
			return {
				type:1,
				WX:{
					code:''
				},
				LoginForm: {
					pass: '',
					delivery: true,
					user: ''
				},
				LoginRule: {
					pass: [{
						validator: validatePass,
						trigger: 'blur'
					}],
					user: [{
						validator: checkUser,
						trigger: 'blur'
					}]
				}
			};
		},		
		created(){
			var Vue = this;
			this.http.axiosLoginWx({
				back_url:window.location.href,
			}).then(function(res){
				Vue.WX.code = res.data;
				console.log(res.data)
			})
		},
		components: {
			QrcodeVue
		},
		mounted(){
			this.$nextTick(()=>{
				document.addEventListener('keydown',this.keydownEnter,false)			
			})
		},
		beforeDestroy(){			
			document.removeEventListener('keydown',this.keydownEnter,false)	
		},
		methods: {
			//
			changeLoginType(type){
				this.type = type;
				this.$set('changeLoginType',type)
			},
			keydownEnter(event){
				if(event.keyCode == 13){
			this.submitForm('LoginForm')
			}
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.axiosLogin()
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$store.state.isShowLogin = false;
				this.$refs[formName].resetFields();
			},
			initLogin(){
				this.LoginForm.pass = '';
				this.LoginForm.delivery = true;
				this.LoginForm.user = '';
			},
			axiosLogin(){
				this.openLoading() 
				this.http.axiosLogin({
					username: this.LoginForm.user,
					password: this.utill.getAES(this.LoginForm.pass)
				}).then((response) => {
					this.closeLoading() 
					if(response.data.status == 1) {
						this.$message.success(response.data.msg)
						this.utill.setCookie('username',this.LoginForm.user)
						this.$store.state.user.username = response.data.data.username
						this.$store.state.user.name = response.data.data.name
						this.$store.state.user.phone = response.data.data.phone
						this.$store.state.user.department = response.data.data.department
						this.$store.state.user.create_time = response.data.data.create_time
						this.$store.state.user.grade = response.data.data.grade
						if(response.data.data.headSrc){
							this.$store.state.user.headSrc = response.data.data.headSrc
						}
						this.$emit('closeLogin')
					} else {
						this.$message.error(response.data.msg)
					}	
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>


