<template>
	<div class="list-page">
		<div class="list-nav">
			<div class="wraper">
				<div class="create">
					<h3></h3>
					<el-button type="primary" icon="el-icon-plus" @click='createEdit'></el-button>
				</div>
			</div>
		</div>
		<ul class="list-edit">
			<li class="list-blank" @click='createEdit'>
				<i class="el-icon-plus"></i>
				<p></p>
			</li>
			<li v-for="(item,index) in arrTemplate" class="list-edit-item">
				<div class="ribbon">
					<span class="ribbon-inner modify" v-if='item.issue_update_time != item.update_time && item.issue=="1"'>  </span>
					<span class="ribbon-inner issue" v-else-if='item.issue=="1"'>  </span>
					<span class="ribbon-inner unissue" v-else>  </span>
				</div>
				<div class="sign" v-if='item.template==1 && $store.state.user.grade==2'>
					<img src="../../assets/images/qy.png"/>
				</div>
				<img :src="item.cover_img==''?'./static/images/cover.png':$api+item.cover_img" alt="" />
				<div class="list-edit-back ">
					<div class="item animated slideInDown">
						<!--<router-link :to="'/detail/'+item.edit_id">-->
						<div class="icon" @click="ToDetail(item,'MyScene_Share')"><i class="el-icon-view"></i></div>
						<!--</router-link>-->
						<p></p>
					</div>
					<div class="item animated slideInDown">
						<router-link :to="'/edit/'+item.edit_id" target="_blank">
							<div class="icon"><i class="el-icon-edit-outline"></i></div>
						</router-link>
						<p></p>
					</div>
					<div class="item animated slideInDown">
						<div class="icon" @click="delData(item.edit_id,item.username)"><i class="el-icon-delete"></i></div>
						<p></p>
					</div>
				</div>
				<div class="list-edit-bottom">
					<h4>{{item.title}}</h4>
					<div class="statistics">
						<span><i class="el-icon-view"></i>{{item.accessCount}}</span>
						<span class="link" @click="ToDetail(item,'MyScene_Statistics')" style="cursor: pointer;"><i class="fa fa-bar-chart statistics-form" aria-hidden="true"></i>{{item.formCount}}</span>
					</div>
					<div class="opera">
						<i class="fa fa-qrcode qrcode-icon" aria-hidden="true" @mouseenter="showQrcode(item)" @mouseleave="hideQrcode(item)"></i>
						<ul class="opera-list">
							<li class="oprera-copy" @click="axiosCopyMyScene(item.edit_id)"><i class="fa fa-clipboard" aria-hidden="true"></i></li>
							<li class="oprera-issue" @click='issueSceneNow(item.edit_id)' v-if='item.issue=="0" ||(item.issue_update_time != item.update_time && item.issue=="1")'><i class="fa fa-paper-plane" aria-hidden="true"></i></li>					
							<li class="oprera-template" @click='updateTemplateScene(item,1)' v-if='item.template==0 && $store.state.user.grade==2'><i class="fa fa-list-alt" aria-hidden="true"></i></li>
							<li class="oprera-template" @click='updateTemplateScene(item,0)' v-if='item.template==1 && $store.state.user.grade==2' ><i class="fa fa-list-alt" aria-hidden="true"></i></li>
						</ul>
					</div>
				</div>
				<div class="qrcode-box" v-show='item.isShowCode'>
					<qrcode-vue class='qrcode animated zoomIn' :value='$link+"#/"+item.edit_id+"/"+$store.state.user.username' :size="160" level="H"></qrcode-vue>
				</div>

			</li>
		</ul>
		<div class="list-page" style="width: 1180px;margin: 0 auto;">
			<el-pagination background layout="prev, pager, next,total,jumper" @current-change='changePage' :page-size='pageSize' :total="total"></el-pagination>
		</div>
	</div>
</template>
<script>
	import QrcodeVue from 'qrcode.vue';
	export default {
		components: {
			QrcodeVue
		},
		data() {
			return {
				pageSize: 11, //
				total: 0, //
				page: 1, //
				title: '',
				arrTemplate: [],
			}
		},
		mounted() {
			if(this.utill.getCookie('username')) {
				this.$store.state.user.username = this.utill.getCookie('username')
				this.axiosGetScene()
			} else {
				this.$store.state.user.username = ''
				this.$router.push({
					name: 'home'
				});
			}
		},
		methods: {
			//
			issueScene(id) {
				var Vue = this;
				this.openLoading()
				return this.http.axiosIssueScene({
					id: id,
					issue: 1,
					username: this.$store.state.user.username
				})
			},
			issueSceneNow(id) {
				this.issueScene(id).then((res) => {
					this.axiosGetScene()
				})
			},
			//
			showQrcode(item) {
				this.$set(item, 'isShowCode', true)
			},
			//
			hideQrcode(item) {
				this.$set(item, 'isShowCode', false)
			},
			//
			ToDetail(item,link) {
				if(item.issue_update_time == item.update_time && item.issue == 1) {
					this.$router.push({
						name: link,
						params: {
							name: link,
							id: item.edit_id,
						}
					})
				} else {
					if(item.issue == 0) {
						this.$confirm('?', '', {
							confirmButtonText: '',
							cancelButtonText: '',
							type: 'warning'
						}).then(() => {
							this.issueScene(item.edit_id).then((res) => {
								this.closeLoading()
								if(res.data.status == 1){
									this.$router.push({
										name: link,
										params: {
											name: link,
											id: item.edit_id,
										}
									})
								}

							})
						}).catch(() => {});
					} else {
						this.$confirm('?', '', {
							confirmButtonText: '',
							cancelButtonText: '',
							type: 'warning'
						}).then(() => {
							this.issueScene(item.edit_id).then((res) => {
								this.closeLoading()
								if(res.data.status == 1){
									this.$router.push({
										name: 'MyScene_Detail',
										params: {
											name: 'MyScene_Detail',
											id: item.edit_id,
										}
									})
								}
							})
						}).catch(() => {
							this.$router.push({
								name: 'MyScene_Detail',
								params: {
									name: 'MyScene_Detail',
									id: item.edit_id,
								}
							})
						});
					}

				}
			},
			createEdit() {
				this.$store.state.user.username = this.utill.getCookie('username')
				if(this.$store.state.user.username) {} else {
					this.$router.push({
						name: 'home'
					});
					this.$store.state.isShowLogin = true
					return false
				}
				let routeData = this.$router.resolve({
					name: "edit",
					params: {
						name: 'edit',
						id: this.$store.state.user.username+this.utill.GenNonDuplicateID(5)
					},
				});
				window.open(routeData.href, '_blank');
			},
			changePage(val) {
				this.page = val
				this.axiosGetScene()
			},
			delData(id, username) {
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.axiosDelScene(id, username)
				}).catch(() => {});
			},
			axiosDelScene(id, username) {
				var Vue = this
				this.openLoading()
				this.http.axiosDelScene({
					id: id,
					username: username
				}).then((res) => {
					if(res.data.status == 1) {
						this.$message.success('')
						this.axiosGetScene()
					} else {
						this.$message.error('')
					}
				})
			},
			axiosCopyMyScene(id){
				var Vue = this
				var new_id = this.utill.random(2) + new Date().getTime()
				this.openLoading()
				this.http.axiosCopyMyScene({
					id: new_id,
					copy_id:id,
					username: this.$store.state.user.username
				}).then((res) => {
					if(res.data.status ==1){
						this.$router.push({
							name: 'edit',
							params: {
							name: 'edit',
							id: new_id
						},
						});
					}
					this.closeLoading()
				})
			},
			axiosGetScene() {
				var Vue = this
				this.openLoading()
				this.http.axiosGetScene({
					page: this.page,
					pageSize: this.pageSize,
					username: this.$store.state.user.username
				}).then((res) => {
					Vue.arrTemplate = res.data.data
					Vue.total = Number(res.data.count)
					this.closeLoading()
				})
			},
			updateTemplateScene(item, type) {
				if(type == 0){
					this.axiosUpdateTemplateScene(item,type)
				}else {
					if(item.issue_update_time == item.update_time && item.issue == 1) {
						this.axiosUpdateTemplateScene(item,type)
					} else {
						if(item.issue == 0) {
							this.$confirm('?', '', {
								confirmButtonText: '',
								cancelButtonText: '',
								type: 'warning'
							}).then(() => {
								this.issueScene(item.edit_id).then((res) => {
									if(res.data.status == 1){
										this.axiosGetScene()
										this.axiosUpdateTemplateScene(item,type)
									}else {
										this.$message.error('')
									}
									
								})
							}).catch(() => {});
						} else {
							this.$confirm('?', '', {
								confirmButtonText: '',
								cancelButtonText: '',
								type: 'warning'
							}).then(() => {
								this.issueScene(item.edit_id).then((res) => {
									if(res.data.status == 1){
										this.axiosGetScene()
										this.axiosUpdateTemplateScene(item,type)
									}else {
										this.$message.error('')
									}
								})
							}).catch(() => {
								this.axiosUpdateTemplateScene(item,type)
							});
						}
					}
				}
			},
			axiosUpdateTemplateScene(item, type) {
				var Vue = this
				this.openLoading()
				this.http.axiosUpdateTemplateScene({
					id: item.edit_id,
					type: type,
					username: this.$store.state.user.username
				}).then((res) => {
					if(res.data.status == 1) {
						item.template = type
					}
					this.closeLoading()
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>


