<template>
	<div id="templet">
		<div class="templet-aside">
			<li class='item active'>
				<i class="icon fa fa-columns" aria-hidden="true"></i>
				<p class="title"></p>
				<i class="caret fa fa-caret-left" aria-hidden="true"></i>
			</li>
		</div>
		<div class="templet-main">
			<div class="templet-nav">
				<li class="item" :class="{active:templatePage.nav==2}" @click='templatePage.nav=2'></li>
				<li class="item" :class="{active:templatePage.nav==1}" @click='templatePage.nav=1'></li>
			</div>
			<!---->
			<div class="templet-list" v-if='templatePage.nav==2'>
				<el-scrollbar style='height: 100%;margin: 0;'>
					<ul class="templet-list-box">
						<img class="empty-icon" src="@/assets/images/empty.png" v-if='templatePage.center.data.length<=0' />
						<li class="item" :title='items.data.name' v-for="(items,index) in templatePage.center.data " @click='useTemplatePage(items,2)'>
							<div class="template-layer">
								<p></p>
							</div>
							<i v-if='templatePage.nav==1' class="template-delete el-icon-delete" @click.stop='delTemplatePage(items,index)'></i>
							<div class="item-wraper pointer-none" :style="{
								opacity:items.data.style.opacity,
								backgroundColor:items.data.style.backgroundColor,
								backgroundImage:'url('+(items.data.style.backgroundImageUrl?($api+items.data.style.backgroundImageUrl):'')+')',
							}">
								<template v-for='mains in items.data.children'>
									<template-list :items='mains' :index='0' :pageActiveIndex='0'></template-list>
								</template>
							</div>
						</li>
						<el-pagination v-if='templatePage.center.count>templatePage.pageSize' :pager-count="templatePage.pagerCount" style='margin-top: 12px;font-weight: 100;' background layout="pager" :current-page.sync="templatePage.center.page" :page-size='templatePage.pageSize' :total="templatePage.center.count" @current-change="changetemplatePageCenter"></el-pagination>
					</ul>
				</el-scrollbar>
			</div>
			<!--  -->
			<div class="templet-list" v-if='templatePage.nav== 1'>
				<el-row :gutter="10" class='templet-list-nav' type='flex'>
					<el-col><span :class="{active:templatePage.person.nav==1}" @click="templatePage.person.nav=1"></span></el-col>
					<el-col v-if='this.$store.state.user.grade ==2'><span :class="{active:templatePage.person.nav==2}" @click="templatePage.person.nav=2"></span></el-col>
					<el-col><span :class="{active:templatePage.person.nav==3}" @click="templatePage.person.nav=3"></span></el-col>
				</el-row>
				<el-scrollbar style='height: calc(100% - 40px);margin: 0;' v-show='templatePage.person.nav==1'>
					<ul class="templet-list-box">
						<img class="empty-icon" src="@/assets/images/empty.png" v-if='templatePage.person.my.data.length<=0' />
						<li class="item" v-for="(items,index) in templatePage.person.my.data " @click='useTemplatePage(items,1)'>
							<div class="template-layer">
								<p></p>
							</div>
							<i v-if='templatePage.nav==1' class="template-delete el-icon-delete" @click.stop='delTemplatePage(items,index,1)'></i>
							<div class="item-wraper pointer-none" :style="{
								opacity:items.data.style.opacity,
								backgroundColor:items.data.style.backgroundColor,
								backgroundImage:'url('+(items.data.style.backgroundImageUrl?($api+items.data.style.backgroundImageUrl):'')+')',
							}">
								<template v-for='mains in items.data.children'>
									<template-list :items='mains' :index='0' :pageActiveIndex='0'></template-list>
								</template>
							</div>
						</li>
						<el-pagination v-if='templatePage.person.my.count>templatePage.pageSize' :pager-count="templatePage.pagerCount" style='margin-top: 12px;font-weight: 100;' background layout="pager" :current-page.sync="templatePage.person.my.page" :page-size='templatePage.pageSize' :total="templatePage.person.my.count" @current-change="changetemplatePagePersonMy"></el-pagination>
					</ul>
				</el-scrollbar>
				<el-scrollbar style='height: calc(100% - 40px);margin: 0;' v-show='templatePage.person.nav==2'>
					<ul class="templet-list-box">
						<img class="empty-icon" src="@/assets/images/empty.png" v-if=' templatePage.person.center.data.length<=0' />
						<li class="item" v-for="(items,index) in templatePage.person.center.data " @click='useTemplatePage(items,2)'>
							<div class="template-layer">
								<p></p>
							</div>
							<i v-if='templatePage.nav==1' class="template-delete el-icon-delete" @click.stop='delTemplatePage(items,index,2)'></i>
							<div class="item-wraper pointer-none" :style="{
								opacity:items.data.style.opacity,
								backgroundColor:items.data.style.backgroundColor,
								backgroundImage:'url('+(items.data.style.backgroundImageUrl?($api+items.data.style.backgroundImageUrl):'')+')',
							}">
								<template v-for='mains in items.data.children'>
									<template-list :items='mains' :index='0' :pageActiveIndex='0'></template-list>
								</template>
							</div>
						</li>
						<el-pagination v-if='templatePage.person.center.count>templatePage.pageSize' :pager-count="templatePage.pagerCount" style='margin-top: 12px;font-weight: 100;' background layout="pager" :current-page.sync="templatePage.person.center.page" :page-size='templatePage.pageSize' :total="templatePage.person.center.count" @current-change="changetemplatePagePersonCenter"></el-pagination>
					</ul>
				</el-scrollbar>
				<el-scrollbar style='height: calc(100% - 40px);margin: 0;' v-show='templatePage.person.nav==3'>
					<ul class="templet-list-box">
						<img class="empty-icon" src="@/assets/images/empty.png" v-if=' templatePage.person.history.data.length<=0' />
						<li class="item" v-for="(items,index) in templatePage.person.history.data " @click='useTemplatePage(items)'>
							<div class="template-layer">
								<p></p>
							</div>
							<i v-if='templatePage.nav==1' class="template-delete el-icon-delete" @click.stop='delTemplatePage(items,index,3)'></i>
							<div class="item-wraper pointer-none" :style="{
								opacity:items.data.style.opacity,
								backgroundColor:items.data.style.backgroundColor,
								backgroundImage:'url('+(items.data.style.backgroundImageUrl?($api+items.data.style.backgroundImageUrl):'')+')',
							}">
								<template v-for='mains in items.data.children'>
									<template-list :items='mains' :index='0' :pageActiveIndex='0'></template-list>
								</template>
							</div>
						</li>
						<el-pagination v-if='templatePage.person.history.count>templatePage.pageSize' :pager-count="templatePage.pagerCount" style='margin-top: 12px;font-weight: 100;' background layout="pager" :current-page.sync="templatePage.person.history.page" :page-size='templatePage.pageSize' :total="templatePage.person.history.count" @current-change="changetemplatePagehistory"></el-pagination>
					</ul>
				</el-scrollbar>
			</div>
		</div>
	</div>
</template>

<script>
	import TemplateList from '@/components/TemplateList'
	export default {
		components:{
			TemplateList
		},
		data() {
			return {
				//
				templatePage: {
					nav: 2,
					pagerCount: 5,
					pageSize: 36,
					center: { //
						page: 1,
						count: 0,
						data: []
					},
					person: {
						nav: 1,
						my: {
							page: 1,
							count: 0,
							data: []
						},
						center: {
							page: 1,
							count: 0,
							data: []
						},
						history: {
							page: 1,
							count: 0,
							data: []
						}
					},
				}, //
			}
		},
		created() {
			this.axiosSelectTemplatePage()
			this.axiosSelectMyTemplatePage()
			this.axiosSelectMyQyTemplatePage()
			this.axiosSelectMyHisTemplatePage()
		},
		methods: {
			//
			changeTemplatePageNav(index, e) {
				var _this = e.currentTarget
				if(this.templatePage.nav == index) {
					return false
				} else {
					this.templatePage.nav = index
					if(index == 2) {
						this.axiosSelectTemplatePage()
					} else {
						this.axiosSelectMyTemplatePage()
					}
				}
			},
			//
			useTemplatePage(item, type) {
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					var Vue = this;
					var pageData = JSON.parse(JSON.stringify(item.data))
					if(!pageData.children) {
						this.$message.error('')
					}
					pageData.children.forEach(function(val, i) {
						if(typeof val.animData == 'undefined') {
							Vue.$set(val, 'animData', [])
						}
					})
					this.$store.state.ElementData.forEach(function(val, i) {
						if(Vue.$store.state.focusPage == val.id) {
							Vue.$set(val, 'children', pageData.children)
							Vue.$set(val, 'style', pageData.style)
						}
					})
					this.$store.state.focusPart = [];
					this.$store.state.editNavNum = 1;
					this.axiosAddTemplatePageHistory(item.id, type)

				}).catch(() => {

				});

			},
			//
			delTemplatePage(data, index, type) {
				this.$confirm('?', '', {
						confirmButtonText: '',
						cancelButtonText: '',
						type: 'warning'
					}).then(() => {
						if(type == 3) {
							this.axiosDelMyTemplatePageHis(data.id, data.type, index)
						} else {
							this.axiosDelMyTemplatePage(data.id, index, type)
						}

					})
					.catch(() => {

					});
			},
			changetemplatePageCenter(page) {
				this.templatePage.center.page = page
				this.axiosSelectTemplatePage()
			},
			changetemplatePagePersonMy(page) {
				this.templatePage.person.my.page = page
				this.axiosSelectMyTemplatePage()
			},
			changetemplatePagePersonCenter(page) {
				this.templatePage.person.center.page = page
				this.axiosSelectMyQyTemplatePage()
			},
			changetemplatePagehistory(page) {
				this.templatePage.person.history.page = page
				this.axiosSelectMyHisTemplatePage()
			},

			axiosAddTemplatePageHistory(id, type) {
				this.http.axiosAddTemplatePageHistory({
					id: id,
					type: type,
					username: this.$store.state.user.username
				}).then((res) => {
					if(res.data.status == 1) {
						this.axiosSelectMyHisTemplatePage()

					}
				})
			},
			//
			axiosCreateTemplatePage(type) {
				var Vue = this;
				var pageData = '';
				this.$store.state.ElementData.forEach(function(val, index) {
					if(val.id == Vue.$store.state.focusPage) {
						pageData = JSON.parse(JSON.stringify(val))
					}
				})
				if(!pageData.children || pageData.children.length <= 0) {
					this.$message.error('')
					return false
				}
				this.openLoading();
				pageData.id = 'template_page_' + new Date().getTime();
				var title = pageData.name;
				this.http.axiosCreateTemplatePage({
					data: pageData,
					username: Vue.$store.state.user.username,
					type: type,
					title: title
				}).then((res) => {
					if(res.data.status == 1) {
						this.templatePage.nav = type
						if(type == 2) {
							this.axiosSelectTemplatePage()
							this.axiosSelectMyQyTemplatePage()
						} else {
							this.axiosSelectMyTemplatePage()
						}
	
					} else {
						this.closeLoading();
						this.$message.error('')
					}
				})
			},
			//
			axiosDelMyTemplatePageHis(id, type, index) {
				this.openLoading()
				this.http.axiosDelMyTemplatePageHis({
					id: id,
					username: this.$store.state.user.username,
					type: type
				}).then((res) => {
					this.closeLoading()
					if(res.data.status == 1) {
						this.templatePage.person.history.data.splice(index, 1)
					}
				})
			},
			//
			axiosDelMyTemplatePage(id, index, type) {
				this.openLoading()
				this.http.axiosDelMyTemplatePage({
					id: id,
					type: type,
					username: this.$store.state.user.username
				}).then((res) => {
					this.closeLoading()
					if(res.data.status == 1) {
						if(type == 1) {
							this.templatePage.person.my.data.splice(index, 1)
						} else if(type == 2) {
							this.templatePage.person.center.data.splice(index, 1)
							this.axiosSelectTemplatePage()
						}
						this.axiosSelectMyHisTemplatePage()
					}
				})
			},
			//
			axiosSelectTemplatePage() {
				this.openLoading()
				this.http.axiosSelectTemplatePage({
					page: this.templatePage.center.page,
					pageSize: this.templatePage.pageSize
				}).then((res) => {
					this.closeLoading()
					this.templatePage.center.data = []
					if(res.data.status == 1) {
						this.templatePage.center.data = res.data.data
						this.templatePage.center.count = parseFloat(res.data.count)
						for(var i = 0; i < this.templatePage.center.data.length; i++) {
							this.templatePage.center.data[i].data = JSON.parse(this.templatePage.center.data[i].data)
						}

					}
				})
			},
			//
			axiosSelectMyTemplatePage() {
				this.openLoading()
				this.http.axiosSelectMyTemplatePage({
					page: this.templatePage.person.my.page,
					pageSize: this.templatePage.pageSize,
					username: this.$store.state.user.username
				}).then((res) => {
					this.closeLoading()
					this.templatePage.person.my.data = []
					if(res.data.status == 1) {
						this.templatePage.person.my.data = res.data.data
						this.templatePage.person.my.count = parseFloat(res.data.count)

						for(var i = 0; i < this.templatePage.person.my.data.length; i++) {
							if(typeof(this.templatePage.person.my.data[i].data) == 'string') {
								this.templatePage.person.my.data[i].data = JSON.parse(this.templatePage.person.my.data[i].data)
							}

						}
					}
				})
			},
			//
			axiosSelectMyQyTemplatePage() {
				this.openLoading()
				this.http.axiosSelectMyQyTemplatePage({
					page: this.templatePage.person.center.page,
					pageSize: this.templatePage.pageSize,
					username: this.$store.state.user.username
				}).then((res) => {
					this.closeLoading()
					this.templatePage.person.center.data = []
					if(res.data.status == 1) {
						this.templatePage.person.center.data = res.data.data
						this.templatePage.person.center.count = parseFloat(res.data.count)
						for(var i = 0; i < this.templatePage.person.center.data.length; i++) {
							if(typeof(this.templatePage.person.center.data[i].data) == 'string') {
								this.templatePage.person.center.data[i].data = JSON.parse(this.templatePage.person.center.data[i].data)
							}

						}
					}
				})
			},
			//
			axiosSelectMyHisTemplatePage() {
				this.http.axiosSelectMyHisTemplatePage({
					page: this.templatePage.person.history.page,
					pageSize: this.templatePage.pageSize,
					username: this.$store.state.user.username
				}).then((res) => {
					this.templatePage.person.history.data = []
					if(res.data.status == 1) {
						this.templatePage.person.history.data = res.data.data
						this.templatePage.person.history.count = parseFloat(res.data.count)
						for(var i = 0; i < this.templatePage.person.history.data.length; i++) {
							if(typeof(this.templatePage.person.history.data[i].data) == 'string') {
								this.templatePage.person.history.data[i].data = JSON.parse(this.templatePage.person.history.data[i].data)
							}

						}
					}
				})
			},
			
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}

	}
</script>

<style>

</style>


