<template>
	<el-collapse-item title="" class='edit-addr-size' name='2'>
		<div class="align">
			<el-tooltip effect="dark" :open-delay='0' transition='el-zoom-in-bottom' content="" placement="top">
				<i class="iconfont icon-zuoduiqi" @click='alignLeft' aria-hidden="true"></i>
			</el-tooltip>
			<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
				<i class="iconfont icon-chuizhijuzhongduiqi" @click="alignLcenter" aria-hidden="true"></i>
			</el-tooltip>
			<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
				<i class="iconfont icon-youduiqi" @click="alignRight" aria-hidden="true"></i>
			</el-tooltip>
			<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
				<i class="iconfont icon-shangduiqi" @click="alignTop" aria-hidden="true"></i>
			</el-tooltip>
			<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
				<i class="iconfont icon-chuizhijunfen" @click="alignHcenter" aria-hidden="true"></i>
			</el-tooltip>
			<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
				<i class="iconfont icon-xiaduiqi" @click="alignBottom" aria-hidden="true"></i>
			</el-tooltip>
		</div>
		<el-form-item label="" class='address'>
			<span class="tip"></span>
			<el-input-number v-model='partData.width' size="small" controls-position="right"></el-input-number>
			</br>
			<span class="tip"></span>
			<el-input-number v-model='partData.height' size="small" controls-position="right"></el-input-number>
		</el-form-item>
		<el-form-item label="" class='address'>
			<span class="tip">X</span>
			<el-input-number v-model='partData.left' size="small" controls-position="right"></el-input-number>
			</br>
			<span class="tip">Y</span>
			<el-input-number v-model='partData.top' size="small" controls-position="right"></el-input-number>
		</el-form-item>
		<el-form-item label="" class='rotate'>
			<div class="block">
				<el-slider v-model='partData.rotate' :max='360' :min='0' show-input input-size='small' :show-input-controls='false'></el-slider>
			</div>
		</el-form-item>
		<el-form-item label="" class='opacity'>
			<div class="block">
				<el-slider v-model='partData.opacity' :max='100' :min='0' show-input input-size='small' :show-input-controls='false'> </el-slider>
			</div>
		</el-form-item>
	</el-collapse-item>
</template>

<script>
	export default {
		props: ['BoxStyleData', 'id'],
		data() {
			return {
				partData:{  
					rotate:0,
					width:0,
					height:0,
					top:0,
					left:0,
					opacity:0
				},
			}
		},
		mounted() {
			var _this = this
			this.$nextTick(function() {
				_this.initPartData()
			})
		},
		watch: {
			BoxStyleData:{
				handler(val, oldVal) {
					this.initPartData()
				},
				deep:true
			},
			partData: {
				handler(val, oldval) {
					this.BoxStyleData.width = val.width + 'px'
					this.BoxStyleData.height = val.height + 'px'
					this.$set(this.BoxStyleData, 'opacity', parseFloat(val.opacity / 100))
					this.$set(this.BoxStyleData, 'transform', "rotateZ(" + val.rotate + "deg)")
					this.BoxStyleData.left = val.left + 'px'
					this.BoxStyleData.top = val.top + 'px'
				},
				deep: true
			}
		},
		methods: {
			initPartData() {
				this.partData.width = parseFloat(this.BoxStyleData.width)
				this.partData.height = parseFloat(this.BoxStyleData.height)
				this.partData.left = parseFloat(this.BoxStyleData.left)
				this.partData.top = parseFloat(this.BoxStyleData.top)
				this.partData.opacity = parseFloat(this.BoxStyleData.opacity * 100)
				this.partData.rotate = parseFloat(this.BoxStyleData.transform.split('(')[1]) ? parseFloat(this.BoxStyleData.transform.split('(')[1]) : 0
			},
			//
			alignLeft() {
				var obj = $("li[data-id=" + this.id + "]")
				var angle = parseFloat(this.BoxStyleData.transform.split('(')[1]);
				var w = parseFloat(this.BoxStyleData.width);
				var h = parseFloat(this.BoxStyleData.height);
				if(0 <= angle && angle <= 90) {
					this.BoxStyleData.left = (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(90 < angle && angle <= 180) {
					this.BoxStyleData.left = -(w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(180 < angle && angle <= 270) {
					this.BoxStyleData.left = -(w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(270 < angle && angle <= 360) {
					this.BoxStyleData.left = (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				}
				this.$emit('cacheData');
				this.initPartData()
			},
			//
			alignRight() {
				var obj = $("li[data-id=" + this.id + "]");
				var mainBox = document.getElementsByClassName('main-box')[0];
				var angle = parseFloat(this.BoxStyleData.transform.split('(')[1]);
				var w = parseFloat(this.BoxStyleData.width);
				var h = parseFloat(this.BoxStyleData.height);
				var left = mainBox.offsetWidth - obj[0].getBoundingClientRect().width;
				if(0 <= angle && angle <= 90) {
					this.BoxStyleData.left = left + (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(90 < angle && angle <= 180) {
					this.BoxStyleData.left = left - (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(180 < angle && angle <= 270) {
					this.BoxStyleData.left = left - (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(270 < angle && angle <= 360) {
					this.BoxStyleData.left = left + (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				}
				this.$emit('cacheData');
				this.initPartData()
			},
			//
			alignLcenter() {
				var mainBox = document.getElementsByClassName('main-box')[0];
				this.BoxStyleData.left = mainBox.offsetWidth / 2 - parseFloat(this.BoxStyleData.width) / 2 + 'px'
				this.initPartData()
				this.$emit('cacheData');
			},
			alignHcenter() {
				var mainBox = document.getElementsByClassName('main-box')[0];
				this.BoxStyleData.top = mainBox.offsetHeight / 2 - parseFloat(this.BoxStyleData.height) / 2 + 'px'
				this.initPartData()
				this.$emit('cacheData');
			},
			alignTop() {
				var obj = $("li[data-id=" + this.id + "]")
				var angle = parseFloat(this.BoxStyleData.transform.split('(')[1]);
				var w = parseFloat(this.BoxStyleData.width);
				var h = parseFloat(this.BoxStyleData.height);
				if(0 <= angle && angle <= 90) {
					this.BoxStyleData.top = (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(90 < angle && angle <= 180) {
					this.BoxStyleData.top = -(h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(180 < angle && angle <= 270) {
					this.BoxStyleData.top = -(h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(270 < angle && angle <= 360) {
					this.BoxStyleData.top = (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				}
				this.initPartData()
				this.$emit('cacheData');
			},
			alignBottom() {
				var obj = $("li[data-id=" + this.id + "]");
				var angle = parseFloat(this.BoxStyleData.transform.split('(')[1]);
				var w = parseFloat(this.BoxStyleData.width);
				var h = parseFloat(this.BoxStyleData.height);
				var mainBox = document.getElementsByClassName('main-box')[0];
				var rh = mainBox.offsetHeight - obj[0].getBoundingClientRect().height;
				if(0 <= angle && angle <= 90) {
					this.BoxStyleData.top = rh + (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(90 < angle && angle <= 180) {
					this.BoxStyleData.top = rh - (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(180 < angle && angle <= 270) {
					this.BoxStyleData.top = rh - (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				} else if(270 < angle && angle <= 360) {
					this.BoxStyleData.top = rh + (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
				}
				this.initPartData()
				this.$emit('cacheData');
			},
		}
	}
</script>
<style src="@/assets/fonts/selectPanel/iconfont.css" scoped>


