<template>	
	<div style="width: 100%;height: 448px;text-align: center;background-color: #000000;">
		<img id="image" :src="src" crossOrigin='anonymous'>		
	</div>
</template>
<script type="text/javascript">
	import Cropper from 'cropperjs';
	export default {		
		props:['src','type'],
		data(){
			return {
				cropper:'',
			}
		},
		mounted() {
			var Vue = this
			var size = 320 / 486;
			if(this.type == 'background'){
				size = 320 / 486;
			}else if(this.type == 'cover' || this.type == 'head'){
				size = 300/300
			}else if(this.type == 'video'){
				size = 320/180
			}
			const image = document.getElementById('image');
			this.cropper = new Cropper(image, {
				aspectRatio: size,
				maxWidth: 640,
 				maxHeight: 972,
				zoomOnWheel: false,						
				viewMode: 2
			});
		},
		methods: {
			uploadBlob(){	
				var Vue = this;
				this.openLoading()
				this.cropper.getCroppedCanvas();
				this.cropper.getCroppedCanvas({
					width: 320,
					fillColor: '#fff',
					imageSmoothingEnabled: false,
					imageSmoothingQuality: 'high',
				});		
				this.cropper.getCroppedCanvas().toBlob((val) => {
					const formData = new FormData();
					formData.append('file', val);
					formData.append('username', Vue.$store.state.user.username);
					$.ajax(Vue.$api+'index.php/Home/Material/UploadBlob', {
						method: "POST",
						data: formData,		 
			            cache: false,
						async: true,
						contentType: false,
						processData: false,
						success(res) {
							Vue.closeLoading()
							Vue.$emit('showBack',res.src)
						},
						error() {
							console.log('Upload error');
						},
					});
				});
		
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>
<style type="text/css" scoped>
	img {
		max-width: 100%;
		max-height: 100%;
	}
</style>


