import Vue from 'vue'
import Router from 'vue-router'
import Edit from '@/pages/edit/Edit'
import Index from '@/pages/Index'
import MyScene_List from '@/pages/MyScene/List'
import MyScene_Detail from '@/pages/MyScene/Detail'
import MyScene_Share from '@/pages/MyScene/Share'
import MyScene_Statistics from '@/pages/MyScene/Statistics'
import UserCenter from '@/pages/user/UserCenter'
import UserCenter_info from '@/pages/user/UserCenter_info'
import templateIndex from '@/pages/template/Index'
import templateDetail from '@/pages/template/Detail'
import Home from '@/pages/Home'
Vue.use(Router)

export default new Router({
  routes: [
   {
      path: '',
      name: '',
      component: Index,
      children:[
      	{path: '',name: 'home',component: Home},
      	{path: 'list',name: 'MyScene_List',component: MyScene_List},
      	{path: 'detail',name: 'MyScene_Detail',component: MyScene_Detail,
      		children:[
      			{path: 'share/:id',name: 'MyScene_Share',component: MyScene_Share},
      			{path: 'statistics/:id',name: 'MyScene_Statistics',component: MyScene_Statistics},
      		]
      	},
      	
      	{path: 'usercenter',name: 'usercenter',component: UserCenter,
      		children:[
      			{path: 'info',name: 'usercenter_info',component: UserCenter_info},
      		]
      	},
      	{path: 'template/index',name: 'templateIndex',component: templateIndex},
      	{path: 'template/detail/:id/:name',name: 'templateDetail',component: templateDetail},
      ]
   },
   {
      path: '/edit/:id',
      name: 'edit',
      component: Edit
    }
  ]
})



