<template>
	<div class="layer-wraper">
		<div class="layer-box">
			<div class="layer-set">
				<el-tooltip content="" placement="bottom">
					<div @click='copyPart'><i class="fa fa-files-o" aria-hidden="true"></i></div>
				</el-tooltip>
				<el-tooltip content="" placement="bottom">
					<div @click='delPart'><i class="fa fa-trash-o" aria-hidden="true"></i></div>
				</el-tooltip>
			</div>			
			<ul class="layer-list">
				<el-scrollbar style="height:100%">
				<li @mousedown="changeDragging" :class='{"active":$store.state.focusPart.indexOf(item.id) != -1}' :key='item.id' v-dragging="{ item: item, list: data.children, group: 'layer'}" v-for="(item,index) in  data.children" @click='setFoucsPart(item.id)'>
					<i class="icon el-icon-view view" :class="{'default':item.BoxStyleData.display == 'none'}" @click.stop='showPart(item)'></i>
					<input class="layer-title" type="text" maxlength='11' readonly @dblclick.stop="actTitle" @blur="blurTitle" v-model="item.title"/>
				</li>
				</el-scrollbar>
			</ul>			
		</div>
	</div>
</template>

<script>
	export default {
		props: ['data'],
		data() {
			return {
				pageData: [],
				part:[
					{type:'text',title:''},
					{type:'image',title:''},
					{type:'svg',title:''},
					{type:'video',title:''},
					{type:'link',title:''},
					{type:'phone',title:''},
					{type:'input',title:''},
					{type:'submit',title:''},
					{type:'message',title:''},
					{type:'radiobox',title:''},
					{type:'checkbox',title:''},
					{type:'select',title:''},
				]
			}
		},
		computed:{
			 sortStudents:function(){
                return this.sortByKey(this.data.children,'BoxStyleData');
            }
		},
		methods:{
			sortByKey(array,key){  //()
	            return array.sort(function(a,b){           	
		            var x=a[key]['zIndex'];
		            var y=b[key]['zIndex'];
		            return ((x<y)?-1:((x>y)?1:0));
		       	 })
	        },
			//
			actTitle(e){	
				$(e.currentTarget).addClass('active').attr('readonly', false)
			},
			blurTitle(e){
				$(e.currentTarget).removeClass('active').attr('readonly', true)
			},
			setFoucsPart(id){
				this.$store.state.focusPart = [];
				this.$store.state.focusPart.push(id)
			},
			changeDragging(){
				this.$set(this.$store.state.dragging,'group','layer')
				this.$set(this.$store.state.dragging,'list',JSON.parse(JSON.stringify(this.data.children)))
			},
			//
			showPart(item){
				if(item.BoxStyleData.display == 'none'){
					this.$set(item.BoxStyleData,'display','block')
				}else {
					this.$set(item.BoxStyleData,'display','none')
				}
			},
			//
			delPart(){
				var Vue = this;
				for (var i = this.data.children.length-1;i>=0;i--) {
					if(Vue.$store.state.focusPart.indexOf(this.data.children[i].id) != -1){
						Vue.data.children.splice(i,1)
					}
				}
			},
			//
			copyPart(){
				var Vue = this;
				var partData = '';				
				this.data.children.forEach(function(val,i){
					if(Vue.$store.state.focusPart.indexOf(val.id) != -1){
						partData = JSON.parse(JSON.stringify(val))
						partData.title = '-'+partData.title
						partData.id = Vue.$store.state.focusPage + '_' + partData.type +Vue.utill.GenNonDuplicateID(5)
						Vue.data.children.push(partData)
					}
				})
			}			
		}
	}
</script>

<style scoped lang="scss">
	.layer-wraper {
		height: 100%;
	}
	.layer-box {
		position: relative;
		height: 100%;
		.layer-set {
			text-align: center;
			padding: 5px 0;
			border-bottom: 1px solid #e6ebed;
			&>div {
				display: inline-block;
				font-size: 12px;
				width: 57px;
				text-align: center;
				height: 32px;
				line-height: 32px;
				&:hover {
					color: #fff;
					background-color: #1593ff;
					cursor: pointer;
				}
				.fa {
					margin-right: 5px;
				}
			}
		}
		.layer-list {
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			top: 43px;
			li {
				height: 36px;
				line-height: 36px;
				background-color: #fff;
				line-height: 36px;
				border-bottom: 1px solid #e6ebed;
				position: relative;				
				&.active,&:hover  {
					.layer-title {
						color: #409EFF;
					}
					color: #409EFF;
					.view.default:after{
						background-color: #409EFF;
					}
				}
				.icon {
					display: block;
					height: 100%;
					width: 28px;
					line-height: 36px;
					text-align: center;
					float: left;
					font-size: 14px;
					cursor: pointer;
				}
				.layer-title {
					position: absolute;
					top: 4px;
					right: 25px;
					bottom: 4px;
					left: 80px;
					font-size: 12px;
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
					display: block;
					border-radius: 4px;
					outline: none;
					border: none;
					padding: 0 8px;
					cursor: pointer;
					&.active {					
						color: #409EFF;
						border:1px solid #409EFF;
						cursor: initial;					
					}
				}
				.view.default{
					position: relative;
					&:after {
						content: '';
						width: 60%;
						height: 1px;
						background: #333;
						position: absolute;
						left: 20%;
						right: 0;
						top: 50%;
						transform: rotateZ(45deg);
					}
					
				}
				
			}
		}
	}
</style>


