<template>
	<div class="material" v-show="Music.isShow">
		<div class="material-box">
			<div class="material-left">
				<h3 class="head">
					&ensp;<span class="el-icon-info"></span>
				</h3>
				<ul class="material-sort">
					<li @click="getMusic($event,'all')" :class="{active:Music.type=='all'}"></li>
					<li @click="getMusic($event,'my')" :class="{active:Music.type=='my'}"></li>
					<li @click="getMusic($event,'collect')" :class="{active:Music.type=='collect'}"></li>
				</ul>
				<ul class="fun">
					<li>
						<el-upload class="upload-demo" ref='upload-music' :action="$api+'/index.php/Home/Material/UploadMusic?username='+$store.state.user.username" :show-file-list="false" :on-success="handleSuccessMusic" :on-error="handleErrorMusic" :before-upload="beforeUploadMusic" multiple>
							<el-button type="primary" icon='el-icon-upload' @click='clearMusicFiles' round></el-button>
						</el-upload>
					</li>
				</ul>
			</div>
			<div class="material-right">
				<i class="el-icon-close colse" @click="closeMusic"></i>
				<ul class="nav" v-if='Music.type=="all"'>
					<li class="active"></li>
				</ul>
				<ul class="nav" v-if='Music.type=="my"'>
					<li :class="{'active':Music.myNav == 1}" @click='changeMyMusicNav(1)'></li>
					<li v-if='$store.state.user.grade == 2' :class="{'active':Music.myNav == 2}" @click='changeMyMusicNav(2)'></li>
				</ul>
				<ul class="nav" v-if='Music.type=="collect"'>
					<li class="active"></li>
				</ul>
				<div class="conent-box" v-if='Music.type=="all"'>
					<el-scrollbar style='height: 408px;'>
						<ul class="music-list">
							<img class="empty-icon" src="@/assets/images/empty.png" v-show='Music.all.count<=0' />
							<li v-for="(item,index) in Music.all.data" @click.stop='selectMusic(item)'>
								<el-tooltip  class="item" effect="dark"  content="" placement="right" :open-delay='0'>							
									<i class="fa fa-heart-o collect-icon" :class="{'active':item.collect == 1}" aria-hidden="true" @click='axiosCollectMusic(item)'></i>
								</el-tooltip>
								<p class="name all-name">{{item.name}}</p>
							</li>
						</ul>
						<div class="line" v-if='Music.all.count>0'></div>
					</el-scrollbar>
					<el-pagination v-if='Music.all.count>0' background layout="prev, pager, next,total,jumper" :current-page.sync="Music.all.page" :page-size='Music.pageSize' :total="Music.all.count" @current-change="changeMusicPage"></el-pagination>
				</div>
				<div class="conent-box" v-if='Music.type=="my"'>
					<el-scrollbar style='height: 408px;'>
						<ul class="music-list">
							<img class="empty-icon" src="@/assets/images/empty.png" v-show='Music.my.count<=0' />
							<li v-for="(item,index) in Music.my.data" @click.stop='selectMusic(item)'>
								<p class="name">{{item.name}}</p>
								<div class="opera" v-if='Music.type=="my"'>
									<el-tooltip v-if='$store.state.user.grade == 2 && Music.myNav ==1' class="item" effect="dark" offset='15' transition='none' :content="item.grade==1?'':''" placement="left" :open-delay='0'>
										<i class="common" :class='{"el-icon-star-off":item.grade==1,"el-icon-star-on":item.grade==2}' @click.stop='setMyMusic(item)'></i>
									</el-tooltip>
									<el-tooltip v-if='Music.myNav ==1' class="item" effect="dark" offset='15' transition='none' content="" placement="left" :open-delay='0'>
										<i class="el-icon-delete del" @click.stop='delMusic(item)'></i>
									</el-tooltip>
									<el-tooltip v-if='$store.state.user.grade == 2 && Music.myNav == 2' class="item" effect="dark" offset='15' transition='none' content="" placement="left" :open-delay='0'>
										<i class="el-icon-delete del" @click.stop='delComMusic(item,index)'></i>
									</el-tooltip>
								</div>
							</li>
						</ul>
						<div class="line" v-if='Music.my.count>0'></div>
					</el-scrollbar>
					<el-pagination v-if='Music.my.count>0' background layout="prev, pager, next,total,jumper" :current-page.sync="Music.my.page" :page-size='Music.pageSize' :total="Music.my.count" @current-change="changeMusicPage"></el-pagination>
				</div>
				<div class="conent-box" v-if='Music.type=="collect"'>
					<el-scrollbar style='height: 408px;'>
						<ul class="music-list">
							<img class="empty-icon" src="@/assets/images/empty.png" v-show='Music.collect.count<=0' />
							<li v-for="(item,index) in Music.collect.data" @click.stop='selectMusic(item)'>
								<p class="name">{{item.name}}</p>
								<div class="opera">
									<el-tooltip  class="item" effect="dark"  transition='none' content="" placement="left" :open-delay='0'>
										<i class="el-icon-delete del" @click.stop='delCollectMusic(item.id,index)'></i>
									</el-tooltip>
								</div>
							</li>
						</ul>
						<div class="line" v-if='Music.collect.count>0'></div>
					</el-scrollbar>
					<el-pagination v-if='Music.collect.count>0' background layout="prev, pager, next,total,jumper" :current-page.sync="Music.my.page" :page-size='Music.pageSize' :total="Music.collect.count" @current-change="changeMusicPage"></el-pagination>
				</div>
				<div class="content-bottom">
					<div class="music-content">
						<audio id="audioTag" :src="!!actMusic.url?$api+actMusic.url:''"></audio>
						<div class="music-opera">
							<i class="fa fa-play-circle" aria-hidden="true" @click="operaAudio" v-if='!isMusicPlay'></i>
							<i class="fa fa-pause-circle" aria-hidden="true" @click="operaAudio" v-if='isMusicPlay'></i>
						</div>
						<div class="music-detail">
							<div class="music-info">
								<div class="music-name">{{!!actMusic?actMusic.name:''}}</div>
								<div class="music-time">
									{{timeConversion(currentTime)}}/{{timeConversion(videoAllTime)}}
								</div>
							</div>
							<div class="music-progress">
								<el-progress :percentage="(currentTime/videoAllTime*100)?(currentTime/videoAllTime*100):0" :show-text="false"></el-progress>
							</div>
						</div>
					</div>
					<div class="music-btn" @click='useMusic'></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				isMusicPlay: false, //
				actMusic: { //
					name: "",
					url: ''
				},
				videoAllTime: 0,
				currentTime: 0,
				Music: { //
					pageSize: 40, //
					my:{
						data:[],//
						page:1,//
						count:0,
					},
					all:{
						data:[],//
						page:1,//
						count:0,
					},
					collect:{
						data:[],//
						page:1,//
						count:0,
					},
					type: 'all',
					myNav: 1, //12
					successUpload: [], //
					isShow: false, //
				},
			}
		},
		methods: {
			closeMusic() {
				this.Music.isShow = false
				this.clearMusic()
			},
			//
			initMusic() {
				var Vue = this
				this.Music.isShow = true
				if(this.Music.type == 'all') {
					this.axiosGetAllMusic()
				} else if(this.Music.type == 'my'){
					if(this.Music.myNav == 1) {
						this.axiosGetMyMusic(1)
					} else if(this.Music.myNav == 2) {
						this.axiosGetMyMusic(2)
					}
				}else if(this.Music.type == 'collect'){
					this.axiosGetCollectMusic()
				}
			},
			//
			selectMusic(item) {
				var Vue = this
				this.clearMusic()
				this.actMusic = JSON.parse(JSON.stringify(item))
				var audio = document.getElementById('audioTag')
				if(audio.paused) {} else {
					audio.pause()
				}
				audio.load()
				audio.oncanplay = function() {
					var videoAllTime = parseInt(audio.duration)
					Vue.videoAllTime = videoAllTime
				}
			},
			handleSuccessMusic(res, file, fileList) {
				this.Music.successUpload.push(file)
				if(this.Music.successUpload.length == fileList.length) {
					this.Music.type = 'my';
					this.Music.myNav = 1;
					this.Music.my.page = 1;
					this.axiosGetMyMusic(1)
				}
			},
			handleErrorMusic() {
				this.$message.error('');
				this.closeLoading()
			},
			//
			beforeUploadMusic(file) {
				const isMP3 = file.type === 'audio/mp3';
				const isMPEG = file.type === 'audio/mpeg';
				if(!isMP3 && !isMPEG) {
					this.$message.error('mp3');
					return false;
				}
				if(file.size / 1024 / 1024 > 8) {
					this.$message.error('8MB');
					return false;
				}
				this.openLoading()
			},
			//
			changeMusicPage(val) {
				
				if(this.Music.type == 'all') {
					this.Music.all.page = val
					this.axiosGetAllMusic()
				} else if(this.Music.type == 'my') {
					this.Music.my.page = val
					if(this.Music.myNav == 1) {
						this.axiosGetMyMusic(1)
					} else if(this.Music.myNav == 2) {
						this.axiosGetMyMusic(2)
					}
				}else if(this.Music.type == 'collect'){
					this.Music.collect.page = val
					this.axiosGetCollectMusic()
				}
			},
			clearMusicFiles() {
				this.$refs['upload-music'].clearFiles();
				this.Music.successUpload = []
			},
			//
			operaAudio() {
				if(!this.actMusic.url) {
					this.$message.error('')
					return false
				}
				var Vue = this
				var audio = document.getElementById('audioTag')
				if(this.isMusicPlay) {
					this.isMusicPlay = false
					audio.pause()
				} else {
					this.isMusicPlay = true
					audio.play()
				}
				audio.ontimeupdate = function() {
					var currentTime = parseInt(audio.currentTime)
					Vue.currentTime = currentTime
				}
				audio.onended = function() {
					Vue.clearMusic()
				}
			},
			//
			useMusic() {
				if(!this.actMusic.url) {
					this.$message.error('')
					return false
				}
				this.$store.state.Music = this.actMusic
				this.closeMusic()
			},
			//
			clearMusic() {
				this.isMusicPlay = false
				this.currentTime = 0
				this.videoAllTime = 0
				var audio = document.getElementById('audioTag')
				if(audio.paused) {} else {
					audio.pause()
				}
				audio.currentTime = 0

			},
			timeConversion(tim) {
				tim = tim.toFixed(3)
				var min = parseInt(tim / 60); //  
				var second = Math.floor(tim % 60); //	
				return(Array(2).join(0) + min).slice(-2) + ':' + (Array(2).join(0) + second).slice(-2);
			},

			//
			delMusic(item) {
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					if(this.actMusic.url == item.url) {
						this.clearMusic()
						this.actMusic = ''
					}
					this.ajaxDelMusic(item.id, item.url)
				}).catch(() => {});
			},
			//
			delCollectMusic(id,index){
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.openLoading()
					this.http.axiosDelCollectMusic({
						id:id,
						username:this.$store.state.user.username
					}).then((response) => {
						this.closeLoading()
						if(response.data.status == 1){
							if((this.Music.collect.count-1)%this.Music.pageSize == 0){
								if(this.Music.my.page<=1){
									this.Music.my.page = 1
								}else {
									this.Music.my.page --
								}
								
							}	
							this.axiosGetCollectMusic()
						}
					})
				}).catch(() => {});
				
			},
			//
			axiosGetAllMusic() {
				var Vue = this
				this.openLoading()
				this.http.axiosGetAllMusic({
					page: Vue.Music.all.page,
					pageSize: Vue.Music.pageSize,
				}).then((response) => {
					this.closeLoading()
					Vue.Music.all.data = response.data.data
					Vue.Music.all.count = parseInt(response.data.count)		
					if(response.data.status == 1) {
						Vue.clearMusic()
					}
				})
			},
			//
			axiosGetMyMusic(grade) {
				var Vue = this
				this.openLoading()
				this.http.axiosGetMyMusic({
					page: Vue.Music.my.page,
					pageSize: Vue.Music.pageSize,
					username: this.$store.state.user.username,
					grade: grade
				}).then((response) => {
					this.closeLoading()	
					Vue.Music.my.data = response.data.data
					Vue.Music.my.count = parseInt(response.data.count)	
					Vue.clearMusic()

				})
			},

			//
			changeMyMusicNav(grade) {
				if(this.Music.myNav == grade) {
					return false;
				}
				this.Music.my.page = 1
				this.Music.myNav = grade
				this.axiosGetMyMusic(grade)
			},
			//
			ajaxDelMusic(id, url) {
				var Vue = this
				this.openLoading()
				this.http.axiosDelMusic({
					id: id,
					url: url
				}).then((response) => {
					if(response.data.status == 1) {
						Vue.$message.success('')
					} else {
						Vue.$message.error('')
					}
					if(this.Music.type == 'all') {
						this.axiosGetAllMusic()
					} else if(this.Music.type == 'my') {
						if((this.Music.my.count-1)%this.Music.pageSize == 0){
							if(this.Music.my.page<=1){
								this.Music.my.page = 1
							}else {
								this.Music.my.page --
							}
							
						}						
						this.axiosGetMyMusic(1)
					}

				})
			},
			//
			axiosCollectMusic(item){
				this.openLoading()
				this.http.axiosCollectMusic({
					id: item.id,
					username: this.$store.state.user.username
				}).then((res)=>{
					this.closeLoading()
					if(res.data.status == 1){
						this.$set(item,'collect',1)
						this.$message.success('')
					}else {
						this.$message.error(res.data.msg)
					}
				})
			},
			//
			axiosGetCollectMusic(){
				this.openLoading()
				this.http.axiosGetCollectMusic({
					page:this.Music.collect.page,
					pageSize:this.Music.pageSize,
					username: this.$store.state.user.username
				}).then((res)=>{
					this.closeLoading()								
					this.clearMusic()
					this.Music.collect.data = res.data.data
					this.Music.collect.count = Number(res.data.count)
					
				})
			},
			//
			axiosUpdatetMyMusic(data) {
				var Vue = this
				var grade = 1;
				if(data.grade == 1) {
					grade = 2;
				} else {
					grade = 1;
				}
				this.openLoading()
				return this.http.axiosUpdatetMyMusic({
					id: data.id,
					grade: grade,
					username: this.$store.state.user.username
				})
			},
			//
			setMyMusic(data) {
				this.axiosUpdatetMyMusic(data).then((res) => {
					this.closeLoading()
					if(res.data.status == 1) {
						if(data.grade == 1) {
							data.grade = 2
						} else {
							data.grade = 1
						}
					}
				})
			},
			//
			delComMusic(data, index) {
				this.$confirm(',""', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.axiosUpdatetMyMusic(data).then((res) => {
						this.closeLoading()
						if(res.data.status == 1) {
							if((this.Music.my.count-1)%this.Music.pageSize == 0){
								if(this.Music.my.page<=1){
									this.Music.my.page = 1
								}else {
									this.Music.my.page --
								}
								
							}						
							this.axiosGetMyMusic(2)
													
						}

					})
				}).catch(() => {

				});
			},
			getMusic(e, type) {
				var obj = e.currentTarget
				if($(obj).hasClass('active')) {
					return false
				} else {
					this.Music.type = type
					if(type == 'my') {
						
						if(this.Music.myNav == 1) {
							this.axiosGetMyMusic(1)
						} else if(this.Music.myNav == 2) {
							this.axiosGetMyMusic(2)
						}

					} else if(type == 'all') {
						this.axiosGetAllMusic()
					}
					else if(type == 'collect'){
						this.axiosGetCollectMusic()
					}
				}
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>

<style scoped lang="scss">
	.collect-icon {
		position: absolute;
		left: 10px;
		line-height: 40px;
		font-size: 14px;
		&:hover,&.active {
			color: red;
		}
	}
	.all-name {
		margin-left: 30px;
	}
</style>


