<template>
	<div class="IndexPage">
		<el-container class='' style="min-height: 100vh;">
			<el-header style='position: fixed;top: 0;left: 0;width: 100%;min-width: 1180px;background: #fff;z-index: 11;border-bottom: 1px solid #e6ebed;'>
				<div class="header-box">
					<div class="logo">
						<img src="../assets/images/logo.png" />
					</div>
					<div class="right">
						<div class="link">
							<li :class='{"active":$route.name=="home"}'>
								<router-link to='/'></router-link>
							</li>
							<li :class='{"active":$route.name=="templateIndex" || $route.name=="templateDetail"}'>
								<router-link to='/template/index'></router-link>
							</li>
							<li :class='{"active":$route.name=="MyScene_List"||$route.matched[1].name == "MyScene_Detail"}' v-if='$store.state.user.username'>
								<router-link to='/list'></router-link>
							</li>
						</div>
						<div class="login-btn" v-if='!$store.state.user.username'>
							<el-button type="primary" @click='$store.state.isShowLogin = true' plain></el-button>
							<el-button type="primary" @click='$store.state.isShowRegister = true'></el-button>
						</div>
						<div class="user" v-else>							
							<el-dropdown :hide-on-click="true" @command="userCommand">
								<span class="el-dropdown-link">
   									<div class="user-head">
										<img :src="$api+$store.state.user.headSrc"/>
										<i class="el-icon-arrow-down el-icon--right"></i>
									</div>
 								</span>
								<el-dropdown-menu slot="dropdown">
									<p style="text-align: center;line-height: 30px;font-size: 15px;color: #999;">{{$store.state.user.username}}</p>
									<el-dropdown-item divided command="1"></el-dropdown-item>		
									<el-dropdown-item command="0"divided ></el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>
						</div>
					</div>
				</div>
			</el-header>
			<el-main class='flex-column' style='background-color: #F0F3F4;padding: 0;margin-top: 60px;'>
				<router-view></router-view>
			</el-main>
			<el-footer class="footer index-footer" style="height: initial;">
				<div class="f-copyright">
					Copyright &#169;2019  
				</div>
			</el-footer>
		</el-container>
		<el-dialog ref='login' title="":close-on-click-modal='false' :visible.sync="$store.state.isShowLogin" width='530px'>
			<login v-if='$store.state.isShowLogin' ref="childLogin" @closeLogin='$store.state.isShowLogin = false'></login>
		</el-dialog>
		<el-dialog ref='reg' title="" :close-on-click-modal='false' :visible.sync="$store.state.isShowRegister" width='550px'>
			<reg v-if='$store.state.isShowRegister' ref="childReg" @closeReg='$store.state.isShowRegister = false'></reg>
		</el-dialog>
	</div>
</template>

<script>
	import Reg from '@/components/Register'
	import Login from '@/components/Login'
	export default {
		components: {
			Reg,
			Login
		},	

		methods: {
			//
			userCommand(command){
				if(command == 1){
					this.$router.push({
						name: 'usercenter_info'
					});
				}else if(command == 0){
					this.exit()
				}
			},
			initLogin() {
				this.username = this.utill.getCookie('username')
				if(this.username) {
					this.axiosFindUser()
				} else {
					this.$router.push({
						name: 'home'
					});
					this.$store.state.isShowLogin = true;
				}
			},
			axiosFindUser() {
				this.openLoading()
				this.http.axiosFindUser({
					username: this.username,
				}).then((response) => {
					this.closeLoading()
					if(response.data.status == 1) {
						this.$store.state.isShowLogin = false;
						this.$store.state.user.username = response.data.data.username
						this.$store.state.user.grade = response.data.data.grade
						this.$store.state.user.name = response.data.data.name
						this.$store.state.user.phone = response.data.data.phone
						this.$store.state.user.department = response.data.data.department
						this.$store.state.user.create_time = response.data.data.create_time
						if( response.data.data.headSrc){
							this.$store.state.user.headSrc = response.data.data.headSrc
						}
					} else {
						this.exit()
					}
				})
			},
			exit() {
				this.utill.delCookie('username')
				this.$store.state.user.username = undefined
				this.initLogin()
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>

<style src="../assets/style/index.scss" lang='scss'></style>


