								<el-color-picker v-model="initEditData.style.color" :predefine="predefineColors"></el-color-picker>
							</el-form-item>
						</template>
						<template v-if='checkType==""'>
							<div class="el-form-item">
								<el-button class='btn-block' icon="el-icon-plus" size='medium' @click="checkImage('phone')"></el-button>
							</div>
						</template>
					</div>
					<el-collapse accordion v-model='$store.state.collapse'>
						<el-collapse-item title="" name='1'>
							<el-form-item label="">
								<el-color-picker v-model="initEditData.style.borderColor" :predefine="predefineColors" show-alpha></el-color-picker>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderWidth' :max="20"></el-slider>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderRadius' :max="75"></el-slider>
							</el-form-item>
						</el-collapse-item>
						<element-edit-addr :id='id' :BoxStyleData='BoxStyleData'></element-edit-addr>
						
					</el-collapse>
				</el-form>
				<!--   -->
				<el-form ref="initEditData.style" v-if="type=='video'" :model="initEditData.style" label-width="84px" v-show='$store.state.editSubnavNum==0' class='edit-style'>
					<div class="edit-attribute-box">
						<!--<el-form-item label="">
						<el-input v-model="initEditData.name" maxlength="12"></el-input>
					</el-form-item>-->
					</div>
					<el-collapse accordion>
						<el-collapse-item title="" name='1'>
							<el-form-item label="">
								<el-color-picker v-model="initEditData.style.borderColor" :predefine="predefineColors" show-alpha></el-color-picker>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderWidth' :max="20"></el-slider>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderRadius' :max="75"></el-slider>
							</el-form-item>
						</el-collapse-item>
						<element-edit-addr :id='id' :BoxStyleData='BoxStyleData'></element-edit-addr>
						
					</el-collapse>
				</el-form>
				<!--   -->
				<el-form ref="initEditData.style" v-if="type=='code'" :model="initEditData.style" label-width="84px" v-show='$store.state.editSubnavNum==0' class='edit-style'>
					<div class="edit-attribute-box">
						<el-form-item label="">
							<el-input v-model.trim="initEditData.form.title"></el-input>
						</el-form-item>
					</div>
					<el-collapse accordion v-model='$store.state.collapse'>
						<el-collapse-item title="" name='1'>
							<el-form-item label="">
								<el-color-picker v-model="initEditData.style.borderColor" :predefine="predefineColors" show-alpha></el-color-picker>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderWidth' :max="20"></el-slider>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderRadius' :max="75"></el-slider>
							</el-form-item>
						</el-collapse-item>
						<element-edit-addr :id='id' :BoxStyleData='BoxStyleData'></element-edit-addr>
					</el-collapse>
				</el-form>
				<!--   -->
				<el-form ref="initEditData.style" v-if="type=='screen'" :model="initEditData.style" label-width="84px" v-show='$store.state.editSubnavNum==0' class='edit-style'>
					<div class="edit-attribute-box">
						<el-form-item label="">
							<el-input-number v-model="initEditData.style.fontSize" :min="12" :max="120"></el-input-number>
						</el-form-item>
						<el-form-item label="">
							<el-radio-group v-model='checkType' @change='changeType'>
								<el-radio-button label=""></el-radio-button>
								<el-radio-button label=""></el-radio-button>
							</el-radio-group>
						</el-form-item>
						<template v-if='checkType==""'>
							<el-form-item label="">
								<el-input v-model="initEditData.form.title" maxlength='8'></el-input>
							</el-form-item>
							<el-form-item label="">
								<el-color-picker v-model="initEditData.style.backgroundColor" :predefine="predefineColors" show-alpha></el-color-picker>
							</el-form-item>
							<el-form-item label="">
								<el-color-picker v-model="initEditData.style.color" :predefine="predefineColors"></el-color-picker>
							</el-form-item>
						</template>
						<template v-if='checkType==""'>
							<div class="el-form-item">
								<el-button class='btn-block' icon="el-icon-plus" size='medium' @click="checkImage('screen')"></el-button>
							</div>
						</template>
					</div>
					<el-collapse accordion v-model='$store.state.collapse'>
						<el-collapse-item title="" name='1'>
							<el-form-item label="">
								<el-color-picker v-model="initEditData.style.borderColor" :predefine="predefineColors" show-alpha></el-color-picker>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderWidth' :max="20"></el-slider>
							</el-form-item>
							<el-form-item label="">
								<el-slider v-model='initEditData.style.borderRadius' :max="75"></el-slider>
							</el-form-item>
						</el-collapse-item>
						<element-edit-addr :id='id' :BoxStyleData='BoxStyleData'></element-edit-addr>
						
					</el-collapse>
				</el-form>
				<!--   -->
				<div class="edit-animation" v-if='$store.state.editSubnavNum==1'>
					<div class="edit-animation-add">
						<el-button type="primary" icon="el-icon-plus" @click='isShowAnimEdit=true'></el-button>
						<el-button icon="el-icon-caret-right" @click="animShow"></el-button>
					</div>
					<ul>
						<li v-for="(item,index) in animData" style="margin-bottom: 12px;">
							<div class="edit-animation-seltop">
								<span class="edit-animation-seltop-tit">{{index+1}}</span>
								<el-button round @click="animSplice(index)">{{item.title}}<i class="el-icon-caret-right"></i></el-button>
								<a href="javascript:;" @click="animPowerAdd(item)"><span class="el-icon-caret-right"></span></a>
								<a href="javascript:;" @click="animDataDel(item,index)"><span class="el-icon-delete"></span></a>
							</div>
							<div class="edit-animation-set">
								<el-row :gutter="10">
									<el-col :span="12">
										<el-row :gutter="4">
											<el-col :span="8">
												<span class="name"></span>
											</el-col>
											<el-col :span="16">
												<el-input-number v-model="item.style.animationDuration" controls-position="right" :min="0" :step="0.1"></el-input-number>
											</el-col>
										</el-row>
									</el-col>
									<el-col :span="12">
										<el-row :gutter="4">
											<el-col :span="8">
												<span class="name"></span>
											</el-col>
											<el-col :span="16">
												<el-input-number v-model="item.style.animationDelay" controls-position="right" :min="0" :step="0.1"></el-input-number>
											</el-col>
										</el-row>
									</el-col>
								</el-row>
								<el-row :gutter="10">
									<el-col :span="12">
										<el-row :gutter="4">
											<el-col :span="8">
												<span class="name"></span>
											</el-col>
											<el-col :span="16">
												<el-input-number v-model="item.style.animationIterationCount" controls-position="right" :disabled='item.style.isInfinite=="true"||item.style.isInfinite==true' :min="1"></el-input-number>
											</el-col>

										</el-row>
									</el-col>
									<el-col :span="12">
										<el-checkbox label="" v-model='item.style.isInfinite' :checked='(item.style.isInfinite=="true"||item.style.isInfinite==true)?true:false' border></el-checkbox>
									</el-col>
								</el-row>
							</div>
						</li>
					</ul>
				</div>
			</el-scrollbar>
		</div>
		<transition name="slide-fade">
			<div class="animate-box" v-if='isShowAnimEdit'>
				<ul class="animate-subnav">
					<li :class="{active:animateSubnavNum==0}" @click="animateSubnavNum=0"></li>
					<li :class="{active:animateSubnavNum==1}" @click="animateSubnavNum=1"></li>
					<li :class="{active:animateSubnavNum==2}" @click="animateSubnavNum=2"></li>
				</ul>
				<ul class="animate-main" v-if='animateSubnavNum==0'>
					<li v-for="(item,index) in arrAinmIn" @mouseenter="animPowerAdd(item)" @mouseleave="animPowerDel(item)" @click="animDataSet(item,$event)">
						<div class="anim-icon" :style="{'background-position':item.isHover?item.actposition:item.position}"></div>
						<p class="anim-title">{{item.title}}</p>
					</li>
				</ul>
				<ul class="animate-main" v-if='animateSubnavNum==1'>
					<li v-for="(item,index) in arrAinmIm" @mouseenter="animPowerAdd(item)" @mouseleave="animPowerDel(item)" @click="animDataSet(item,$event)">
						<div class="anim-icon" :style="{'background-position':item.isHover?item.actposition:item.position}"></div>
						<p class="anim-title">{{item.title}}</p>
					</li>
				</ul>
				<ul class="animate-main" v-if='animateSubnavNum==2'>
					<li v-for="(item,index) in arrAinmOut" @mouseenter="animPowerAdd(item)" @mouseleave="animPowerDel(item)" @click="animDataSet(item,$event)">
						<div class="anim-icon" :style="{'background-position':item.isHover?item.actposition:item.position}"></div>
						<p class="anim-title">{{item.title}}</p>
					</li>
				</ul>
			</div>
		</transition>
	</div>
</template>

<script>
	import ElementEdit from '@/assets/script/ElementEdit.js'
	export default {
		...ElementEdit

	}
</script>
<style src="@/assets/fonts/selectPanel/iconfont.css" scoped>


