 <template>
	<li :data-id='id' class="item-comp"  @mousedown.stop="partDown" @mouseup='partFocus'  @contextmenu.prevent="operateBox" :style="BoxStyleData">
		<div class="line-box" v-show='$store.state.focusPart.indexOf(id) != -1'>	
			<div class="line line-top">				
				<div class="rotate-circle" @mousedown="rotateBox" v-if ='type != "video" '>
					<span v-if='isShowAngle'>{{angle}}</span>
				</div>
				<div class="rotate-line" v-if ='type != "video"'></div>
				<div class="circle circle-col" data-dir='n' @mousedown="changeBox" @mouseenter="changeBoxCursor" v-show ='type != "video" && type != "code"'></div><br />
			</div>
			<div class="line line-left">
				<div class="circle circle-row" data-dir='w' @mousedown="changeBox" @mouseenter="changeBoxCursor" v-show ='type != "video"&& type != "code"'></div>
			</div>
			<div class="line line-right">
				<div class="circle circle-row" data-dir='e' @mousedown="changeBox" @mouseenter="changeBoxCursor" v-show ='type != "video"&& type != "code"'></div>
			</div>
			<div class="line line-bottom">
				<div class="circle circle-col" data-dir='s' @mousedown="changeBox" @mouseenter="changeBoxCursor" v-show ='type != "video"&& type != "code"'></div>
			</div>
			
			<div class="circle circle-ne" data-dir='ne' @mousedown="changeBox" @mouseenter="changeBoxCursor"></div>
			<div class="circle circle-se" data-dir='se' @mousedown="changeBox" @mouseenter="changeBoxCursor"></div>
			<div class="circle circle-nw" data-dir='nw' @mousedown="changeBox" @mouseenter="changeBoxCursor"></div>
			<div class="circle circle-sw" data-dir='sw' @mousedown="changeBox" @mouseenter="changeBoxCursor"></div>
		</div>	
		<!--  -->
		<div class="element-box animated" @mousedown.ctrl.stop="focusArrBox"  :style='[textStyle,animPower.style]' :class="animPower.className" :textStyle='comTextStyle'>
			<!--   -->
			<template  v-if='type == "text"' >
				<div class="edit-text-box" @dblclick="editText">
					<textarea class='edit-text pointer-none' placeholder="" v-model="textValue"   @blur="blurText"></textarea>
				</div>
			</template>
			<!--  -->
			<template v-if='type == "image"'>
				<img :src="data?$api+data:$api+imgSrc"  style="width: 100%;height: 100%;display: block;" />
			</template>
			<!--  -->
			<template v-if='type == "svg"'>
				<svg v-if='type == "svg"' class="icon" :viewBox = 'data.view_box?data.view_box[0]:"0 0 1024 1024"' width="100%" height="100%"  version="1.1" xmlns="http://www.w3.org/2000/svg">
					<path :d='path.d[0]' :fill="path.fill?path.fill[0]:''" v-for = 'path in (data?data.path:svg.path)'></path>
				</svg>
			</template>
			<!--  -->
			<template v-if='type == "input"'>
				<input class='edit-input pointer-none'  :placeholder="elementForm.title"  ></input>
			</template>
			<!--  -->
			<template  v-if='type == "message"'>
				<textarea class='edit-message pointer-none' :placeholder="elementForm.title"  type="textarea" ></textarea>
			</template>
			<!--  -->
			<template  v-if='type == "submit"'>
				<button class="edit-submit"    readonly style="cursor: initial;">{{elementForm.title}}</button>
			</template>
			<!--  -->
			<template  v-if='type == "checkbox"'>
				<div class="edit-check">		
					<div class="title" :style="{'background-color':elementForm.style?elementForm.style.backgroundColor:'','color':elementForm.style?elementForm.style.color:''}">{{elementForm.title}}</div>
					<el-checkbox-group class="list" v-model='checkbox'>
						<li :style="{'border-color':elementForm.style?elementForm.style.backgroundColor:''}" v-for ='(item,index) in elementForm.data' :key='index'>
					 		<el-checkbox :label="item" class="pointer-none">{{item}}</el-checkbox>				 		
						</li>
					</el-checkbox-group>
				</div>
			</template>
			<!--  -->
			<template  v-if='type == "radiobox"'>
				<div class="edit-check" >		
					<div class="title" :style="{'background-color':elementForm.style?elementForm.style.backgroundColor:'','color':elementForm.style?elementForm.style.color:''}">{{elementForm.title}}</div>
					<el-radio-group v-model="radiobox" class="list" style='display: block;'>
					    <li :style="{'border-color':elementForm.style?elementForm.style.backgroundColor:''}" v-for ='(item,index) in elementForm.data' :key='index'>
					 		<el-radio  :label="item" class='pointer-none'>{{item}}</el-radio >				 		
						</li>
					</el-radio-group>
				</div>
			</template>
			<!--  -->
			<template v-if='type == "select"'>
				<div class="select-box">
					<select class="edit-select pointer-none">
						<option :value='elementForm.title' disabled selected>{{elementForm.title}}</option>
						<option :value="item" v-for ='(item,index) in elementForm.data' :key='index'>{{item}}</option>			
					</select>
					<i class="el-icon el-select__caret el-input__icon el-icon-arrow-down"></i>
				</div>
			</template>			
			<!--  -->	
			<template v-if='type == "link"'>
				<a class="link-box">		
					<img v-if='data' class="link-box-img" :src="$api+data"  />
					<button v-show='!data' class="link-btn pointer-none">{{elementForm.title}}</button>							
				</a>
			</template>	
			<!--  -->	
			<template v-if='type == "phone"'>
				<a class="link-box">	
					<img v-if='data' class="link-box-img" :src="$api+data"  />
					<button v-show='!data' class="link-btn pointer-none">{{elementForm.title}}</button>										
				</a>
			</template>
			<!--  -->
			<template v-if='type == "video"'>
				<div class="edit-video" @dblclick='changeVideo(data)'>
					<img class="video-cover" :src="$api+data.cover" v-if='data.cover'/>
					<img src="../../assets/images/video_play.png" class=" video-play  pointer-none"/>			
				</div>
			</template>
			<!--  -->
			<template v-if='type == "code"'>
				<qrcode-vue :value="elementForm.title" :size="size" level="H"></qrcode-vue>
			</template>
			<!-- -->
			<template v-if='type == "screen"'>
				<div class="link-box">
					<img v-if='data' class="link-box-img" :src="$api+data"  />
					<button v-show='!data' class="link-btn pointer-none"><i class="fa fa-crop" aria-hidden="true"></i>&ensp;{{elementForm.title}}</button>	
				</div>
			</template>
		</div>		
	</li>
</template>
<style type="text/css">
	.element-box .edit-text-box {
		height: 100%;
		text-decoration: inherit;
	}
</style>
<script>

	import ElementBox from '@/assets/script/ElementBox.js'
	export default {
		...ElementBox	
	}
</script>


