import Anim from '@/assets/script/Anim.js'
import FontHuakang from '@/assets/script/FontHuakang.js'
import ElementEdit_addr from '@/pages/edit/ElementEdit_addr'
export default {
	props: ['id', 'type', 'animPower', 'animData', 'data', 'svg','BoxStyleData'],
	components:{
		'element-edit-addr':ElementEdit_addr
	},
	data() {
		return {
			editSubnavNum: 0, //
			animateSubnavNum: 0, //
			name: "",
			setAnimTimeOut: "", //
			setAniStoreTime: '',
			isShowAnimEdit: false, //
			isAnimSplice: false, //
			animSpliceIndex: 0,
			ElementDataIndex: '',
			initFontFamily: FontHuakang.ActiveFontFamily,
			checkType:'',
			initEditData: { //
				style: {
					fontFamily: '', //
					fontSize: 15, //
					lineHeight: 1.5, //
					letterSpacing: 2, //
					color: '', //
					backgroundColor: '', //
					textAlign: '',
					fontStyle: '',
					fontWeight: '',
					textDecoration: '',
					borderWidth: 0,
					borderRadius: 0,
					borderStyle: 'solid',
					borderColor: '',
				},
				form: { //
					title: '',
					style: {},
					data: '',
					store: '', //					
				},
			},
			newEditData: { //
			},
			arrAinmIn: [],
			arrAinmIm: [],
			arrAinmOut: [],
			//
			predefineColors: [
				'#ff4500',
				'#ff8c00',
				'#ffd700',
				'#90ee90',
				'#00ced1',
				'#1e90ff',
				'#c71585',
				'rgba(255, 69, 0, 0.68)',
				'rgb(255, 120, 0)',
				'hsv(51, 100, 98)',
				'hsva(120, 40, 94, 0.5)',
				'hsl(181, 100%, 37%)',
				'hsla(209, 100%, 56%, 0.73)',
				'#c7158577'
			]
		}
	},
	mounted() {
		this.arrAinmIn = Anim.arrAinmIn
		this.arrAinmIm = Anim.arrAinmIm
		this.arrAinmOut = Anim.arrAinmOut
		var _this = this
		this.$nextTick(function() {
			_this.initEleStyle();
			!!_this.data?_this.checkType='':_this.checkType='';
		})
	},
	computed: {
		computedStyle() {
			this.newEditData = JSON.parse(JSON.stringify(this.initEditData))	
			if(this.newEditData.style.fontSize) this.newEditData.style.fontSize = this.newEditData.style.fontSize + 'px';
			if(this.newEditData.style.letterSpacing) this.newEditData.style.letterSpacing = this.newEditData.style.letterSpacing + 'px'
			if(this.newEditData.style.borderWidth) this.newEditData.style.borderWidth = this.newEditData.style.borderWidth + 'px';
			if(this.newEditData.style.borderRadius) this.newEditData.style.borderRadius = this.newEditData.style.borderRadius + 'px';		
			if(this.type == 'checkbox') {
				this.newEditData.form.store = new Array()
			} 
			return this.newEditData
		}
	},
	watch: {
		initEditData: {
			handler(val, oldVal) {	
				var _this = this;
				this.$store.state.ElementData.forEach(function(val, index) {
					if(!val.children) {
						return false
					}
					for(var i = 0; i < val.children.length; i++) {
						if(val.children[i].id == _this.id) {
							val.children[i].EditStyleData = JSON.parse(JSON.stringify(_this.computedStyle))
							break;
						}
					}
				})				
			},
			deep: true
		},

	},
	methods: {
		//
		changeType(val){
			var Vue = this;
			var elementData;
			if(val==''){			
				this.$store.state.ElementData.forEach(function(val, index) {
					if(!val.children) {
						return false
					}
					for(var i = 0; i < val.children.length; i++) {
						if(val.children[i].id == Vue.id) {
							elementData = JSON.parse(JSON.stringify(val.children[i]));
							if(elementData.data == undefined){
								return false;
							}
							elementData.data = undefined;	
							Vue.$set(Vue.initEditData.style,'backgroundColor','rgba(244, 113, 31, 1)')
							elementData.BoxStyleData.width = '160px';
							elementData.BoxStyleData.height = '30px';
							Vue.$set(val.children,i,elementData)							
							break;
						}
					}
				})
			}
		},
		//
		animPowerAdd(item) {
			var Vue = this
			item.isHover = true
			Vue.animPowerClear()
			clearInterval(this.setAnimTimeOut)
			this.setAnimTimeOut = setTimeout(function() {
				if(item.style) {
					Vue.animPower.style.animationDuration = parseFloat(item.style.animationDuration) + 's'
					Vue.animPower.style.animationDelay = parseFloat(item.style.animationDelay) + 's'
					Vue.animPower.style.isInfinite = item.style.isInfinite
					if(Vue.animPower.style.isInfinite) {
						Vue.animPower.style.animationIterationCount = "infinite"
					} else {
						Vue.animPower.style.animationIterationCount = parseFloat(item.style.animationIterationCount)
					}
				}
				Vue.animPower.className = item.className
			}, 50)
		},
		//
		animPowerDel(item) {
			var Vue = this
			this.animPowerClear()
			item.isHover = false
		},
		checkImage(type) {
			this.$emit('checkImage',type)
		},
		//
		animDataSet(item, e) {
			if(!this.isShowAnimEdit) {
				return false
			}
			this.animPowerClear(item)
			if(this.isAnimSplice) {
				this.animData.splice(this.animSpliceIndex, 1, {
					title: item.title,
					className: item.className,
					style: {
						animationDuration: 1,
						animationDelay: 0,
						animationIterationCount: 1,
						isInfinite: false
					}
				})
			} else {
				this.animData.push({
					title: item.title,
					className: item.className,
					style: {
						animationDuration: 1,
						animationDelay: 0,
						animationIterationCount: 1,
						isInfinite: false
					}
				})
			}
			this.isShowAnimEdit = false
			this.isAnimSplice = false
			item.isHover = false
		},
		//
		addCheck(index) {
			var length = this.initEditData.form.data.length
			this.initEditData.form.data.splice(index + 1, 0, '' + (length + 1))
		},
		removeCheck(index) {
			var length = this.initEditData.form.data.length
			if(length <= 1) {
				return false
			}
			this.initEditData.form.data.splice(index, 1)
		},
		//
		animShow() {
			this.animPowerClear()
			var Vue = this
			var count = 0,
				time = 0,
				animShowTimeOut;
			clearInterval(this.setAnimTimeOut)
			this.setAnimTimeOut = setTimeout(function() {
				setAnim()
			}, 50)

			function setAnim() {
				animShowTimeOut = setTimeout(function() {
					if(count >= Vue.animData.length) {
						clearInterval(animShowTimeOut)
						return false
					}
					var className = Vue.animData[count].className,
						animationDuration = parseFloat(Vue.animData[count].style.animationDuration),
						animationDelay = parseFloat(Vue.animData[count].style.animationDelay),
						animationIterationCount = parseInt(Vue.animData[count].style.animationIterationCount),
						isInfinite = Vue.animData[count].style.isInfinite
						Vue.animPower.className = className
						Vue.animPower.style.animationDuration = animationDuration + 's'
						Vue.animPower.style.animationDelay = animationDelay + 's'
						Vue.animPower.style.animationIterationCount = animationIterationCount

					if(isInfinite) {
						time = (animationDuration + animationDelay) * 1
					} else {
						time = (animationDuration + animationDelay) * animationIterationCount
					}
					count++
					setAnim()

				}, time * 1000)
			}
		},
		
		//
		animSplice(index) {
			this.isShowAnimEdit = true
			this.isAnimSplice = true
			this.animSpliceIndex = index
		},
		//
		animDataDel(val, index) {
			this.animData.splice(index, 1)
		},
		//
		animPowerClear() {
			this.animPower.className = ''
			this.animPower.style.animationDuration = ''
			this.animPower.style.animationDelay = ''
			this.animPower.style.animationIterationCount = ''
		},
		//
		initEleStyle() {	
			var _this = this
			var isHasStyle = false;
			_this.initEditData.type = _this.type;
			this.$store.state.ElementData.forEach(function(val, index) {
				if(!val.children) {
					return false
				}
				for(var i = 0; i < val.children.length; i++) {				
					if(val.children[i].id == _this.id) {
						if( val.children[i].EditStyleData && val.children[i].EditStyleData.style) {
							_this.initEditData = JSON.parse(JSON.stringify(val.children[i].EditStyleData))
							if(_this.initEditData.style.fontSize) _this.initEditData.style.fontSize = parseFloat(_this.initEditData.style.fontSize)
							if(_this.initEditData.style.lineHeight) _this.initEditData.style.lineHeight = parseFloat(_this.initEditData.style.lineHeight)
							if(_this.initEditData.style.letterSpacing) _this.initEditData.style.letterSpacing = parseFloat(_this.initEditData.style.letterSpacing)
							if(_this.initEditData.style.borderRadius) _this.initEditData.style.borderRadius = parseFloat(_this.initEditData.style.borderRadius);
							if(_this.initEditData.style.borderWidth) _this.initEditData.style.borderWidth = parseFloat(_this.initEditData.style.borderWidth);
							
						
						} else {
							console.log('')		

						}
					}
				}
			})
		}
	},
}


