import Vue from 'vue'
import CryptoJS from 'crypto-js'

export default {
	getAES(data) { //
		var key = CryptoJS.enc.Hex.parse("e10adc3949ba59abbe56e057f20f883e"); //md5('123456')
		var iv = CryptoJS.enc.Hex.parse("1234567890abcdef1234567890abcdef");
		var opinion = {
			iv: iv,
			padding: CryptoJS.pad.ZeroPadding
		};
		var encrypted = CryptoJS.AES.encrypt(data, key, opinion);
		encrypted = encrypted.toString();
		return encrypted;
	},
	getDAes(data) { //
		var key = CryptoJS.enc.Hex.parse("e10adc3949ba59abbe56e057f20f883e"); //md5('123456')
		var iv = CryptoJS.enc.Hex.parse("1234567890abcdef1234567890abcdef");
		var opinion = {
			iv: iv,
			padding: CryptoJS.pad.ZeroPadding
		};
		var decrypted = CryptoJS.AES.decrypt(data, key, opinion);
		decrypted = decrypted.toString(CryptoJS.enc.Utf8);
		return decrypted;
	},
	getCookie(name) {
		var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
		if(arr = document.cookie.match(reg))
			return(arr[2]);
		else
			return null;
	},

	//cookie,vue
	setCookie(c_name, value, expiredays) {
		var exdate = new Date();
		exdate.setDate(exdate.getDate() + expiredays);
		document.cookie = c_name + "=" + escape(value) + ((expiredays == null) ? "" : ";expires=" + exdate.toGMTString());
	},

	//cookie
	delCookie(name) {		
		var exp = new Date();
		exp.setTime(exp.getTime() - 1);
		var cval = this.getCookie(name);
		if(cval != null)
			document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
	},
	setSessionStorag(key,value){
		sessionStorage.setItem(key, value);
	},
	getSessionStorag(key){
		var value = sessionStorage.getItem(key);     
		return value;
	},
	clearSessionStorag(key){
		sessionStorage.removeItem(key);
	},
	//
	timestampToTime(timestamp) {
		var date = new Date(timestamp * 1000); //10*1000131000
		var Y = date.getFullYear() + '-';
		var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
		var D = (date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate()) + ' ';
		var h = (date.getHours() < 10 ? ('0' + date.getHours()) : date.getHours()) + ':';
		var m = (date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()) + ':';
		var s = (date.getSeconds() < 10 ? ('0' + date.getSeconds()) : date.getSeconds());
		return Y + M + D + h + m + s;
	},
	//IE
	isIE() {
		if(!!window.ActiveXObject || "ActiveXObject" in window) {
			return true;
		} else {
			return false;
		}
	},
	random(n) { // n
		var str = "abcdefghijklmnopqrstuvwxyz0123456789"; // random
		var result = "";
		for(var i = 0; i < n; i++) {
			result += str[parseInt(Math.random() * str.length)];
		}
		return result.toString();
	},
	//
	getStyle(obj, attr) {
			if(obj.currentStyle) {
				return obj.currentStyle[attr];
			} else if(getComputedStyle) {
				var styleVal = getComputedStyle(obj, null)[attr] ?
					getComputedStyle(obj, null)[attr] :
					getComputedStyle(obj, null).getPropertyValue(attr);
				return styleVal;
			}
		},
	//
	GenNonDuplicateID(randomLength){
	  return Number(Math.random().toString().substr(3,randomLength) + Date.now()).toString(36)
	}
		
}


