<template>
		<div class="detail-content">
			<div class="preview-show">
				<element-list  v-if='isLoadSuccess' ref='ElementList'  :arrEleData='arrEleData' :isAutoPlay='swiper.isAutoPlay' :autoplay="swiper.autoplay" :music='music' :effect='swiper.type' :direction='swiper.direction'></element-list>	
				<div class="preview-page">
					<div class="preview-page-up"></div>
					<div class="preview-page-down"></div>
				</div>
			</div>
			<div class="detail-info">
				<div class="detail-info-top">
					<div class="cover-img">
						<img :src="cover_img==''?'./static/images/cover.png':$api+cover_img" />
					</div>
					<div class="content">
						<p style="font-size: 17px;margin-bottom: 10px;">{{title}}</p>						
						<p style="margin-bottom: 10px;">{{create_time}}</p>
						<p>{{describe}}</p>
					</div>
				</div>
				<div class="detail-info-link">
					<input type="text" :value='$link+"#/"+id' readonly id="share-link" />
					<el-button type="primary" @click="copyLink"></el-button>
				</div>
				<div class="detail-info-code">
					<qrcode-vue :value='$link+"#/"+id' :size="160" level="H"></qrcode-vue>
				</div>
				</br>
				<router-link :to="'/edit/'+id">
					<el-button plain type="primary" style="margin-top: 15px;"></el-button>
				</router-link>
			</div>
		</div>

</template>

<script>
	import QrcodeVue from 'qrcode.vue';
	import ElementList from '@/components/ElementList'
	export default {
		components: {
			QrcodeVue,
			ElementList
		},
		name: 'preview',
		data() {
			return {
				Vue: this,
				id: this.$route.params.id,
				isShowPreviewSlip: false,
				setAnimTimeOut: '',
				arrEleData: [],
				music: '',
				title: '',
				cover_img: '',
				describe: '',
				create_time: '',
				isPlayMusic: false,
				previewForm: {
					titleLimit: 48,
					describeLimit: 60,
				},
				swiper: '',
				checkbox: [],
				radiobox: [],
				selectbox: [],
				isSubmitForm: false,
				isShowTip: false, //
				isShowTipSuccess: false, //
				slideArr: [], //slide
				pageActiveIndex:0, //
				isLoadSuccess:false
			}
		},
		created() {
			if(this.utill.getCookie('username')) {
				this.$store.state.user.username = this.utill.getCookie('username')
				this.axiosGetSceneData()
			} else {
				this.$router.push({
					name: 'home'
				});
			}
		},
		methods: {
			axiosGetSceneData() {
				var Vue = this
				Vue.openLoading()
				this.http.axiosGetSceneData({
					id: Vue.id,
					username: this.$store.state.user.username
				}).then((res) => {
					if(res.data.status == 1) {
						for(var i = 0; i < res.data.data.length; i++) {
							Vue.arrEleData.push(JSON.parse(res.data.data[i].data))
						}
						//
						if(res.data.info.issue != 1) {
							this.$alert('', '', {
								confirmButtonText: '',
								callback: action => {
									this.$router.push({
										name: 'MyScene_List',
									})
								}
							});
							
						}
						Vue.swiper = JSON.parse(res.data.info.swiper)
						Vue.music = JSON.parse(res.data.info.music)
						Vue.describe = res.data.info.describe
						Vue.cover_img = res.data.info.cover_img
						Vue.title = res.data.info.title
						Vue.create_time = Vue.utill.timestampToTime(Number(res.data.info.create_time))
						//
						Vue.arrEleData.forEach(function(val, index) {
							if(val.children) {
								for(var i = 0; i < val.children.length; i++) {
									if(val.children[i].type == "checkbox") {
										Vue.$set(val.children[i].EditStyleData.form, 'store', [])
									}
									//
									if(!val.children[i].animData) {
										Vue.$set(val.children[i], 'animData', [])
									} else {
										//
										for(var t = 0; t < val.children[i].animData.length; t++) {
											if(typeof(val.children[i].animData[t].style.isInfinite) == 'string') {
												val.children[i].animData[t].style.isInfinite = eval(val.children[i].animData[t].style.isInfinite.toLowerCase())
											}
										}
									}

								}
							}
						})
						Vue.$nextTick(() => {
							Vue.closeLoading()
							Vue.isLoadSuccess = true														
//							Vue.$refs.ElementList.createSwiper()
						})

					} else if(res.data.status == 0) {

					}
				})
			},

			copyLink() {
				var shareLink = document.getElementById("share-link");
				shareLink.select(); // 
				document.execCommand("Copy"); // 
				this.$message.success('')
			},			
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>
<style src='@/assets/style/preview.scss' lang="scss"></style>
<style src='@/assets/style/myScene.scss' lang="scss"></style>


