<template>
	<div class="template-page">
		<div class="searchbox">
			<div class="search-wraper">
				<div class="seach">
					<input type="text"  v-model.trim="Scene.title" />
					<button @click='searchTemplateScene'>&emsp;</button>
				</div>
			</div>
		</div>
		<div class="scene-box">
			<h2 class="title"></h2>
			<ul class="scene-list">				
				<li class="item list-blank" @click="createEdit">
					<i class="el-icon-plus"></i>
					<p></p>
				</li>				
				<li class="item" v-for="item in Scene.data">
					<router-link target="_blank" class='link' :to='"/template/detail/"+item.edit_id+"/"+item.username'>
						<img class="template-cover" :src="$api + item.cover_img"/>
						<p class="template-title overone">{{item.title}}</p>
						<div class="template-visit"><i class="fa fa-fire" aria-hidden="true"></i><span style="margin-left: 5px;">{{item.count}}</span></div>
						<div class="template-author">
							<p class="name">{{item.username}}</p>
							<img class="head" :src="item.head_src?$api+item.head_src:''"/>
						</div>
					</router-link>
				</li>
				
			</ul>
			<el-pagination background layout="prev, pager, next,total,jumper" @current-change='changePage' :page-size='Scene.pageSize' :total="Scene.count"></el-pagination>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				Scene:{
					page:1,
					pageSize:24,
					data:[],
					count:0,
					search:'',
					title:'',
				}
			}
		},
		mounted(){
			this.$nextTick(()=>{
				this.listenerAction()
			})
		},
		beforeDestroy() { //			
            window.removeEventListener('scroll', this.scrollhandle);
        },
        created(){
        	this.axiosSelectTemplateScene()
        },
		methods:{
			changePage(page){
				this.Scene.page = page;
				this.axiosSelectTemplateScene()
			},
			listenerAction() { 
                window.addEventListener('scroll', this.scrollhandle);
			},
			scrollhandle() {
                var scrollTop =  window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop 
				var secachBox = document.getElementsByClassName('searchbox')[0]
				if(scrollTop>50){
					secachBox.style.backgroundColor = 'rgba(255,255,255,.7)'					
				}else {
					secachBox.style.backgroundColor = 'rgba(255,255,255,1)'
				}
			},
			//
			searchTemplateScene(){
				this.Scene.page = 1;
				this.Scene.search = this.Scene.title;
				this.axiosSelectTemplateScene()
			},
			createEdit() {
				this.$store.state.user.username = this.utill.getCookie('username')
				if(this.$store.state.user.username) {} else {	
					this.$store.state.isShowLogin = true
				}
				let routeData = this.$router.resolve({
					name: "edit",
					params: {
						name: 'edit',
						id: this.$store.state.user.username+this.utill.GenNonDuplicateID(5)
					},
				});
				window.open(routeData.href, '_blank');
			},
			axiosSelectTemplateScene(){
				this.openLoading()
				this.http.axiosSelectTemplateScene({
					page:this.Scene.page,
					pageSize: this.Scene.pageSize,
					search:this.Scene.search
				}).then((res)=>{
					this.closeLoading()
					this.Scene.data = [];
					this.Scene.count = 0;
					this.Scene.title = this.Scene.search;
					if(res.data.status==1){
						this.Scene.data = res.data.data
						this.Scene.count = res.data.count
					}										
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>

<style src='@/assets/style/template.scss' lang='scss' scoped></style>


