<template>
	<div class="editPage">
		<div class="wrap">
			<!--   -->
			<div class="nav">
				<ul class="nav-list">
					<li @click="setPart($event,'text')">
						<i class="fa fa-text-width" aria-hidden="true"></i>
						<span></span>
					</li>
					<li @click="showImgBar('image')">
						<i class="fa fa-file-image-o" aria-hidden="true"></i>
						<span></span>
					</li>
					<li @click="showImgBar('background')">
						<i class="fa fa-columns" aria-hidden="true"></i>
						<span></span>
					</li>
					<li @click='$refs.materialMusic.initMusic()'>
						<i class="fa fa-music" aria-hidden="true"></i>
						<span></span>
					</li>
					<li @click='$refs.materialSvg.showSvgsBar()'>
						<i class="fa fa-retweet" aria-hidden="true"></i>
						<span></span>
					</li>
					<li v-popover:ppor-subassembly>
						<i class="fa fa-th-large" aria-hidden="true"></i>
						<span></span>
					</li>
					<li v-popover:ppor-form>
						<i class="fa fa-file-text-o" aria-hidden="true"></i>
						<span></span>
					</li>
				</ul>
				<!--  -->
				<el-popover placement="bottom"  trigger="hover" ref='ppor-subassembly'>
					<div style="width: 460px;" class='ppor-form'>
						<el-row>
							<el-col :span="6">
								<div class="grid-title bg-gray"></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="initVideo"><i class="fa fa-video-camera" aria-hidden="true"></i></i></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'link')"><i class="fa fa-link" aria-hidden="true"></i></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'phone')"><i class="fa fa-phone-square" aria-hidden="true"></i></div>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span="6">
								<div class="grid-title bg-gray"></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'code')"><i class="fa fa-qrcode" aria-hidden="true"></i></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'screen')"><i class="fa fa-crop" aria-hidden="true"></i></div>
							</el-col>
						</el-row>
					</div>
				</el-popover>
				<!--  -->
				<el-popover placement="bottom" trigger="hover" ref='ppor-form'>
					<div style="width: 460px;" class='ppor-form'>
						<el-row>
							<el-col :span="6">
								<div class="grid-title bg-gray"></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'input')"><i class="fa fa-edit"></i></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'submit')"><i class="fa fa-hand-paper-o"></i></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'message')"><i class="fa fa-sort-alpha-desc"></i></div>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span="6">
								<div class="grid-title bg-gray"></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'radiobox')"><i class="fa fa-dot-circle-o"></i></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'checkbox')"><i class="fa fa-check-square-o"></i></div>
							</el-col>
							<el-col :span="6">
								<div class="grid-content bg-white" @click="setPart($event,'select')"><i class="fa fa-navicon"></i></div>
							</el-col>
						</el-row>
					</div>
				</el-popover>
				<div class="nav-set">
					<el-button type="primary" @click='$store.state.isShowPreview=true'></el-button>
					<el-button type="primary" @click="saveData"></el-button>
					<el-button type="primary" @click="confirmIssue"></el-button>
					<el-button type="info" @click="closeEditor"></el-button>
				</div>
			</div>
			<!--   -->
			<div class="material" v-if='VideoForm.isShow'>
				<div class="video-box">
					<div class="video-top">
						<el-row>
							<el-col :span="12" class="title"></el-col>
							<el-col :span="12" class="close"><i class="el-icon-close colse" @click="VideoForm.isShow = false"></i></el-col>
						</el-row>
					</div>
					<div class="video-main">
						<div class="left">
							<div class="video-cover">
								<img class="video-cover-img" :src="VideoForm.cover?$api+VideoForm.cover:''" v-if='VideoForm.cover' />
								<img class="video-cover-play" src="../../assets/images/video_play.png" />
							</div>
						</div>
						<div class="right">
							<el-form :model="VideoForm" label-width="80px" class='video-form' ref='video-form' :rules='VideoRule'>
								<a class="tip" target="_blank" href="https://jingyan.baidu.com/article/020278114678a81bcd9ce55e.html"></a>
								<el-form-item label-width="0px" prop='addr'>
									<el-input class="textarea" v-model="VideoForm.addr" type="textarea" placeholder=""></el-input>
								</el-form-item>
								<el-form-item label="">
									<el-select v-model="VideoForm.type" size="small">
										<el-option label="" value="1"></el-option>
										<el-option label="" value="2"></el-option>
									</el-select>
								</el-form-item>
								<el-form-item label="">
									<el-button type="primary" size="small" style="width: 100%;" @click='showImgBar("video","video-form")'></el-button>
								</el-form-item>
							</el-form>
						</div>
					</div>
					<div class="video-footer">
						<el-button type="default" @click="VideoForm.isShow = false"></el-button>
						<el-button type="primary" @click='createVideo("video-form",$event)'></el-button>
					</div>
				</div>
			</div>
			<!--   -->
			<material-img @setPartMaterial='setPartMaterial' ref='materialImg'></material-img>
			<!--   -->
			<material-music ref='materialMusic'></material-music>
			<material-svg @setPart='setPart' ref='materialSvg'></material-svg>
			<!--  -->
			<board ref='board'></board>
			<!--  -->
			<ul id="opera">
				<el-tooltip class="item" effect="dark" content="" placement="left">
					<li @click="revocation"><i class="fa fa-reply" aria-hidden="true"></i></li>
				</el-tooltip>
				<el-tooltip class="item" effect="dark" content="" placement="left">
					<li @click="backRevocation"><i class="fa fa-share" aria-hidden="true"></i></li>
				</el-tooltip>
				<el-tooltip class="item" effect="dark" content="" placement="left">
					<li @click='$store.state.isShowPreview=true'><i class="fa fa-play" aria-hidden="true"></i></li>
				</el-tooltip>
				<el-tooltip class="item" effect="dark" content="" placement="left">
					<li @click='copyPage'><i class="fa fa-files-o" aria-hidden="true"></i></li>
				</el-tooltip>
				<el-tooltip class="item" effect="dark" content="" placement="left">
					<li @click='foucsPageAttr'><i class="fa fa-file-text-o" aria-hidden="true"></i></li>
				</el-tooltip>
				<el-tooltip class="item" effect="dark" content="" placement="left">
					<li @click='$refs.materialMusic.initMusic()'><i class="fa fa-music" aria-hidden="true"></i></li>
				</el-tooltip>
				<el-tooltip class="item" effect="dark" content="" placement="left">
					<li @click='setPhoneBorder'><i style="font-size: 22px;" class="fa fa-mobile" aria-hidden="true"></i></li>
				</el-tooltip>
				
				<el-tooltip class="item" effect="dark" content="PSD" placement="left">					
					<li>
						<el-upload class="psd-uploader" 
							:action="$api+'/index.php/Home/Material/UploadPsd?username='+$store.state.user.username" 
							:on-success="handlePsdSuccess"
							:show-file-list="false"
							:before-upload="beforePsdUpload">
						<i class="fa fa-paypal" aria-hidden="true"></i>
						</el-upload>
					</li>					
				</el-tooltip>
				<li class="scale-add" @click='operaScale("add")'><i style="font-weight: 700;" class="el-icon-plus" aria-hidden="true"></i</li>
				<li class="scale-value"><span>{{$store.state.scaleRatio}}%</span></li>
				<li class="scale-minus" @click='operaScale("minus")'><i style="font-weight: 700;" class="el-icon-minus" aria-hidden="true"></i></li>
			</ul>
			<!--  -->
			<div class="edit">
				<div class="edit-set">
					<ul class="edit-nav">
						<li v-if='focusPart.length==1' :class="{active:$store.state.editNavNum == 0}" @click="$store.state.editNavNum=0"></li>
						<li v-else-if='focusPart.length<=0' :class="{active:$store.state.editNavNum == 1}" @click="$store.state.editNavNum=1"> </li>
						<li v-else :class="{active:$store.state.editNavNum == 4}" @click="$store.state.editNavNum=4"> </li>
						<li :class="{active:$store.state.editNavNum == 3}" @click="$store.state.editNavNum=3"></li>
						<li :class="{active:$store.state.editNavNum == 2}" @click="$store.state.editNavNum=2"></li>
					</ul>
					<div class="edit-main">
						<!--  -->
						<div v-for="(item,index) in $store.state.ElementData" v-if='$store.state.editNavNum==0'>
							<component @cacheData='cacheData' @checkImage='showImgBar' is="element-edit" :type='items.type' :svg='items.svg' v-if='focusPart.indexOf(items.id) !=-1' v-show='focusPart.length==1' :data='items.data' :id='items.id' v-for="items in item.children" :animPower='items.animPower' :BoxStyleData='items.BoxStyleData' :animData='items.animData' :key='items.id'></component>
						</div>
						<!--  -->
						<div v-if='$store.state.editNavNum==4'>
							<select-panel @clipPart='clipPart' @copyPart='copyPart' @cacheData='cacheData'></select-panel>
						</div>
						<!--   -->
						<div v-show='$store.state.editNavNum==1'>
							<div class="pageAttr" v-for="(item,index) in $store.state.ElementData" v-if='item.id == $store.state.focusPage'>
								<div class="pageAttr-wraper">
									<div class='cutoff'>
										<div></div>
									</div>
									<div style="margin-bottom: 14px;">
										<label></label>
										<div class="pageAttr-backWrap">
											<div class="pageAttr-backBox" :style="{
											opacity:item.style.opacity,
											backgroundColor:item.style.backgroundColor,
											backgroundImage:'url('+(item.style.backgroundImageUrl?($api+item.style.backgroundImageUrl):'')+')',
										}"></div>
										</div>
										<div class="pageAttr-set">
											<el-button type="primary" @click='showImgBar("background")'></el-button>
											<el-button type="primary" plain @click='clearPageBack(item)'></el-button>
										</div>
									</div>
									<el-row :gutter="20">
										<el-col :span="8">
											<label></label>
										</el-col>
										<el-col :span="16">
											<el-color-picker  :predefine="predefineColors" v-model='item.style.backgroundColor' @change='pageBackColor(item)'></el-color-picker>
										</el-col>
									</el-row>
									<el-row :gutter="20">
										<el-col :span="8">
											<label></label>
										</el-col>
										<el-col :span="16">
											<el-input-number v-model="item.style.opacity" :min="0" :max="1" :step="0.1"></el-input-number>
										</el-col>
									</el-row>
									<div class='cutoff'>
										<div></div>
									</div>
									<div>
										<label></label></br>
										<el-button class='back-music-btn' @click='$refs.materialMusic.initMusic()'>
											<template v-if='$store.state.Music.name'>
												<i class="el-icon-edit"></i>{{$store.state.Music.name?$store.state.Music.name:''}}
											</template>
											<template v-else>
												<i class="el-icon-plus"></i>
											</template>
										</el-button>
									</div>
								</div>
							</div>
						</div>
						<!--  -->
						<div class="pageEdit" v-show='$store.state.editNavNum==2'>
							<div class="pageEdit-set">
								<el-tooltip content="" placement="bottom">
									<div @click='setPage'><i class="fa fa-plus" aria-hidden="true"></i></div>
								</el-tooltip>
								<el-tooltip content="" placement="bottom">
									<div @click='copyPage'><i class="fa fa-files-o" aria-hidden="true"></i></div>
								</el-tooltip>
								<el-tooltip content="" placement="bottom">
									<div @click='delPage'><i class="fa fa-trash-o" aria-hidden="true"></i></div>
								</el-tooltip>
							</div>
							<ul class="pageEdit-list">
								<el-scrollbar style='height: 100%;'>
									<li @mousedown="changeDragging" v-for='(item,index) in $store.state.ElementData' :key='item.id' :class="{'active':item.id==$store.state.focusPage}" v-dragging="{ item: item, list: $store.state.ElementData, group: 'page'}" @click='changePage(item.id)' @contextmenu.prevent="opearPage(item.id,$event)">
										<span class="page-index">{{Number(index)+1}}</span>
										<input class="page-name" @blur="pageNameBlur" maxlength="10" @change="changePageName($event,item)" @dblclick.stop="changePageInput" type="text" :value="item.name" readonly/>
									</li>
								</el-scrollbar>
							</ul>
							<div class="pageEdit-bottom">
								<el-button type="primary" @click='setPage'><i class="el-icon-plus"></i></el-button>
								<el-button type="info" @click='delPage'><i class="el-icon-delete"></i> </el-button>
							</div>
						</div>
						<div v-if='$store.state.editNavNum==3 && item.id == $store.state.focusPage' style="height: 100%;" v-for='item in $store.state.ElementData'>
							<layer :data="item"></layer>
						</div>
					</div>
				</div>
			</div>
			<!--  -->
			<div id="main-parent" @mouseup="cacheData" @contextmenu.prevent='showOperate'>
				<div class="main-parent-box">
					<div class="main-parent-line">
						<div class="line-x"></div>
						<div class="line-y"></div>
					</div>
					<div id="main">
						<div class="border-iphonex" v-show="isShowIphoneX">
							<span class="title">iPhone X</span>
						</div>
						<div class="main-box" >
							<ul class="main-wraper" v-for="(item,index) in $store.state.ElementData" v-if='item.id==$store.state.focusPage'>
								<div class="main-back" :style="{
									opacity:item.style.opacity,
									backgroundColor:item.style.backgroundColor,
									backgroundImage:'url('+(item.style.backgroundImageUrl?($api+item.style.backgroundImageUrl):'')+')',
								}"></div>
								<ul class="main-ruler">
									<li class="item" v-for='item in 96'></li>
								</ul>
								<component ref='element-box' @changeVideo="changeVideo" is="element-box" :type='items.type' :data='items.data' :svg='items.svg' :imgSrc='items.imgSrc' :animPower='items.animPower' :BoxStyleData="items.BoxStyleData" :animData='items.animData' :id='items.id' v-for="items in item.children" :key='items.id' @showOpetare='showOperate'></component>
							</ul>
						</div>
					</div>
				</div>
			</div>
			<preview v-if='$store.state.isShowPreview' @saveDate="saveData" @confirmIssue='confirmIssue' @changeCover='showImgBar("cover")'></preview>
		</div>
		<ul class='menuOpera' id="templateOpera" v-show='templateOpera'>
			<li v-if='$store.state.ElementClipboard' @click.prevent='clipPart'></li>
			<template v-show='focusPart.length == 1'>
				<li @click.prevent='copyPart'></li>
				<li @click.prevent='clearPart'></li>
				<li @click='changeBoxZIndex("top")'></li>
				<li @click='changeBoxZIndex("bottom")'></li>
				<li @click='changeBoxZIndex("up")'></li>
				<li @click='changeBoxZIndex("down")'></li>
			</template>
		</ul>
		<ul class='menuOpera' id="pageOpera" v-show='pageOpera'>
			<template>
				<li @click='setPage'></li>
				<li @click="changePageInput"></li>
				<li @click='copyPage'></li>
				<li @click='delPage'></li>
				<li @click="savePageboard(1)"></li>
				<li v-if='this.$store.state.user.grade == 2' @click="savePageboard(2)"></li>
			</template>
		</ul>
		<el-dialog title="" :close-on-click-modal='false' :visible.sync="$store.state.isShowLogin" width='550px'>
			<login v-if='$store.state.isShowLogin' ref="childLogin" @closeLogin='$store.state.isShowLogin = false'></login>
		</el-dialog>
		<el-dialog title="" :close-on-click-modal='false' :visible.sync="dialogBlob" v-if='dialogBlob' width='960px'>
			<blob :src='$api+imgBlobSrc' :type="materialType" ref='imgBlob' @showBack='showBack'></blob>
			<div style="text-align: right;margin-top: 20px;">
				<el-button type="primary" @click="setBlob"></el-button>
				<el-button plain @click='dialogBlob = false'></el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import Edit from '@/assets/script/Edit.js'
	export default {
		...Edit
	}
</script>
<style src='@/assets/style/edit.scss' lang='scss'>
	.material-content>li img {
		max-width: 100%;
		max-height: 100%;
	}
</style>


