<template>
	<div class="material" v-if="Images.isShow">
		<div class="material-box">
			<div class="material-left">
				<h3 class="head">
					&ensp;<span class="el-icon-info"></span>
				</h3>
				<ul class="material-sort">
					<li @click="getImages($event,'all')" :class="{active:Images.type=='all'}"></li>
					<li @click="getImages($event,'my')" :class="{active:Images.type=='my'}"></li>
					<li @click="getImages($event,'collect')" :class="{active:Images.type=='collect'}"></li>
				</ul>
				<ul class="fun">
					<li>
						<el-upload class="upload-demo" ref='upload-image' :action="$api+'/index.php/Home/Material/UploadImg?username='+$store.state.user.username" :show-file-list="false" :on-success="handleSuccessImg" :on-error="handleErrorImg" :before-upload="beforeUploadImg" multiple>
							<el-button type="primary" icon='el-icon-upload' @click='clearImageFiles' round></el-button>
						</el-upload>
					</li>
				</ul>
			</div>
			<div class="material-right">
				<i class="el-icon-close colse" @click="closeImageMaterial"></i>
				<div v-if='Images.type=="all"'>
					<ul class="nav" >
						<li class="active"></li>
					</ul>
					<div class="conent-box">
						<el-scrollbar style='height: 469px;'>
							<ul class="material-content">
								<img class="empty-icon" src="@/assets/images/empty.png" v-if='Images.count<=0' />
								<li class="material-item" v-for="item in Images.data" @click.stop="setPartMaterial($event,item.img_url)">
									<img :src="$api+item.img_url" :class="{'IE-IMG':utill.isIE()}" />
									<div class="check-back" :class='{check:item.check}' @click.stop='checkImage(item)' v-if='Images.isShowOpera'>
										<div class="check-box" v-if='Images.isShowOpera && Images.type=="my"'>
											<i class="el-icon-check"></i>
										</div>
									</div>
									<i :class="{'active':item.collect == 1}" class="fa fa-heart-o collect-icon"@click.stop='collectImage(item)' aria-hidden="true"></i>
								</li>
							</ul>
						</el-scrollbar>
						<el-pagination v-if='Images.count>0' background layout="prev, pager, next,total,jumper" :current-page.sync="Images.page" :page-size='Images.pageSize' :total="Images.count" @current-change="changeImagePage"></el-pagination>
					</div>
				</div>
				<div v-if='Images.type=="my"'>
					<ul class="nav">
						<li :class="{'active':Images.myNav == 1}" @click='changeMyImageNav(1)'></li>
						<li v-if='$store.state.user.grade == 2' :class="{'active':Images.myNav == 2}" @click='changeMyImageNav(2)'></li>
					</ul>
					<div class="conent-box">
						<div class="material-opera" v-if='!Images.isShowOpera && Images.count>0'>
							<el-button type="primary" icon="el-icon-setting" plain round @click.stop='Images.isShowOpera=true'></el-button>
						</div>
						<div class="material-opera" v-if='Images.isShowOpera && Images.count>0'>
							<el-checkbox v-model="Images.checkAll" class='check-all' @change='checkAllImage'></el-checkbox>
							<el-button type="danger" round :disabled='Images.cash.length<=0' v-if='Images.myNav == 1' @click.stop='delImage'></el-button>
							<el-button type="danger" round :disabled='Images.cash.length<=0' v-if='$store.state.user.grade == 2 && Images.myNav == 2' @click.stop='delComImage'></el-button>
							<el-dropdown trigger="click" @command='handSetImageType' v-if='Images.myNav == 1 && $store.state.user.grade == 2'>
								<el-button plain round :disabled='Images.cash.length<=0'>
									&ensp;&emsp;&emsp;<i class="el-icon-arrow-down el-icon--right"></i>
								</el-button>
								<el-dropdown-menu slot="dropdown">
									<el-dropdown-item command='2'></el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>
							<el-button type="primary" round @click.stop='Images.isShowOpera=false'></el-button>
						</div>
						<el-scrollbar style='height: 424px;'>
							<ul class="material-content">
								<img class="empty-icon" src="@/assets/images/empty.png" v-if='Images.count<=0' />
								<li class="material-item" v-for="item in Images.data" @click.stop="setPartMaterial($event,item.img_url)">
									<img :src="$api+item.img_url" :class="{'IE-IMG':utill.isIE()}" />
									<div class="check-back" :class='{check:item.check}' @click.stop='checkImage(item)' v-if='Images.isShowOpera'>
										<div class="check-box" v-if='Images.isShowOpera && Images.type=="my"'>
											<i class="el-icon-check"></i>
										</div>
									</div>
								</li>
							</ul>
						</el-scrollbar>
						<el-pagination v-if='Images.count>0' background layout="prev, pager, next,total,jumper" :current-page.sync="Images.page" :page-size='Images.pageSize' :total="Images.count" @current-change="changeImagePage"></el-pagination>
					</div>
				</div>
				<div v-if='Images.type=="collect"'>
					<ul class="nav">
						<li class="active"></li>						
					</ul>
					<div class="conent-box">
						<div class="material-opera" v-if='!Images.isShowOpera && Images.count>0'>
							<el-button type="primary" icon="el-icon-setting" plain round @click.stop='Images.isShowOpera=true'></el-button>
						</div>
						<div class="material-opera" v-if='Images.isShowOpera && Images.count>0'>
							<el-checkbox v-model="Images.checkAll" class='check-all' @change='checkAllImage'></el-checkbox>
							<el-button type="danger" round :disabled='Images.cash.length<=0' @click.stop='delCollectImage'></el-button>
							<el-button type="primary" round @click.stop='Images.isShowOpera=false'></el-button>
						</div>
						<el-scrollbar style='height: 424px;'>
							<ul class="material-content">
								<img class="empty-icon" src="@/assets/images/empty.png" v-if='Images.count<=0' />
								<li class="material-item" v-for="item in Images.data" @click.stop="setPartMaterial($event,item.img_url)">
									<img :src="$api+item.img_url" :class="{'IE-IMG':utill.isIE()}" />
									<div class="check-back" :class='{check:item.check}' @click.stop='checkImage(item)' v-if='Images.isShowOpera'>
										<div class="check-box" v-if='Images.isShowOpera'>
											<i class="el-icon-check"></i>
										</div>
									</div>
								</li>
							</ul>
						</el-scrollbar>
						<el-pagination v-if='Images.count>0' background layout="prev, pager, next,total,jumper" :current-page.sync="Images.page" :page-size='Images.pageSize' :total="Images.count" @current-change="changeImagePage"></el-pagination>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				Images: { //
					page: 1, //
					count: 0, //
					pageSize: 40, //
					data: [], //
					cash: [], //
					checkAll: false,
					isShowOpera: false,
					type: 'all',
					successUpload: [], //
					isShow: false, //
					nav: 'all',
					myNav: 1, //12
					allNav: 1
				},
			}
		},
		methods: {
			setPartMaterial(e, value) {
				this.$emit('setPartMaterial', e, value)
			},
			//
			showImgBar(type) {
				this.Images.isShow = true;
				this.Images.cash = [];
				this.Images.checkAll = false;
				this.Images.isShowOpera = false;
				this.materialType = type;
				if(this.Images.type == 'all') {
					this.axiosGetAllImages();
				} else if(this.Images.type == 'my') {
					if(this.Images.myNav == 1) {
						this.axiosGetMyImages();
					} else if(this.Images.myNav == 2) {
						this.axiosGetMyComImages(2);
					}

				}

			},
			initImage() {
				this.Images.isShowOpera = false;
				this.Images.page = 1;
				this.Images.cash = [];
				this.Images.checkAll = false;
			},
			//
			closeImageMaterial() {
				this.Images.isShow = false
			},
			changeMyImageNav(type) {
				if(this.Images.myNav == type) {
					return false;
				}
				this.initImage()
				this.Images.myNav = type
				if(type == 1) {
					this.axiosGetMyImages()
				} else if(type == 2) {
					this.axiosGetMyComImages(2)
				}
			},
			//
			collectImage(item){
				this.openLoading()
				this.http.axiosCollectImage({
					id: item.id,
					username: this.$store.state.user.username
				}).then((res)=>{
					this.closeLoading()
					if(res.data.status == 1){
						this.$set(item,'collect',1)
						this.$message.success('')
					}else {
						this.$message.error(res.data.msg)
					}
				})
			},
			//
			axiosGetCollectImages(){
				this.openLoading()
				this.http.axiosGetCollectImage({
					page:this.Images.page,
					pageSize:this.Images.pageSize,
					username: this.$store.state.user.username
				}).then((res)=>{
					this.closeLoading()								
					this.Images.data = res.data.data
					this.Images.count = Number(res.data.count)					
				})
			},
			//
			delCollectImage(){
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.openLoading()
					this.http.axiosDelCollectImage({
						data: this.Images.cash
					}).then((response) => {
						this.closeLoading()
						this.Images.checkAll = false;						
						if(response.data.status == 1){
							if((this.Images.count - this.Images.cash.length) % this.Images.pageSize == 0) {
								if(this.Images.page<=1){
									this.Images.page = 1
								}else {
									this.Images.page --
								}
								
							}	
							this.axiosGetCollectImages()
						}
						this.Images.cash = [];
					})
				}).catch(() => {});
				
			},
			//
			axiosGetAllImages() {
				var Vue = this
				this.openLoading()
				this.http.axiosGetAllImages({
					page: Vue.Images.page,
					pageSize: Vue.Images.pageSize,
				}).then((response) => {
					this.closeLoading()
					Vue.Images.data = response.data.data
					Vue.Images.count = parseInt(response.data.count)
				})
			},
			//
			axiosGetMyImages() {
				var Vue = this
				this.openLoading()
				this.http.axiosGetMyImages({
					page: Vue.Images.page,
					pageSize: Vue.Images.pageSize,
					username: this.$store.state.user.username
				}).then((response) => {
					this.closeLoading()
					Vue.Images.data = response.data.data
					Vue.Images.count = parseInt(response.data.count)
				})
			},
			axiosGetMyComImages(grade) {
				var Vue = this
				this.openLoading()
				this.http.axiosGetMyImages({
					page: Vue.Images.page,
					pageSize: Vue.Images.pageSize,
					username: this.$store.state.user.username,
					grade: grade,
				}).then((response) => {
					this.closeLoading()
					Vue.Images.data = response.data.data
					Vue.Images.count = parseInt(response.data.count)
				})
			},
			//
			axiosUpdatetMyImage(type) {
				var Vue = this
				this.openLoading()
				return this.http.axiosUpdatetMyImage({
					data: this.Images.cash,
					grade: type,
					username: this.$store.state.user.username
				})
			},
			//
			handSetImageType(val) {
				this.axiosUpdatetMyImage(val).then((res) => {
					if(res.status == 200) {
						this.$message.success('')
					}
					this.closeLoading()
				})

			},

			//
			beforeUploadImg(file) {
				const isJPG = file.type === 'image/jpeg';
				const isPNG = file.type === 'image/png';
				const isGIF = file.type === 'image/gif';
				const isLt2M = file.size / 1024 / 1024 < 2;
				if(!isJPG && !isPNG && !isGIF) {
					this.$message.error('!');
					return false;
				}
				if(!isLt2M) {
					this.$message.error(' 2MB!');
					return false;
				}
				this.openLoading()
			},
			getImages(e, type) {
				var obj = e.currentTarget
				this.Images.isShowOpera = false;
				if($(obj).hasClass('active')) {
					return false
				} else {
					this.Images.type = type
					this.initImage()
					if(type == 'my') {
						if(this.Images.myNav == 1) {
							this.axiosGetMyImages()
						} else if(this.Images.myNav == 2) {
							this.axiosGetMyComImages(2)
						}

					} else if(type == 'all') {
						this.axiosGetAllImages()
					}else if(type == 'collect'){
						this.axiosGetCollectImages()
					}
				}

			},
			//
			checkAllImage() {
				var Vue = this;
				this.Images.data.forEach(function(val, index) {
					Vue.$set(val, 'check', Vue.Images.checkAll)
				})
				this.saveWaitDelImage()
			},
			//
			checkImage(item) {
				if(!item.check) {
					this.$set(item, 'check', true)
				} else {
					item.check = false;
				}
				this.saveWaitDelImage()
				if(this.Images.cash.length == this.Images.data.length) {
					this.Images.checkAll = true
				} else {
					this.Images.checkAll = false
				}
			},
			//
			saveWaitDelImage() {
				var Vue = this;
				this.Images.cash = [];
				this.Images.data.forEach(function(val, index) {
					if(val.check) {
						Vue.Images.cash.push({
							id: val.id,
							url: val.img_url
						});
					}
				})
			},
			//
			delImage() {
				this.$confirm('?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.ajaxDelImage()
				}).catch(() => {});
			},
			//
			ajaxDelImage() {
				var Vue = this
				this.openLoading()
				this.http.axiosDelImgaes({
					data: Vue.Images.cash,
				}).then((response) => {

					if(response.data.status == 1) {
						Vue.Images.checkAll = false;
						Vue.$message.success('')
						if((this.Images.count - this.Images.cash.length) % this.Images.pageSize == 0) {
							if(this.Images.page <= 1) {
								this.Images.page == 1
							} else {
								this.Images.page--
							}

						}
					} else {
						Vue.$message.error('')
					}

					this.Images.checkAll = false;
					this.Images.cash = [];
					if(this.Images.type == 'all') {
						this.axiosGetAllImages()
					} else if(this.Images.type == 'my') {
						this.axiosGetMyImages()
					}
				})
			},
			//
			delComImage() {
				this.$confirm(',""', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					this.axiosUpdatetMyImage(1).then((response) => {
						this.closeLoading()
						if(response.status == 200) {
							if((this.Images.count - this.Images.cash.length) % this.Images.pageSize == 0) {
								if(this.Images.page <= 1) {
									this.Images.page == 1
								} else {
									this.Images.page--
								}

							}
							this.$message.success('')
							this.Images.checkAll = false;
							this.Images.cash = [];
							this.axiosGetMyComImages(2)
						}
					})

				}).catch(() => {});
			},
			//
			changeImagePage(val) {
				this.Images.page = val
				if(this.Images.type == 'all') {
					this.axiosGetAllImages()
				} else if(this.Images.type == 'my') {
					if(this.Images.myNav == 1) {
						this.axiosGetMyImages()
					} else if(this.Images.myNav == 2) {
						this.axiosGetMyComImages(2)
					}
				}else if(this.Images.type == 'collect'){
					this.axiosGetCollectImages()
				}
			},
			clearImageFiles() {
				this.$refs['upload-image'].clearFiles();
				this.Images.successUpload = []
			},
			handleSuccessImg(res, file, fileList) {
				this.Images.successUpload.push(file)
				this.initImage()
				if(this.Images.successUpload.length == fileList.length) {
					this.Images.type = 'my'
					this.Images.myNav = 1;
					this.axiosGetMyImages()
				}
			},
			handleErrorImg() {
				this.$message.error('');
				this.closeLoading()
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>

<style scoped lang="scss">
	.collect-icon {
		position: absolute;
		bottom: 8px;
		left: 8px;
		font-size: 15px;
		&:hover,&.active{
			color: red;
		}
	}
</style>


