<template>
	<div class="regBox">
		<el-form :model="RegForm" status-icon :rules="RegRule" ref="RegForm" label-width="80px" class="demo-ruleForm">
			<el-form-item label="" prop="user">
				<el-input type="text"  v-model.trim="RegForm.user" autocomplete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="name">
				<el-input type="text"  v-model.trim="RegForm.name" autocomplete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="department">
				<el-input type="text"  v-model.trim="RegForm.department" autocomplete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="phone">
				<el-input type="text"  v-model.trim="RegForm.phone" autocomplete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="pass">
				<el-input type="password" v-model.trim="RegForm.pass" autocomplete="off"></el-input>
			</el-form-item>
			<el-form-item label="" prop="checkPass">
				<el-input type="password" v-model.trim="RegForm.checkPass"></el-input>
			</el-form-item>
			<el-form-item>
				<el-row :gutter="30">
				  	<el-col :span="12">
				  		<el-button type="primary" @click="submitForm('RegForm')" style='width: 100%;'></el-button>
				  	</el-col>
				  	<el-col :span="12">
				  		<el-button @click="resetForm('RegForm')"  style='width: 100%;'></el-button>
				  	</el-col>
				</el-row>
			</el-form-item>
		</el-form>
	</div>
</template>
<style type="text/css">
	.regBox {padding: 0 50px;}
</style>
<script>
	import utill from '@/assets/script/utill.js'
	export default {
		data() {
			var checkUser = (rule, value, callback) => {
				if(!value) {
					return callback(new Error(''));
				}else if(value.length<2 || value.length>12){
					return callback(new Error(''));
				}else {
					this.http.axiosFindUser({
						username:value
					}).then((response) => {
						if(response.data.status !=0){
							return callback(new Error(''));
						}else {
							callback();
						}	
					})		
				}				
			};
			var checkPhone = (rule, value, callback) => {
				if(value === '') {
					callback(new Error(''));
				} else if(!(/^1[34578]\d{9}$/.test(value))) {
					callback(new Error(''));
				} else {
					callback();
				}
			};
			var validatePass = (rule, value, callback) => {
				if(value === '') {
					callback(new Error(''));
				} else {
					if(this.RegForm.checkPass !== '') {
						this.$refs.RegForm.validateField('checkPass');
					}
					callback();
				}
			};
			var validatePass2 = (rule, value, callback) => {
				if(value === '') {
					callback(new Error(''));
				} else if(value !== this.RegForm.pass) {
					callback(new Error('!'));
				} else {				
					callback();
				}
			};
			return {
				RegForm: {
					pass: '',
					checkPass: '',
					user: '',
					name:'',
					department:'',
					phone: ''
				},
				RegRule: {
					pass: [{
						required: true,
						validator: validatePass,
						trigger: 'blur'
					}],
					checkPass: [{
						required: true,
						validator: validatePass2,
						trigger: 'blur'
					}],
					user: [{
						required: true,
						validator: checkUser,
						trigger: 'blur'
					}],
					phone:[{ 
						required: true, validator: checkPhone, trigger: 'blur' 
					}],
					name:[{ 
						required: true, message: '', trigger: 'blur' 
					}],
					department:[{ 
						required: true, message: '', trigger: 'blur' 
					}],
				}
			};
		},
		mounted(){
			this.$nextTick(()=>{
				document.addEventListener('keydown',this.keydownEnter,false)			
			})
		},
		beforeDestroy(){			
			document.removeEventListener('keydown',this.keydownEnter,false)	
		},
		methods: {
			keydownEnter(event){
				if(event.keyCode == 13){
			this.submitForm('RegForm')
			}
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						this.axiosReg()
					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			resetForm(formName) {
				this.$store.state.isShowRegister = false;
				this.$refs[formName].resetFields();
			},
			initReg(){
				this.RegForm.pass = '';
				this.RegForm.checkPass = true;
				this.RegForm.user = '';
				this.RegForm.name = '';
				this.RegForm.department = '';
			},
			axiosReg(){
				this.openLoading()
				this.http.axiosReg({
					username: this.RegForm.user,
					password: utill.getAES(this.RegForm.pass),
					name:this.RegForm.name,
					department: this.RegForm.department,
					phone: this.RegForm.phone
				}).then((response) => {
					this.closeLoading()
					if(response.data.status == 1) {
						this.$message.success(response.data.msg)
						utill.setCookie('username',this.RegForm.user)
						this.$store.state.user.username =  response.data.data.username
						this.$store.state.user.name = response.data.data.name
						this.$store.state.user.phone = response.data.data.phone
						this.$store.state.user.department = response.data.data.department
						this.$store.state.user.create_time = response.data.data.create_time
						this.$store.state.user.grade = response.data.data.grade
						this.$emit('closeReg')
					} else {
						this.$message.error(response.data.msg)
					}	
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>

<style>

</style>


