<template>
	<div class="item-comp" :style="items.BoxStyleData" :class="{'vis-hidden':pageActiveIndex !=index}">
		<div class="element-box animated" :style='[items.EditStyleData.style,items.animPower.style]' :class="pageActiveIndex==index?items.animPower.className:''">
			<template v-if='items.type == "text"'>
				<textarea class='edit-text' :value='items.data?items.data:items.text' readonly></textarea>
			</template>
			<template v-if='items.type == "image"'>
				<img :src="items.data?$api+items.data:$api+items.imgSrc" style="width: 100%;height: 100%;display: block;" />
			</template>
			<template v-if='items.type == "svg"'>
				<svg class="icon" :viewBox='items.data.view_box?items.data.view_box[0]:"0 0 1024 1024"' width="100%" height="100%" version="1.1" xmlns="http://www.w3.org/2000/svg">
					<path :d='path.d[0]' :fill="path.fill?path.fill[0]:''" v-for='path in (items.data?items.data.path:items.svg.path)'></path>
				</svg>
			</template>
			<template v-if='items.type == "input"'>
				<input class='edit-input' v-model="items.EditStyleData.form.store" :placeholder="items.EditStyleData.form.title"></input>
			</template>
			<template v-if='items.type == "message"'>
				<textarea class='edit-message' v-model="items.EditStyleData.form.store" :placeholder="items.EditStyleData.form.title" type="textarea"></textarea>
			</template>
			<template v-if='items.type == "submit"'>
				<button class="edit-submit" @click='submitForm'>{{items.EditStyleData.form.title}}</button>
			</template>
			<template v-if='items.type == "checkbox"'>
				<div class="edit-check">
					<div class="title" :style="{'background-color':items.EditStyleData.form.style?items.EditStyleData.form.style.backgroundColor:'','color':items.EditStyleData.form.style?items.EditStyleData.form.style.color:''}">{{items.EditStyleData.form.title}}</div>
					<el-checkbox-group class="list" v-model="items.EditStyleData.form.store">
						<li :style="{'border-color':items.EditStyleData.form.style?items.EditStyleData.form.style.backgroundColor:''}" v-for='(item,index) in items.EditStyleData.form.data' :key='index'>
							<el-checkbox :label="item">{{item}}</el-checkbox>
						</li>
					</el-checkbox-group>
				</div>
			</template>
			<template v-if='items.type == "radiobox"'>
				<div class="edit-check">
					<div class="title" :style="{'background-color':items.EditStyleData.form.style?items.EditStyleData.form.style.backgroundColor:'','color':items.EditStyleData.form.style?items.EditStyleData.form.style.color:''}">{{items.EditStyleData.form.title}}</div>
					<el-radio-group v-model="items.EditStyleData.form.store" class="list" style='display: block;'>
						<li :style="{'border-color':items.EditStyleData.form.style?items.EditStyleData.form.style.backgroundColor:''}" v-for='(item,index) in items.EditStyleData.form.data' :key='index'>
							<el-radio :label="item">{{item}}</el-radio>
						</li>
					</el-radio-group>
				</div>
			</template>
			<!--  -->
			<template v-if='items.type == "select"'>
				<el-select v-model="items.EditStyleData.form.store" :placeholder="items.EditStyleData.form.title">
					<el-option v-for='(option,index) in items.EditStyleData.form.data' :key="index" :label="option" :value="option"></el-option>
				</el-select>
			</template>
			<!--  -->
			<template v-if="items.type== 'link'">
				<div @click='bindLink(items.EditStyleData.form.data)'class='link-box'>
					<button v-show='!items.data' class="link-btn">{{items.EditStyleData.form.title}}</button>
					<img v-if='items.data' class="link-box-img" :src="$api+items.data" />
				</div>
			</template>
			<!--  -->
			<template v-if="items.type == 'phone'">
				<a :href="'tel:'+items.EditStyleData.form.data" class='link-box'>
					<button v-show='!items.data' class="link-btn">{{items.EditStyleData.form.title}}</button>
					<img v-if='items.data' class="link-box-img" :src="$api+items.data" />
				</a>
			</template>
			<!--  -->
			<template v-if='items.type == "video"'>
				<div class="edit-video">
					<img class="video-cover" :src="$api+items.data.cover" v-if='items.data.cover && !items.data.isShowVideo' />
					<img src="../assets/images/video_play.png" class=" video-play" @click="playVideo(items.data)" v-if='!items.data.isShowVideo' />
					<iframe :src="items.data.src" width="100%" height="100%" v-if='items.data.type == "1" && items.data.isShowVideo'></iframe>
				</div>
			</template>
			<!--  -->
			<template v-if='items.type == "code"'>
				<qrcode-vue :value="items.EditStyleData.form.title" :size="parseFloat(items.BoxStyleData.width)-parseFloat(items.EditStyleData.style.borderWidth)*2" level="H"></qrcode-vue>
			</template>
			<!-- -->
			<template v-if='items.type == "screen"'>
				<img v-if='items.data' class="link-box-img" :src="$api+items.data" />
				<button v-show='!items.data' class="link-btn edit-screen" @click='screenShot'><i class="fa fa-crop" aria-hidden="true"></i>&ensp;{{items.EditStyleData.form.title}}</button>
			</template>
		</div>
	</div>
</template>

<script>
	import QrcodeVue from 'qrcode.vue';
	import html2canvas from 'html2canvas';
	import canvg from 'canvg';
	export default {
		props: ['items', 'index', 'pageActiveIndex'],
		components: {
			QrcodeVue
		},
		mounted() {
			this.$nextTick(()=>{
				var copyDom = $('.preview-main .item-comp'); //dom
				var svgElem = copyDom.find('svg'); //divReportdomid
				svgElem.each(function(index, node) {
					var parentNode = node.parentNode;
					var svg = node.outerHTML.trim();
					var canvas = document.createElement('canvas');
					canvg(canvas, svg);
					if(node.style.position) {
						canvas.style.position += node.style.position;
						canvas.style.left += node.style.left;
						canvas.style.top += node.style.top;
					}
					canvas.width = '100%';
					canvas.height = '100%';
					canvas.style.width = '100%';
					canvas.style.height = '100%';
					parentNode.removeChild(node);
					parentNode.appendChild(canvas);
				});
			})
		},
		methods: {		
			//
			bindLink(val){
				this.http.axiosSaveLink({
					link: val,
					id: this.$route.params.id,
				}).then((res) => {					
				})
				window.location.href = val;
			},
			//
			screenShot(e) {			
				var copyDom = $(e.currentTarget).parents('.main-wraper'); //dom
				var editScreen = $(e.currentTarget).parents('.main-wraper').find('.edit-screen');
				var elementBox = copyDom.find('.element-box');
				var arrClassName = [];
				for(var i = 0; i < elementBox.length; i++) {
					arrClassName.push(elementBox.eq(i).attr('class'))
				}
				elementBox.attr('class', 'element-box');
				var itemComp = undefined;
				var Vue = this;
				if(editScreen.length > 0) {
					itemComp = editScreen.parents('.item-comp')
				}
				itemComp.hide();
				var width = copyDom[0].offsetWidth; //dom
				var height = copyDom[0].offsetHeight; //dom
				var scale = 2; //
				html2canvas(copyDom[0], {
					dpi: window.devicePixelRatio * 2,
					scale: scale,
					width: width,
					heigth: height,
					useCORS: true // 
				}).then(function(canvas) {
					var type = 'png';
					var imgData = canvas.toDataURL(type);
					var newImg = new Image();
					newImg.src = imgData;
					newImg.onload = function() {
						Vue.$emit('showFeedback', imgData, Vue.index)
						itemComp.show();
						for(var i = 0; i < elementBox.length; i++) {
							elementBox.eq(i).attr('class', arrClassName[i])
						}
					}

				});
			},
			submitForm() {
				this.$emit('submitForm')
			},
			playVideo(obj) {
				this.$set(obj, 'isShowVideo', true)
				if(obj.type == "1") {
					return false
				}
				this.isShowMusic = false;
				var audio = document.getElementById('preview-audio')
				if(this.isPlayMusic) {
					this.isPlayMusic = false
					audio.pause()
					$('.preview-music').addClass('pause')
					this.isChangeMusic = true;
				} else {
					this.isChangeMusic = false;
				}
			},
		},
	}
</script>


