<template>
	<div class="template-detail">
		<div class="template-info">
			<div class="show">
				<element-list v-if='isLoadSuccess' ref='ElementList' :arrEleData='arrEleData' :isAutoPlay='swiper.isAutoPlay' :autoplay="swiper.autoplay" :music='music' :effect='swiper.effect' :direction='swiper.direction'></element-list>
				<div class="preview-page">
					<div class="preview-page-up">
						<i class="el-icon-arrow-up"></i>
					</div>
					<div class="preview-page-down">
						<i class="el-icon-arrow-down"></i>
					</div>
				</div>
			</div>
			<div class="info">
				<h2 class="title over-oneline">{{title}}</h2>
				<p class="describe over-oneline">{{describe.length<=0? '':describe}}</p>
						<ul class="opera">
							<li>
								<i class="fa fa-fire" aria-hidden="true"></i>
								<span>{{count}}</span>
							</li>
							<li class="link" @click="copyLink">
								<i class="fa fa-link" aria-hidden="true"></i>
								<span></span>
								<input type="text" name="copylink" id="copylink" :value="link" />
							</li>
						</ul>
						<qrcode-vue :value='$link+"#/"+id+"/"+name' :size="160" level="H"></qrcode-vue>
						<el-button class='user-scene' type="primary" @click='useTemplateScene(tempid,author.username)'></el-button>
			</div>
			<div class="author">
				<img class="head" :src="author.head?$api+author.head:''" />
				<p class="name">{{author.name}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	import QrcodeVue from 'qrcode.vue';
	import ElementList from '@/components/ElementList'
	export default {
		components: {
			QrcodeVue,
			ElementList
		},
		data() {
			return {
				id: this.$route.params.id,
				name: this.$route.params.name,
				isShowPreviewSlip: false,
				setAnimTimeOut: '',
				arrEleData: [],
				music: '', //
				title: '', //
				cover_img: '', //
				describe: '', //
				create_time: '',
				isPlayMusic: false,
				previewForm: {
					titleLimit: 48,
					describeLimit: 60,
				},
				swiper: '',
				isSubmitForm: false,
				isShowTip: false, //
				isShowTipSuccess: false, //
				slideArr: [], //slide
				pageActiveIndex: 0, //
				isLoadSuccess: false,
				tempid: '',
				count:'',//
				author: { //
					name: '',
					head: '',
					username:''
				},
				link:window.location.href
			}
		},
		created() {
			this.axiosSelectTemplateSceneData()
		},
		methods: {
			//
			copyLink() {
				var shareLink = document.getElementById("copylink");
				shareLink.select(); // 
				document.execCommand("Copy"); // 
				this.$message.success('')
			},	
			useTemplateScene(tempid, author) {
				var id = this.utill.random(2) + new Date().getTime()
				if(this.$store.state.user.username) {
					this.axiosUseTemplateScene(id, tempid, author)
				}else {
					this.$store.state.isShowLogin = true
				}
			},
			axiosUseTemplateScene(id, tempid, author) {
				this.openLoading()
				this.http.axiosUseTemplateScene({
					id: id,
					tempid: tempid,
					author: author,
					username: this.$store.state.user.username
				}).then((res) => {
					this.closeLoading()					
					if(res.data.status == 1) {
						this.$router.push({
							name: "edit",
							params: {
								name: 'edit',
								id: id
							},
						});
					}else {
						this.$message.error('')
					}
				})
			},
			axiosSelectTemplateSceneData() {
				var Vue = this
				Vue.openLoading()
				this.http.axiosSelectTemplateSceneData({
					id: Vue.id,
					username: this.$route.params.name
				}).then((res) => {
					if(res.data.status == 1) {
						for(var i = 0; i < res.data.data.length; i++) {
							Vue.arrEleData.push(JSON.parse(res.data.data[i].data))
						}
						Vue.swiper = JSON.parse(res.data.info.swiper)
						Vue.music = JSON.parse(res.data.info.music)
						Vue.describe = res.data.info.describe
						Vue.cover_img = res.data.info.cover_img
						Vue.title = res.data.info.title
						Vue.create_time = Vue.utill.timestampToTime(Number(res.data.info.create_time))
						Vue.author.name = res.data.author.name
						Vue.author.username = res.data.author.username
						Vue.author.head = res.data.author.head_src
						Vue.tempid = res.data.info.edit_id
						Vue.count = res.data.info.count
						//
						Vue.arrEleData.forEach(function(val, index) {
							if(val.children) {
								for(var i = 0; i < val.children.length; i++) {
									if(val.children[i].type == "checkbox") {
										Vue.$set(val.children[i].EditStyleData.form, 'store', [])
									}
									//
									if(!val.children[i].animData) {
										Vue.$set(val.children[i], 'animData', [])
									} else {
										//
										for(var t = 0; t < val.children[i].animData.length; t++) {
											if(typeof(val.children[i].animData[t].style.isInfinite) == 'string') {
												val.children[i].animData[t].style.isInfinite = eval(val.children[i].animData[t].style.isInfinite.toLowerCase())
											}
										}
									}

								}
							}
						})
						Vue.$nextTick(() => {
							Vue.closeLoading()
							Vue.isLoadSuccess = true
						})

					} else if(res.data.status == 0) {

					}
				})
			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}

	}
</script>

<style src='@/assets/style/template.scss' lang='scss' scoped></style>


