<template>
	<div class='preview-wraper'>
		<div class="preview-content">
			<div class="preview-box">
				<h4 class="preview-title">{{$store.state.Title}}</h4>
				<span v-for="item in directions">
					<span v-for ='effect in effects' >	
						<element-list ref='ElementList'  v-if="(effect == $store.state.Swiper.type) && (item == $store.state.Swiper.direction)" :arrEleData='arrEleData' :isAutoPlay='$store.state.Swiper.isAutoPlay' :autoplay="$store.state.Swiper.autoplay" :music='music' :effect='effect' :direction='item'></element-list>	
					</span>
				</span>
				<div class="preview-page">
					<div class="preview-page-up"></div>
					<div class="preview-page-down"></div>
				</div>
			</div>
		</div>
		<transition name="slide-fade">
			<div class="preview-slip" v-if='isShowPreviewSlip'>
				<a href="javascript:;" class="preview-close" @click="closePreview">
					<i class="el-icon-close" aria-hidden="true"></i>
				</a>
				<div class="preview-slip-wrap">
					<div class="setImg" @click="changeCover">
						<img :src="$api+$store.state.CoverImg" class="cover-img" />
						<span class="changeCover"></span>
					</div>
					<div class="preview-form">
						<el-form ref='previewForm' :model='previewForm' label-width="80px">
							<el-form-item label="">
								<el-input :maxlength='14' v-model="$store.state.Title"></el-input>
								<div class="inp-limit">{{$store.state.Title.length}}/{{previewForm.titleLimit}}</div>
							</el-form-item>
							<el-form-item label="">
								<el-input type="textarea" maxlength='30' v-model="$store.state.Describe"></el-input>
								<div class="inp-limit">{{$store.state.Describe.length}}/{{previewForm.describeLimit}}</div>
							</el-form-item>
						</el-form>
					</div>
					<div class="preview-swiper">
						<div class="item">
							<p class="title">:</p>
							<div class="content">
								<el-radio-group v-model="$store.state.Swiper.direction">
									<el-radio label="vertical"></el-radio>
									<el-radio label="horizontal"></el-radio>
								</el-radio-group>
							</div>
						</div>
						<div class="item">
							<p class="title">:</p>
							<div class="content">
								<el-radio-group v-model="$store.state.Swiper.type" size="medium">
									<el-radio-button label="slide"></el-radio-button>
									<el-radio-button label="fade"></el-radio-button>
									<!--<el-radio-button label="cube"></el-radio-button>-->
									<el-radio-button label="coverflow"></el-radio-button>
									<el-radio-button label="flip"></el-radio-button>
								</el-radio-group>
							</div>
						</div>
						<div class="item">
							<p class="title">:</p>
							<div class="content">
								<!--<el-checkbox v-model="$store.state.Swiper.loop" @change='changeSwiper'></el-checkbox>-->
								<el-checkbox v-model="$store.state.Swiper.isAutoPlay" @change='changeSwiper'></el-checkbox>
								<div v-if="$store.state.Swiper.isAutoPlay" style="display: inline-block;width: 130px;margin-left: 20px;">
									<el-input-number size="mini" v-model="$store.state.Swiper.autoplay" :precision="2" :step="0.1" :min='1' :max="10" @change='changeSwiper'></el-input-number>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="preview-bottom">
					<el-button type="primary" @click='confirmIssue'></el-button>
					<el-button @click='saveDate'></el-button>
					<el-button @click="$store.state.isShowPreview=false"></el-button>
				</div>
			</div>
		</transition>
	</div>
</template>

<script>
	import ElementList from '@/components/ElementList'
	export default {
		components: {
			ElementList
		},
		name: 'preview',
		data() {
			return {
				Vue: this,
				isShowPreviewSlip: false,
				setAnimTimeOut: '',
				arrEleData: '',
				music: '',
				isPlayMusic: false,
				directions: ['vertical', 'horizontal'],
				effects: ['slide', 'fade', 'coverflow', 'flip'],
				isAutoPlay: false,
				autoplay: 5,
				isShowPreviewSlip: false,
				setAnimTimeOut: '',
				music: '',
				isPlayMusic: false,
				previewForm: {
					titleLimit: 14,
					describeLimit: 30,
				},
			}
		},
		mounted() {
			var Vue = this
			this.$nextTick(function() {
				Vue.isShowPreviewSlip = true
			})

		},
		created() {
			this.arrEleData = JSON.parse(JSON.stringify(this.$store.state.ElementData))
			this.music = JSON.parse(JSON.stringify(this.$store.state.Music))
		},
		methods: {
			//
			saveDate() {
				this.$emit('saveDate')
			},
			//
			confirmIssue() {
				this.$emit('confirmIssue')
			},
			changeCover() {
				this.$emit('changeCover')
			},
			closePreview() {
				this.$store.state.isShowPreview = false
			},
			changeSwiper() {
				this.$nextTick(function() {
					this.$refs.ElementList[0].createSwiper()
				})
			}

		}
	}
</script>
<style src='@/assets/style/preview.scss' lang="scss"></style>


