<template>
	<div class="select-panel">
		<ul class="item">
			<li class="align">
				<el-tooltip effect="dark" :open-delay='0' transition='el-zoom-in-bottom' content="" placement="top">
					<i class="iconfont icon-zuoduiqi" @click='alignLeft' aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
					<i class="iconfont icon-chuizhijuzhongduiqi" @click="alignLcenter" aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
					<i class="iconfont icon-youduiqi" @click="alignRight" aria-hidden="true"></i>
				</el-tooltip>
			</li>
			<li class="align">
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
					<i class="iconfont icon-shangduiqi" @click="alignTop" aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
					<i class="iconfont icon-chuizhijunfen" @click="alignHcenter" aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="top">
					<i class="iconfont icon-xiaduiqi" @click="alignBottom" aria-hidden="true"></i>
				</el-tooltip>
			</li>
		</ul>
		<ul class="item">
			<li class="align zindex">
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="bottom">
					<i class="iconfont icon-zhiding" @click="changeZindex('top')" aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="bottom">
					<i class="iconfont icon-dibu" @click="changeZindex('bottom')" aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="bottom">
					<i class="iconfont icon-shangyiyiceng" @click="changeZindex('up')" aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="bottom">
					<i class="iconfont icon-xiayiyiceng" @click="changeZindex('down')" aria-hidden="true"></i>
				</el-tooltip>
			</li>
			<li class="align zindex">
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="bottom">
					<i class="iconfont icon-copy2" @click="copyPart"  aria-hidden="true"></i>
				</el-tooltip>
				<el-tooltip effect="dark" content="" :open-delay='0' transition='el-zoom-in-bottom' placement="bottom" v-if='$store.state.ElementClipboard.length>0'>
					<i class="iconfont icon-niantie"@click="clipPart" aria-hidden="true"></i>
				</el-tooltip>
			</li>
		</ul>
		<el-form class='select-style' label-width="75px" label-position='left'>
			<el-form-item label="" class='address'>
				<span>X&emsp;&emsp;</span>
				<el-input-number @change='changeX' v-model='partX' size="small" controls-position="right"></el-input-number>
				</br>
				<span>Y&emsp;&emsp;</span>
				<el-input-number @change='changeY' v-model='partY' size="small" controls-position="right"></el-input-number>
			</el-form-item>
			<el-form-item label="" class='rotate'>
				<div class="block">
					<el-slider :max='360' :min='0' v-model='angle' show-input input-size='small' :show-input-controls='false'> </el-slider>
				</div>
			</el-form-item>
			<el-form-item label="">
				<div class="block">
					<el-slider :max='100' :min='0' v-model='opacity' show-input input-size='small' :show-input-controls='false'> </el-slider>
				</div>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				angle: 0, //
				opacity: 0, //
				partX: null,
				partY: null,
				pageData:[],
			}
		},
		computed: {
			initData() {
				var Vue = this;
				this.arrPartData = [];
				this.$store.state.ElementData.forEach(function(val, i) {
					if(val.id == Vue.$store.state.focusPage) {
						Vue.pageData = val.children
						for(var t = 0; t < val.children.length; t++) {
							if(Vue.$store.state.focusPart.indexOf(val.children[t].id) != -1) {
								Vue.arrPartData.push(val.children[t])
							}
						}
					}
				})
				return Vue.arrPartData
			}
		},
		watch: {
			angle(val) {
				for(var i = 0; i < this.initData.length; i++) {
					this.$set(this.initData[i].BoxStyleData, 'transform', "rotateZ(" + parseInt(val) + "deg)")
				}
			},
			opacity(val) {
				for(var i = 0; i < this.initData.length; i++) {
					this.$set(this.initData[i].BoxStyleData, 'opacity', parseFloat(val / 100))
				}
			},
		},
		methods: {
			//()
			sortByKey(array,key){  
	            return array.sort(function(a,b){           	
		            var x=a[key]['zIndex'];
		            var y=b[key]['zIndex'];
		            return ((x<y)?-1:((x>y)?1:0));
		       	 })
		    },
			copyPart(){
				this.$emit('copyPart')
			},
			clipPart (){
				this.$emit('clipPart')
			},
			changeY(val) {
				for(var i = 0; i < this.initData.length; i++) {
					this.initData[i].BoxStyleData.top = parseFloat(val) + 'px'
				}
			},
			changeX(val) {
				for(var i = 0; i < this.initData.length; i++) {
					this.initData[i].BoxStyleData.left = parseFloat(val) + 'px'
				}
			},
			//
			alignLeft() {
				var arrLeft = [];
				for(var i = 0; i < this.initData.length; i++) {
					arrLeft.push(parseFloat($("li[data-id=" + this.initData[i].id + "]").position().left))
				}
				for(var i = 0; i < arrLeft.length; i++) {
					for(var j = 0; j < arrLeft.length - i; j++) {
						if(arrLeft[j] > arrLeft[j + 1]) {
							var swap = arrLeft[j];
							arrLeft[j] = arrLeft[j + 1];
							arrLeft[j + 1] = swap;
						}
					}
				}
				for(var i = 0; i < this.initData.length; i++) {
					var angle = parseFloat(this.initData[i].BoxStyleData.transform.split('(')[1]);
					var w = parseFloat(this.initData[i].BoxStyleData.width);
					var h = parseFloat(this.initData[i].BoxStyleData.height);
					if(0 <= angle && angle <= 90) {
						this.initData[i].BoxStyleData.left = arrLeft[0] + (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(90 < angle && angle <= 180) {
						this.initData[i].BoxStyleData.left = arrLeft[0] - (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(180 < angle && angle <= 270) {
						this.initData[i].BoxStyleData.left = arrLeft[0] - (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(270 < angle && angle <= 360) {
						this.initData[i].BoxStyleData.left = arrLeft[0] + (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					}
				}
				this.$emit('cacheData');
			},
			//
			alignLcenter() {
				var arrLeft = [];
				var num = 0;
				for(var i = 0; i < this.initData.length; i++) {
					var l = parseFloat(this.initData[i].BoxStyleData.left)
					var w = parseFloat(this.initData[i].BoxStyleData.width)
					arrLeft.push(l + w / 2)
				}
				for(var i = 0; i < arrLeft.length; i++) {
					num += arrLeft[i]
				}
				num = num / arrLeft.length
				for(var i = 0; i < this.initData.length; i++) {
					var w = parseFloat(this.initData[i].BoxStyleData.width) / 2
					this.initData[i].BoxStyleData.left = num - w + 'px'
				}
				this.$emit('cacheData');
			},
			//
			alignRight() {
				var arrLeft = [];
				for(var i = 0; i < this.initData.length; i++) {
					var l = parseFloat($("li[data-id=" + this.initData[i].id + "]").position().left)
					var w = parseFloat($("li[data-id=" + this.initData[i].id + "]")[0].getBoundingClientRect().width)
					arrLeft.push(l + w)
				}
				for(var i = 0; i < arrLeft.length; i++) {
					for(var j = 0; j < arrLeft.length - i; j++) {
						if(arrLeft[j] < arrLeft[j + 1]) {
							var swap = arrLeft[j];
							arrLeft[j] = arrLeft[j + 1];
							arrLeft[j + 1] = swap;
						}
					}
				}
				for(var i = 0; i < this.initData.length; i++) {
					var angle = parseFloat(this.initData[i].BoxStyleData.transform.split('(')[1]);
					var w = parseFloat(this.initData[i].BoxStyleData.width);
					var h = parseFloat(this.initData[i].BoxStyleData.height);
					var rw = $("li[data-id=" + this.initData[i].id + "]")[0].getBoundingClientRect().width
					if(0 <= angle && angle <= 90) {
						this.initData[i].BoxStyleData.left = arrLeft[0] - rw + (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(90 < angle && angle <= 180) {
						this.initData[i].BoxStyleData.left = arrLeft[0] - rw - (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) + (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(180 < angle && angle <= 270) {
						this.initData[i].BoxStyleData.left = arrLeft[0] - rw - (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(270 < angle && angle <= 360) {
						this.initData[i].BoxStyleData.left = arrLeft[0] - rw + (w / 2) * Math.cos(Math.PI * angle / 180) - (w / 2) - (h / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					}

				}
				this.$emit('cacheData');
			},
			//
			alignTop() {
				var arrTop = [];
				for(var i = 0; i < this.initData.length; i++) {
					arrTop.push(parseFloat($("li[data-id=" + this.initData[i].id + "]").position().top))
				}
				for(var i = 0; i < arrTop.length; i++) {
					for(var j = 0; j < arrTop.length - i; j++) {
						if(arrTop[j] > arrTop[j + 1]) {
							var swap = arrTop[j];
							arrTop[j] = arrTop[j + 1];
							arrTop[j + 1] = swap;
						}
					}
				}
				for(var i = 0; i < this.initData.length; i++) {
					var angle = parseFloat(this.initData[i].BoxStyleData.transform.split('(')[1]);
					var w = parseFloat(this.initData[i].BoxStyleData.width);
					var h = parseFloat(this.initData[i].BoxStyleData.height);
					if(0 <= angle && angle <= 90) {
						this.initData[i].BoxStyleData.top = arrTop[0] + (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(90 < angle && angle <= 180) {
						this.initData[i].BoxStyleData.top = arrTop[0] - (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(180 < angle && angle <= 270) {
						this.initData[i].BoxStyleData.top = arrTop[0] - (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(270 < angle && angle <= 360) {
						this.initData[i].BoxStyleData.top = arrTop[0] + (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					}

				}
				this.$emit('cacheData');
			},
			//
			alignBottom() {
				var arrTop = [];
				for(var i = 0; i < this.initData.length; i++) {
					var t = parseFloat($("li[data-id=" + this.initData[i].id + "]").position().top)
					var h = parseFloat($("li[data-id=" + this.initData[i].id + "]")[0].getBoundingClientRect().height)
					arrTop.push(t + h)
				}
				for(var i = 0; i < arrTop.length; i++) {
					for(var j = 0; j < arrTop.length - i; j++) {
						if(arrTop[j] < arrTop[j + 1]) {
							var swap = arrTop[j];
							arrTop[j] = arrTop[j + 1];
							arrTop[j + 1] = swap;
						}
					}
				}
				for(var i = 0; i < this.initData.length; i++) {
					var angle = parseFloat(this.initData[i].BoxStyleData.transform.split('(')[1]);
					var w = parseFloat(this.initData[i].BoxStyleData.width);
					var h = parseFloat(this.initData[i].BoxStyleData.height);
					var rh = $("li[data-id=" + this.initData[i].id + "]")[0].getBoundingClientRect().height
					if(0 <= angle && angle <= 90) {
						this.initData[i].BoxStyleData.top = arrTop[0] - rh + (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(90 < angle && angle <= 180) {
						this.initData[i].BoxStyleData.top = arrTop[0] - rh - (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) + (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(180 < angle && angle <= 270) {
						this.initData[i].BoxStyleData.top = arrTop[0] - rh - (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					} else if(270 < angle && angle <= 360) {
						this.initData[i].BoxStyleData.top = arrTop[0] - rh + (h / 2) * Math.cos(Math.PI * angle / 180) - (h / 2) - (w / 2) * Math.sin(Math.PI * angle / 180) + 'px'
					}

				}
				this.$emit('cacheData');
			},
			//
			alignHcenter() {
				var arrTop = [];
				var num = 0;
				for(var i = 0; i < this.initData.length; i++) {
					var t = parseFloat(this.initData[i].BoxStyleData.top)
					var h = parseFloat(this.initData[i].BoxStyleData.height)
					arrTop.push(t + h / 2)
				}
				for(var i = 0; i < arrTop.length; i++) {
					num += arrTop[i]
				}
				num = num / arrTop.length
				for(var i = 0; i < this.initData.length; i++) {
					var h = parseFloat(this.initData[i].BoxStyleData.height) / 2
					this.initData[i].BoxStyleData.top = num - h + 'px'
				}
				this.$emit('cacheData');
			},
			//
			changeZindex(type) {
				var Vue = this
				for(var i = 0; i < this.initData.length; i++) {
					switch(type) {
						case 'top':
							this.initData[i].BoxStyleData.zIndex = 50;
							break;
						case 'bottom':
							this.initData[i].BoxStyleData.zIndex = 5;
							break;
						case 'up':
							this.initData[i].BoxStyleData.zIndex += 1
							if(this.initData[i].BoxStyleData.zIndex >= 50) {
								this.initData[i].BoxStyleData.zIndex = 50
							}
							break;
						case 'down':
							this.initData[i].BoxStyleData.zIndex -= 1;
							if(this.initData[i].BoxStyleData.zIndex <= 5) {
								this.initData[i].BoxStyleData.zIndex = 5
							}
							break;
					}
				}
				this.sortByKey(this.pageData,'BoxStyleData')
				this.$emit('cacheData');
			}
		
		}
	}
</script>

<style src="@/assets/fonts/selectPanel/iconfont.css" scoped>

</style>
<style lang="scss">
	.select-panel {
		padding: 16px;
		.item {
			overflow: hidden;
			margin-bottom: 12px;
			.align {
				float: left;
				margin-left: 10px;
				&:first-of-type {
					margin: 0;
				}
				i {
					color: #76838f;
					font-size: 19px;
					border: 1px solid #bbb;
					padding: 2px;
					margin-right: 1px;
					cursor: pointer;
					display: inline-block;
					float: left;
					&:hover {
						border-color: #409EFF ;
						color: #409EFF;
					}
				}
			}
		}
		.select-style {
			.address {
				.el-input-number--small {
					width: 110px;
				}
			}
			margin-top: 22px;
			.el-slider__runway.show-input {
				margin-right: 70px;
			}
			.el-slider__input {
				width: 55px !important;
			}
		}
	}
</style>


