<template>
	<!-- svg  -->
	<div class="material" v-if="Svgs.isShow">
		<div class="material-box">
			<div class="material-left">
				<h3 class="head">
					&ensp;<span class="el-icon-info"></span>
				</h3>
			</div>
			<div class="material-right">
				<i class="el-icon-close colse" @click="Svgs.isShow=false"></i>
				<div class="conent-box">
					<el-tabs v-model="activeNavName">
						<el-tab-pane label="" name="all">
							<el-scrollbar style='height: 476px;'>
								<ul class="material-content">
									<img class="empty-icon" src="@/assets/images/empty.png" v-if='Svgs.data.length<=0' />
									<li class="material-item" v-for="item in Svgs.data" @click="setSvgs($event,item)">
										<svg class="icon" :viewBox = 'item.view_box[0]' width="64px" height="64.00px"  version="1.1" xmlns="http://www.w3.org/2000/svg">
											<path :d='path.d[0]' :fill="path.fill?path.fill[0]:''" v-for='path in item.path'></path>
										</svg>
									</li>
								</ul>
							</el-scrollbar>
							<el-pagination background layout="prev, pager, next,total,jumper" :current-page.sync='Svgs.page' :page-size='Svgs.pageSize' :total="Svgs.count" @current-change="changeSvgPage"></el-pagination>
						</el-tab-pane>
					</el-tabs>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				activeNavName: "all",
				Svgs: { //
					page: 1, //
					count: 0, //
					pageSize: 40, //
					data: [], //						
					isShow: false, //
				},
			}
		},
		methods: {
			//
			showSvgsBar() {
				this.Svgs.isShow = true;
				this.axiosGetSvgs()
			},
			//
			setSvgs(e, item) {
				var obj = e.currentTarget
				this.$emit('setPart',e, 'svg', JSON.parse(JSON.stringify(item)))
				this.Svgs.isShow = false
			},
			changeSvgPage(val) {
				this.Svgs.page = val
				this.axiosGetSvgs()
			},
			//
			axiosGetSvgs() {
				var Vue = this
				this.openLoading()
				this.http.axiosGetSvgs({
					page: Vue.Svgs.page,
					pageSize: Vue.Svgs.pageSize,
				}).then((response) => {
					this.closeLoading()
					Vue.Svgs.data = response.data.data
					Vue.Svgs.count = parseInt(response.data.count)
					for(var i = 0; i < Vue.Svgs.data.length; i++) {
						Vue.Svgs.data[i].url = Vue.Svgs.data[i].url
						if(Vue.Svgs.data[i].view_box){
							Vue.Svgs.data[i].view_box = JSON.parse(Vue.Svgs.data[i].view_box)
						}else {
							Vue.Svgs.data[i].view_box = Vue.Svgs.data[i].view_box.split('')
							Vue.Svgs.data[i].view_box[0] = "0 0 1024 1024"
						}
						
						Vue.Svgs.data[i].path = JSON.parse(Vue.Svgs.data[i].path)
						for(var t = 0; t < Vue.Svgs.data[i].path.length; t++) {
							if(!(Vue.Svgs.data[i].path[t].fill)) {
								Vue.$set(Vue.Svgs.data[i].path[t], 'fill', [])
							}
						}
					}
					var arrSvgs = [];
					arrSvgs = arrSvgs.concat(Vue.$store.state.Svgs, Vue.Svgs.data)
					Vue.$store.state.Svgs = Vue.uniquelize(arrSvgs)
				})
			},
			//
			uniquelize(str) {
				var newArr = [],
					i = 0,
					len = str.length;
				for(; i < len; i++) {
					var a = JSON.stringify(str[i]);
					if(newArr.indexOf(a) !== -1) {
						continue;
					} else {
						newArr[newArr.length] = a;
					}
				}
				for(var i = 0; i < newArr.length; i++) {
					newArr[i] = JSON.parse(newArr[i])
				}
				return newArr;

			},
			openLoading() {
				this.loading = this.$loading({
					lock: true,
					text: 'Loading',
					spinner: 'el-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				});
			},
			closeLoading() {
				this.loading.close();
			},
		}
	}
</script>

<style>

</style>


